var RasterTileImageryProvider = require('./RasterTileImageryProvider');
var RasterImageGenerator = require('../Render/RasterImageGenerator');
var AwxRasterHeader = require('../Data/Awx/AwxRasterHeader');


var RasterTileData = RasterTileImageryProvider.RasterTileData;
/**
*@constructor
*@extends MeteoLib.Scene.RasterTileImageryProvider.RasterTileData
*@memberof MeteoLib.Scene.AwxSateImageryProvider
*/
function AwxRasterTileData(options) {
    RasterTileData.apply(this, [options]);
    /**
    *@type {MeteoLib.Data.Awx.AwxRasterHeader}
    */
    this.header = options.header;
}
AwxRasterTileData.prototype = new RasterTileData();
/**
*
*@constructor
*@memberof MeteoLib.Scene
*@param {Object}options
*@param {String}options.fileName
*@param {String}options.url
*@constructor
*@extends MeteoLib.Scene.RasterTileImageryProvider
*/
function AwxSateImageryProvider(options) {
    if (!options) {
        throw new Error("options")
    }

    options.headerSize = options.headerSize ? options.headerSize : 14096;

    options.getImageGenerator = options.getImageGenerator ? options.getImageGenerator : this.getImageGenerator;
    options.parseHeader = options.parseHeader ? options.parseHeader : this.parseHeader;

    RasterTileImageryProvider.apply(this, [options]);

    /**
    *@type {MeteoLib.Scene.RasterTileImageryProvider.AwxRasterTileData}
    */
    this.overviewData = null
}

AwxSateImageryProvider.AwxRasterTileData = AwxRasterTileData;

AwxSateImageryProvider.prototype = new RasterTileImageryProvider();

AwxSateImageryProvider.prototype.getImageGenerator = function (header) {
    var sateNames = ['FY2E', 'F2E', 'FY2G', 'F2G', 'FY4A', 'HMW8']
    var fileName = this.fileName.name || this.fileName;
    var dataNameParts = fileName.split(/[\._-]+/g);
    var sateName;
    for (var i = 0; i < dataNameParts.length; i++) {
        sateName = dataNameParts[i].toUpperCase()
        var exits = false;
        for (var j = 0; j < sateNames.length; j++) {
            if (sateNames[j] == sateName) {
                exits = true;
                break;
            }
        }
        if (exits) break;
    }

    var dataAttributes = header.Attributes;
    var bandNo = dataAttributes[''];
    var imageGenerator;

    if (sateName == 'FY2E'
        || sateName == 'FY2G'
        || sateName == 'F2E'
        || sateName == 'F2G') {

        if (bandNo == 1 || bandNo == 3) {
            imageGenerator = RasterImageGenerator.FY2G.IR01_02;
        } else if (bandNo == 2) {
            imageGenerator = RasterImageGenerator.FY2G.IR03;
        } else {
            imageGenerator = RasterImageGenerator.FY2G.IR4_VIS;
        }
    }
    else if (sateName == 'FY4A') {

        if (bandNo == 4) {
            imageGenerator = RasterImageGenerator.FY4A.C01_C06;
        } else if (bandNo == 5) {
            imageGenerator = RasterImageGenerator.FY4A.C07_C08;
        } else if (bandNo == 2) {
            imageGenerator = RasterImageGenerator.FY4A.C09_C10;
        } else if (bandNo == 1) {
            imageGenerator = RasterImageGenerator.FY4A.C11_C14;
        }

    } else if (sateName == 'HMW8') {

        if (bandNo == 4) {
            imageGenerator = RasterImageGenerator.HMW8.B03;
        } else if (bandNo == 2) {
            imageGenerator = RasterImageGenerator.HMW8.B08;
        } else if (bandNo == 1) {
            imageGenerator = RasterImageGenerator.HMW8.B13;
        }
    }
    return imageGenerator;
}

AwxSateImageryProvider.prototype.parseHeader = function (headerBuffer, headerFileName) {
    this.header = new AwxRasterHeader(headerBuffer, headerFileName);
    this.width = this.header.Width;
    this.height = this.header.Height;
    var ce = this.header.CoordEnvelope;
    this.bbox = [ce.MinX, ce.MinY, ce.MaxX, ce.MaxY];
    this._dataRectangle = Cesium.Rectangle.fromDegrees(
        ce.MinX, ce.MinY, ce.MaxX, ce.MaxY, undefined, this._dataRectangle
    );
    this.headerSize = this.header.HeaderSize;
    return this.header;
}

module.exports = AwxSateImageryProvider;
