var ExtendMethod=require('../../Util/ExtendMethod');

    /**
    *
    *@constructor
    *@memberof MeteoLib.Data.RasterData
    *@property {String}Satellite
    *@property {String}Sensor 
    *@property {Boolean}IsOrbit 
    *@property {Boolean}IsProduct  
    *@property {String}ProductIdentify 
    *@property {String}SubProductIdentify 
    *@property {Date}OrbitDateTime 
    */
    function DataIdentify(satellite, sensor) {
        this.Satellite;
        this.Sensor;
        this.IsOrbit;
        this.IsProduct;
        this.ProductIdentify;
        this.SubProductIdentify;
        this.OrbitDateTime;


        this.Satellite = satellite;
        this.Sensor = sensor;

        this.CopyAttributesToIfNull = function (targetDataIdentify) {
            if (String.IsNullOrEmpty(targetDataIdentify.Satellite)) {
                targetDataIdentify.Satellite = this.Satellite;
            }
            if (String.IsNullOrEmpty(targetDataIdentify.Sensor)) {
                targetDataIdentify.Sensor = this.Sensor;
            }
        }
    }
    module.exports= DataIdentify;
