/** 
*
*@memberof MeteoLib.Util
*@constructor
 */
function MeteoSign() {
    this.color = 'rgba(0,0,255,1)';
    this.size = 100; //
    this.lineWidth = 4;
}

/** 
 * @param {object} options 
 * @param {Viewer} options.viewer cesium
 * @param {int} [options.size] 
 * @param {string} [options.color] 
 */
MeteoSign.prototype.Initialize = function (options) {
    this.earth.viewer = options.viewer;
    if (options.color)
        this.color = options.color;
    else
        this.color = 'dark';
    if (options.size)
        this.size = options.size;
    else
        this.size = 100;
}

/**
 * 
 * @param {int} options.type ,0;1;23456
 * @param {array} options.tags taglonlatspeedangletag.type
 * speed''tag.type-1
 * type==1tag.type012345678
 * type==2tag.type0123456
 * type==3tag.type01
 * type==4tag.type012
 * type==5tag.type0123
 * type==6tag.type012345N6L5G5D
 * @param {int} options.len 
 */
MeteoSign.prototype.ShowTag = function (options) {
    for (var i = 0; i < options.len; i++) {
        var tag = options.tags[i];
        switch (options.type) {
            case 0: //
                canvas = this.GetWindCanvas(tag.speed);
                break;
            case 1: //
                canvas = this.GetRainCanvas(tag.type);
                break;
            case 2: //
                canvas = this.GetSnowCanvas(tag.type);
                break;
            case 3: //
                canvas = this.GetFogCanvas(tag.type);
                break;
            case 4: //
                canvas = this.GetCloudCanvas(tag.type);
                break;
            case 5: //
                canvas = this.GetSandCanvas(tag.type);
                break;
            case 6: //
                canvas = this.GetOtherCanvas(tag.type);
                break;
        }
        if (!tag.angle)
            tag.angle = 0;
        //
        var citizensBankPark = viewer.entities.add({
            position: Cesium.Cartesian3.fromDegrees(tag.lon, tag.lat),
            //
            billboard: {
                image: canvas, //canvas,
                width: this.size,
                height: this.size,
                rotaion: tag.angle
            },
        });

        // //
        // str = 'flame' + i;
        // var redRectangle = viewer.entities.add({
        //     name: str,
        //     rectangle: {
        //         coordinates: Cesium.Rectangle.fromDegrees(tag.lon, tag.lat, tag.lon + this.size * 2, tag.lat + this.size),
        //         material: canvas,
        //         rotaion: tag.angle
        //     }
        // });
    }
};

/**
 * 
 * @param {int} options.type ,0;1
 * @param {array} options.points [-76.091308, 18.427501]2
 * @param {int} [options.lineColor] cesiumCesium.Color.WHITE
 * @param {int} [options.lineWidth] 5
 * @param {int} [options.text] 
 * @param {int} [options.font] "30px sans-serif"
 * @param {int} [options.textColor] 
 */
MeteoSign.prototype.ShowLine = function (options) {
    if (!options.lineColor)
        options.lineColor = Cesium.Color.WHITE;
    if (!options.lineWidth)
        options.lineWidth = 5;
    switch (options.type) {
        case 0: //
            coords = options.points;
            break;
        case 1: //
            {
                line = turf.lineString(options.points);
                var curved = turf.bezierSpline(line);
                var coords = turf.getCoords(curved);
            }
            break;
    }
    data = [];
    for (var i = 0; i < coords.length; i++) {
        data.push(coords[i][0]);
        data.push(coords[i][1]);
    }
    var redLine = viewer.entities.add({
        name: 'line',
        polyline: {
            positions: Cesium.Cartesian3.fromDegreesArray(data),
            width: options.lineWidth,
            material: options.lineColor,
        }
    });
    if (options.text) {

        if (!options.font)
            options.font = "30px sans-serif";
        if (!options.textColor)
            options.textColor = options.lineColor;
        var lableEntity = viewer.entities.add({
            position: Cesium.Cartesian3.fromDegrees(coords[coords.length - 1][0], coords[coords.length - 1][1]),
            label: {
                text: options.text,
                fillColor: options.textColor,
                font: options.font
            }
        });
    }

};

/**
 * canvas
 * @param {float} speed m/s-1
 * @return {Canvas}
 */
MeteoSign.prototype.GetWindCanvas = function (speed) {
    var canvas = document.createElement('canvas');
    canvas.width = this.size;
    canvas.height = this.size;
    var context = canvas.getContext("2d");

    //
    context.lineWidth = this.lineWidth ? this.lineWidth : this.size / 40;
    context.strokeStyle = this.color;

    var x = context.lineWidth / 2;
    var y = this.size / 4;
    //
    context.moveTo(x, y); //
    context.lineTo(x, this.size); //

    //1/4
    var disY = Math.floor((this.size - y) / 7); //
    var longPlume = (this.size - y) / 2; //
    var angle = 70; //
    var nowX = x;
    var nowY = y + context.lineWidth / 2;
    context.moveTo(nowX, nowY);

    if (speed == -1) {
        context.lineTo(nowX + longPlume, nowY);
        context.lineTo(nowX, nowY + longPlume);
        nowX = x + longPlume * 1.5;
        nowY = y;
        context.moveTo(nowX, nowY);
        context.lineTo(nowX, this.size - context.lineWidth / 2);
        context.lineTo(nowX - longPlume, this.size - context.lineWidth / 2);
        context.lineTo(nowX, this.size - longPlume - context.lineWidth / 2);
    } else {
        //
        var longNum = 0; //
        var shortNum = 0; //
        var triNum = 0; //
        //
        //1
        if (speed <= 1.5) {
            shortNum = 1;
        }
        //2
        else if (speed <= 3.3) {
            longNum = 1;
        }
        //3
        else if (speed <= 5.4) {
            longNum = 1;
            shortNum = 1;
        }
        //4
        else if (speed <= 7.9) {
            longNum = 2;
        }
        //5
        else if (speed <= 10.7) {
            longNum = 2;
            shortNum = 1;
        }
        //6
        else if (speed <= 13.8) {
            longNum = 3;
        }
        //7
        else if (speed <= 17.1) {
            longNum = 4;
            shortNum = 1;
        }
        //8
        else if (speed <= 20.7) {
            triNum = 1;
            shortNum = 1;
        }
        //9
        else if (speed <= 24.4) {
            triNum = 1;
            longNum = 1;
            shortNum = 1;
        }
        //10
        else if (speed <= 28.4) {
            triNum = 1;
            longNum = 2;
            shortNum = 1;
        }
        //11
        else if (speed <= 32.6) {
            triNum = 1;
            longNum = 3;
            shortNum = 1;
        }
        //12
        else {
            triNum = Math.floor(speed / 20);
            longNum = Math.floor((speed - triNum * 20) / 4);
            shortNum = Math.floor((speed - triNum * 20 - longNum * 4) / 2);
        }

        //
        //
        for (var i = 0; i < triNum; i++) {
            //
            context.lineTo(nowX + longPlume * Math.sin(angle * Math.PI / 180), nowY);
            context.lineTo(nowX, nowY + disY);
            nowY += 2 * disY;
            context.moveTo(nowX, nowY);
        }
        //
        for (var i = 0; i < longNum; i++) {
            context.lineTo(nowX + longPlume * Math.sin(angle * Math.PI / 180), nowY - longPlume * Math.cos(angle * Math.PI / 180));
            nowY += disY;
            context.moveTo(nowX, nowY);
        }
        //
        for (var i = 0; i < shortNum; i++) {
            //1/2
            context.lineTo(nowX + longPlume / 2 * Math.sin(angle * Math.PI / 180), nowY - longPlume / 2 * Math.cos(angle * Math.PI / 180));
            nowY += disY;
            context.moveTo(nowX, nowY);
        }
    }

    //
    context.stroke();

    return canvas;
}

/**
 * canvas
 * @param {int} type 012345678
 * @return {Canvas}
 */
MeteoSign.prototype.GetRainCanvas = function (type) {
    //
    var canvas = document.createElement('canvas');
    canvas.width = this.size;
    canvas.height = this.size;
    var context = canvas.getContext("2d");
    //
    context.fillStyle = this.color;
    radius = this.size / 7; //1/7
    context.strokeStyle = this.color;
    //
    switch (type) {
        case 0: //
            {
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 1: //
            {
                context.beginPath();
                x = this.size / 2 - radius * 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x += radius * 4;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 2: //
            {
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2 - radius * 1.2 / Math.sin(60 * Math.PI / 180);
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 - radius * 1.2;
                y = this.size / 2 + radius * 1.2 * Math.tan(30 * Math.PI / 180);
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 + radius * 1.2;
                y = this.size / 2 + radius * 1.2 * Math.tan(30 * Math.PI / 180);
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 3: //
            {
                context.beginPath();
                x = this.size / 2 + radius * 1.5;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 - radius * 1.5;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2 + radius * 1.5;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2 - radius * 1.5;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 4: //
            {
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 + radius * 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 - radius * 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2 + radius * 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2;
                y = this.size / 2 - radius * 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 5: //
            {
                context.beginPath();
                x = this.size / 2 - radius * 2;
                y = this.size / 2 - radius * 2.2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x += radius * 4;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 - radius * 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x += radius * 4;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x = this.size / 2 - radius * 2;
                y = this.size / 2 + radius * 2.2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x += radius * 4;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
        case 6: //
            {
                radius = this.size / 6;
                context.lineWidth = radius / 5;
                context.beginPath();
                x = this.size / 2;
                y = radius;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                len = radius * 4;
                x -= len / 2;
                y += radius + context.lineWidth / 2;
                context.beginPath();
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                context.lineTo(x + len / 2, y + len * Math.sin(60 * Math.PI / 180));
                context.closePath();
                context.stroke();
            }
            break;
        case 7: //
            {
                radius = this.size / 5;
                radius2 = radius / 2.5;
                context.lineWidth = radius / 5;
                x = this.size / 2 - radius;
                y = this.size / 2;
                context.arc(x, y, radius, 1 * Math.PI, 2 * Math.PI);
                x += radius * 2;
                context.moveTo(x + radius, y);
                context.arc(x, y, radius, 0, 1 * Math.PI);
                context.stroke();

                context.beginPath();
                x = this.size / 2 - radius;
                y = this.size / 2 - (radius - context.lineWidth) / 2;
                context.arc(x, y, radius2, 0, 2 * Math.PI);
                context.fill();
            }
            break;
        case 8: //
            {
                radius = this.size / 5;
                radius2 = radius / 2.5;
                context.lineWidth = radius / 5;
                x = this.size / 2 - radius;
                y = this.size / 2;
                context.arc(x, y, radius, 1 * Math.PI, 2 * Math.PI);
                x += radius * 2;
                context.moveTo(x + radius, y);
                context.arc(x, y, radius, 0, 1 * Math.PI);
                context.stroke();

                context.beginPath();
                x = this.size / 2 - radius;
                y = this.size / 2 - (radius - context.lineWidth) / 2;
                context.arc(x, y, radius2, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
                context.beginPath();
                x += radius * 2;
                y += radius - context.lineWidth;
                context.arc(x, y, radius2, 0, 2 * Math.PI);
                context.closePath();
                context.fill();
            }
            break;
    }

    return canvas;
}

/**
 * canvas
 * @param {int} type 0123456
 * @return {Canvas}
 */
MeteoSign.prototype.GetSnowCanvas = function (type) {
    //
    var canvas = document.createElement('canvas');
    canvas.width = this.size;
    canvas.height = this.size;
    var context = canvas.getContext("2d");
    //
    context.fillStyle = this.color;
    radius = this.size / 7; //1/7
    context.strokeStyle = this.color;
    //
    switch (type) {
        case 0: //
            {
                len = this.size * 0.9;
                context.lineWidth = len / 20;
                x = (this.size - len) / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - len / 2 * Math.sin(45 * Math.PI / 180);
                y = x;
                context.moveTo(x, y);
                context.lineTo(this.size - x, this.size - y);
                context.moveTo(this.size - x, y);
                context.lineTo(x, this.size - y);
                context.stroke();
                radius = len / 6;
                context.beginPath();
                context.arc(this.size / 2, this.size / 2 - radius * 2, radius, 0, 2 * Math.PI);
                context.fill();
            }
            break;
        case 1: //
            {
                len = this.size * 0.9;
                context.lineWidth = len / 20;
                x = (this.size - len) / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - len / 2 * Math.sin(45 * Math.PI / 180);
                y = x;
                context.moveTo(x, y);
                context.lineTo(this.size - x, this.size - y);
                context.moveTo(this.size - x, y);
                context.lineTo(x, this.size - y);
                context.stroke();
            }
            break;
        case 2: //
            {
                len = this.size / 2 * 0.9;
                context.lineWidth = len / 20;
                x = (this.size / 2 - len) / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                height = len / 2 * Math.sin(45 * Math.PI / 180);
                x = this.size / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size * 3 / 4 - len / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size * 3 / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();
            }
            break;
        case 3: //
            {
                len = this.size / 2 * 0.9;
                context.lineWidth = len / 20;
                x = (this.size / 2 - len) / 2;
                y = this.size * 3 / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                height = len / 2 * Math.sin(45 * Math.PI / 180);
                x = this.size / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size * 3 / 4 - len / 2;
                y = this.size * 3 / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size * 3 / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size / 2 - len / 2;
                y = this.size / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();
            }
            break;
        case 4: //
            {
                len = this.size / 3 * 0.9;
                context.lineWidth = len / 20;
                x = (this.size / 2 - len) / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                height = len / 2 * Math.sin(45 * Math.PI / 180);
                x = this.size / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size * 3 / 4 - len / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size * 3 / 4 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size / 2 - len / 2;
                y = this.size / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                x = this.size / 2 - len / 2;
                y = this.size * 3 / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - height;
                y -= height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();
            }
            break;
        case 5: //
            {
                len = this.size / 3 * 0.9;
                context.lineWidth = len / 20;
                height = len / 2 * Math.sin(45 * Math.PI / 180);
                x = this.size / 2 - len / 2;
                y = this.size / 4 - len / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                x = this.size / 2 - height;
                y = y - height;
                context.moveTo(x, y);
                context.lineTo(x + height * 2, y + height * 2);
                context.moveTo(x, y + height * 2);
                context.lineTo(x + height * 2, y);
                context.stroke();

                trilen = len * 2.5;
                x = this.size / 2 - trilen / 2;
                y = len + context.lineWidth / 2;
                context.beginPath();
                context.moveTo(x, y);
                context.lineTo(x + trilen, y);
                context.lineTo(x + trilen / 2, y + trilen * Math.sin(60 * Math.PI / 180));
                context.closePath();
                context.stroke();
            }
            break;
        case 6: //
            {
                len = this.size;
                context.lineWidth = len / 15;
                x = context.lineWidth / 2;
                y = this.size / 2;
                context.moveTo(x, y);
                context.lineTo(x, this.size - context.lineWidth / 2);
                context.lineTo(this.size - context.lineWidth / 2, this.size - context.lineWidth / 2);
                context.lineTo(this.size - context.lineWidth / 2, y);
                context.stroke();
            }
            break;
    }

    return canvas;
}

/**
 * canvas
 * @param {int} type 01
 * @return {Canvas}
 */
MeteoSign.prototype.GetFogCanvas = function (type) {
    //
    var canvas = document.createElement('canvas');
    canvas.width = this.size;
    canvas.height = this.size;
    var context = canvas.getContext("2d");

    //
    context.fillStyle = this.color;
    context.strokeStyle = this.color;
    len = this.size; //
    context.lineWidth = len / 15;
    //
    switch (type) {
        case 0: //
            {
                x = 0;
                y = this.size / 3;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                y += this.size / 3;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                context.stroke();
            }
            break;
        case 1: //
            {
                x = 0;
                y = this.size / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                y += this.size / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                y += this.size / 4;
                context.moveTo(x, y);
                context.lineTo(x + len, y);
                context.stroke();
            }
            break;
    }

    return canvas;
}

/**
 * canvas
 * @param {int} type 012
 * @return {Canvas}
 */
MeteoSign.prototype.GetCloudCanvas = function (type) {
    //
    var canvas = document.createElement('canvas');
    var context = canvas.getContext("2d");
    canvas.width = this.size;
    canvas.height = this.size;

    //
    context.fillStyle = this.color;
    context.strokeStyle = this.color;
    radius = this.size / 3;
    context.lineWidth = this.size / 15;
    x = this.size / 2;
    y = this.size / 2;
    //
    switch (type) {
        case 0: //
            {
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.stroke();
            }
            break;
        case 1: //
            {
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.stroke();
                context.beginPath();
                context.arc(x, y, radius, -Math.PI / 2, Math.PI / 2);
                context.closePath();
                context.fill();
            }
            break;
        case 2: //
            {
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.fill();
            }
            break;
    }

    return canvas;
}

/**
 * canvas
 * @param {int} type 0123
 * @return {Canvas}
 */
MeteoSign.prototype.GetSandCanvas = function (type) {
    //
    var canvas = document.createElement('canvas');
    canvas.width = this.size;
    canvas.height = this.size;
    var context = canvas.getContext("2d");

    //
    context.fillStyle = this.color;
    context.strokeStyle = this.color;
    //
    switch (type) {
        case 0: //
            {
                radius = this.size / 5;
                context.lineWidth = this.size / 20;
                x = this.size / 2;
                y = this.size / 2 - radius;
                context.arc(x, y, radius, Math.PI / 2, Math.PI * 7 / 4);
                context.moveTo(x, y + radius);
                y += radius * 2;
                context.arc(x, y, radius, -Math.PI / 2, Math.PI * 3 / 4);
                context.stroke();
            }
            break;
        case 1: //
            {
                radius = this.size / 5;
                context.lineWidth = this.size / 20;
                x = this.size / 2;
                y = this.size / 2 - radius * 0.75;
                context.arc(x, y, radius, Math.PI / 2, Math.PI * 7 / 4);
                context.moveTo(x, y + radius);
                y += radius * 2;
                context.arc(x, y, radius, -Math.PI / 2, Math.PI * 3 / 4);
                context.stroke();

                context.beginPath();
                y = this.size;
                context.moveTo(x, y);
                y = this.size * 0.12;
                context.lineTo(x, y);
                context.closePath();
                context.stroke();
                context.beginPath();
                context.moveTo(x - radius / 2, y);
                context.lineTo(x + radius / 2, y);
                context.lineTo(x, y - radius / 2 * Math.sin(60 * Math.PI / 180));
                context.closePath();
                context.fill();
            }
            break;
        case 2: //
            {
                radius = this.size / 5;
                context.lineWidth = this.size / 20;
                x = this.size / 2;
                y = this.size / 2 - radius;
                context.arc(x, y, radius, Math.PI / 2, Math.PI * 7 / 4);
                context.moveTo(x, y + radius);
                y += radius * 2;
                context.arc(x, y, radius, -Math.PI / 2, Math.PI * 3 / 4);
                context.stroke();

                context.lineWidth = this.size / 50;
                context.beginPath();
                y = this.size / 2 - context.lineWidth * 2;
                x = radius;
                context.moveTo(x, y);
                x = this.size * 0.75;
                context.lineTo(x, y);
                context.closePath();
                context.stroke();
                context.beginPath();
                y = this.size / 2 + context.lineWidth * 2;
                x = radius;
                context.moveTo(x, y);
                x = this.size * 0.75;
                context.lineTo(x, y);
                context.closePath();
                context.stroke();
                context.beginPath();
                y = this.size / 2;
                context.moveTo(x, y - radius / 2);
                context.lineTo(x, y + radius / 2);
                context.lineTo(x + radius / 2 * Math.sin(60 * Math.PI / 180), y);
                context.closePath();
                context.fill();

            }
            break;
        case 3: //
            {
                radius = this.size / 5;
                context.lineWidth = this.size / 20;
                x = this.size / 2;
                y = this.size / 2 - radius;
                context.arc(x, y, radius, Math.PI / 2, Math.PI * 7 / 4);
                context.moveTo(x, y + radius);
                y += radius * 2;
                context.arc(x, y, radius, -Math.PI / 2, Math.PI * 3 / 4);
                context.stroke();
                context.beginPath();
                y = this.size / 2;
                x = radius;
                context.moveTo(x, y);
                x = this.size * 0.75;
                context.lineTo(x, y);
                context.closePath();
                context.stroke();
                context.beginPath();
                context.moveTo(x, y - radius / 2);
                context.lineTo(x, y + radius / 2);
                context.lineTo(x + radius / 2 * Math.sin(60 * Math.PI / 180), y);
                context.closePath();
                context.fill();
            }
            break;
    }

    return canvas;
}


/**
 * canvas
 * @param {int} type 012345N6L7G8D
 * @param {Cesium.Color|String}color
 * @return {Canvas}
 */
MeteoSign.prototype.GetOtherCanvas = function (type, color) {
    if (color instanceof Cesium.Color) {
        color = color.toCssColorString();
    }
    //
    var canvas = document.createElement('canvas');
    var context = canvas.getContext("2d");
    canvas.width = this.size;
    canvas.height = this.size;
    //
    context.fillStyle = color ? color : this.color;
    context.strokeStyle = color ? color : this.color;
    //
    switch (type) {
        case 0: //
            {
                context.lineWidth = this.size / 20;
                x = this.size / 2;
                y = this.size * 0.1;
                context.moveTo(x - this.size / 10, y);
                context.lineTo(x + this.size / 10, y);
                context.stroke();
                x = this.size / 4;
                y += y;
                context.moveTo(x, this.size);
                context.lineTo(x, y);
                x += this.size / 2;
                context.lineTo(x, y);
                len = this.size / 3;
                context.lineTo(x - len, y + len);
                y += len * 2;
                context.lineTo(x, y);
                context.stroke();
                context.beginPath();
                context.moveTo(x, y);
                len = len / 3 * Math.sin(45 * Math.PI / 180);
                context.lineTo(x + len, y - len);
                context.lineTo(x + len, y + len);
                context.lineTo(x - len, y + len);
                context.closePath();
                context.fill();
            }
            break;
        case 1: //
            {
                context.lineWidth = this.size / 15;
                context.beginPath();
                len = this.size * 2 / 3;
                x = this.size / 2;
                y = this.size / 2 - context.lineWidth;
                context.moveTo(x - len / 2, y);
                context.lineTo(x + len / 2, y);
                context.lineTo(x, y - len / 2);
                context.closePath();
                context.stroke();
                context.beginPath();
                y += context.lineWidth * 2;
                context.moveTo(x - len / 2, y);
                context.lineTo(x + len / 2, y);
                context.lineTo(x, y + len / 2);
                context.closePath();
                context.stroke();
            }
            break;
        case 2: //
            {
                radius = this.size / 5;
                context.lineWidth = this.size / 10;
                x = this.size / 2;
                y = this.size / 2;
                context.arc(x, y, radius, 0, 2 * Math.PI);
                context.stroke();
                context.lineWidth = 5;
                context.beginPath();
                radius2 = radius + context.lineWidth / 2;
                context.moveTo(x - radius2, y);
                context.arc(x + radius2, y, radius2 * 2 + context.lineWidth * 2, Math.PI, Math.PI * 3 / 2);
                context.arc(x + radius2, y - radius2 - context.lineWidth * 2, radius, Math.PI * 3 / 2, Math.PI, true);
                context.closePath();
                context.fill();
                context.beginPath();
                context.moveTo(x + radius2, y);
                context.arc(x - radius2, y, radius2 * 2 + context.lineWidth * 2, 0, Math.PI / 2);
                context.arc(x - radius2, y + radius2 + context.lineWidth * 2, radius, -Math.PI * 3 / 2, 0, true);
                context.closePath();
                context.fill();
            }
            break;
        case 3: //
            {
                context.lineWidth = this.size / 15;
                x = context.lineWidth / 2;
                y = this.size / 3;
                radius = (this.size - y) / 4;
                context.moveTo(x, this.size);
                context.lineTo(x, y);
                context.arc(x + radius, y, radius, Math.PI, 2 * Math.PI, false);
                context.arc(x + radius * 3, y, radius, Math.PI, 2 * Math.PI, false);
                context.stroke();
            }
            break;
        case 4: //
            {
                context.lineWidth = this.size / 15;
                radius = this.size / 5;
                x = this.size / 2;
                y = this.size / 2;
                context.arc(x - radius, y, radius, 0, 2 * Math.PI);
                context.moveTo(x + radius * 2, y);
                context.arc(x + radius, y, radius, 0, 2 * Math.PI);
                context.stroke();
            }
            break;
        case 5: //N
            {
                context.lineWidth = this.size / 10;
                x = this.size / 4;
                y = context.lineWidth / 2;
                context.moveTo(x, this.size);
                context.lineTo(x, y);
                context.lineTo(x + this.size / 2, this.size);
                context.lineTo(x + this.size / 2, 0);
                context.stroke();
            }
            break;
        case 6: //L
            {
                context.lineWidth = this.size / 10;
                x = this.size / 4;
                y = this.size - context.lineWidth / 2;
                context.moveTo(x, 0);
                context.lineTo(x, y);
                context.lineTo(x + this.size / 2, y);
                context.stroke();
            }
            break;
        case 7: //G
            {
                context.lineWidth = this.size / 10;
                radius = this.size / 2 - context.lineWidth / 2;
                x = this.size / 2;
                y = this.size / 2;
                context.arc(this.size / 2, this.size / 2, radius, Math.PI / 5, Math.PI * 7 / 4, false);
                context.stroke();
                context.moveTo(x + radius / 3, y);
                x += radius * 4 / 5;
                context.lineTo(x, y);
                context.lineTo(x, this.size);
                context.stroke();
            }
            break;
        case 8: //D
            {
                context.lineWidth = this.size / 10;
                radius = this.size / 2 - context.lineWidth / 2;
                x = this.size / 4;
                y = this.size / 2;
                context.arc(x, y, radius, -Math.PI / 2, Math.PI / 2);
                context.moveTo(x, 0);
                context.lineTo(x, this.size);
                context.stroke();
            }
            break;
    }


    return canvas;
}

module.exports = MeteoSign;
