/**
* ndarraynew ndarray ndarray(data[, shape, stride, offset])
* scijs.ndarray{@link https://github.com/scijs/ndarray}
*@param {Array|Object}data  is a 1D array storage. It is either an instance of Array, a typed array, or an object that implements get(), set(), 
*@param {Array}[shape] is the shape of the view (Default: data.length)
*@param {Array}[stride]  is the resulting stride of the new array. (Default: row major)
*@param {Number}[offset=0]  is the offset to start the view (Default: 0)
*@returns {ndarray} an n-dimensional array view of the buffer
*@property {Array|Object}data  is a 1D array storage. It is either an instance of Array, a typed array, or an object that implements get(), set(), 
*@property {Array}[shape] is the shape of the view (Default: data.length)
*@property {Array}[stride]  is the resulting stride of the new array. (Default: row major)
*@property {Number}[offset=0]  is the offset to start the view (Default: 0)
*@name  ndarray
*@class 
*@memberof MeteoLib.Util.Scijs
*@example  
To access elements of the array, you can use the set/get methods:
 
array.get(i,j,...)
 
Retrieves element i,j,... from the array. In psuedocode, this is implemented as follows:
 
function get(i,j,...) {
return this.data[this.offset + this.stride[0] * i + this.stride[1] * j + ... ]
}
array.set(i,j,...,v)
 
Sets element i,j,... to v. Again, in psuedocode this works like this:
 
function set(i,j,...,v) {
return this.data[this.offset + this.stride[0] * i + this.stride[1] * j + ... ] = v
}
array.index(i,j, ...)
 
Retrieves the index of the cell in the underlying ndarray. In JS,
 
function index(i,j, ...) {
return this.offset + this.stride[0] * i + this.stride[1] * j + ...
}
*/

