/**
*@param {Array}array2d
*@param {TypeArrayConstructor}typeArrayConstructor
*@memberof MeteoLib.Util.Scijs.ndarray
*@static
*/
Scijs.ndarray.fromArray2d = function (array2d, typeArrayConstructor) {
    if (!array2d || !array2d.length) {
        return null;
    }
    var ndArray = Scijs.ndarray(
        new typeArrayConstructor(array2d.length * array2d[0].length),
        [array2d.length, array2d[0].length]);
    for (var i = 0; i < array2d.length; i++) {
        for (var j = 0; j < array2d[i].length; j++) {
            ndArray.set(i, j, array2d[i][j]);
        }
    }
    return ndArray;
}
/**
*@param {Array}array3d
*@param {TypeArrayConstructor}typeArrayConstructor
*@memberof MeteoLib.Util.Scijs.ndarray
*@static
*/
Scijs.ndarray.fromArray3d = function (array3d, typeArrayConstructor) {
    if (!array3d || !array3d.length
        || !array3d[0] || !array3d[0].length
        || !array3d[0][0] || !array3d[0][0].length) {
        return null;
    }
    var ndArray = Scijs.ndarray(
        new typeArrayConstructor(array3d.length * array3d[0].length * array3d[0][0].length),
        [array3d.length, array3d[0].length, array3d[0][0].length]);
    for (var i = 0; i < array3d.length; i++) {
        for (var j = 0; j < array3d[i].length; j++) {
            for (var k = 0; k < array3d[i][j].length; k++) {
                ndArray.set(i, j, k, array3d[i][j][k]);
            }
        }
    }
    return ndArray;
}

