var BinaryReader = require('../../Util/BinaryReader');
var MeteorologicalData = require('./MeteorologicalData');
var getString = require('../../Util/getString');



/**
 * 
 * @constructor
 * @memberof MeteoLib.Data.Micaps
 * @extends MeteoLib.Data.Micaps.MeteorologicalData
 *
 * @property {Boolean}isEnsemble 
 * @property {String}element  50500
 * @property {Number}description  30GBK300
 * @property {Number}level  float4
 * @property {Number}year 2014int4
 * @property {Number}month int4
 * @property {Number}day int4
 * @property {Number}hour 240-23int4
 * @property {Number}period int4
 * @property {Number}startLongitude float4
 * @property {Number}endLongitude float4
 * @property {Number}longitudeGridSpace 0float4
 * @property {Number}latitudeGridNumber int4( endLongitude - startLongitude ) / longitudeGridSpace + 1
 * @property {Number}startLatitude float4
 * @property {Number}endLatitude float4
 * @property {Number}latitudeGridSpace 0float4
 * @property {Number}longitudeGridNumber int4( endLatitude - startLatitude ) / latitudeGridSpace + 1latitudeGridNumber * longitudeGridNumber = 
 * @property {Number}isolineStartValue float4type4type110
 * @property {Number}isolineEndValue float4type4type110
 * @property {Number}isolineSpace float4type4type110
 * @property {Number}perturbationNumber ,,2
 * @property {Number}ensembleTotalNumber ,3-4
 * @property {Array.<Number>}dataArray  
 * @property {Array.<Number>}angleArray 090180270
 * @example
 * 
 * 
    var GDSClient = MeteoLib.Data.Micaps.GDSClient;
 
    GDSClient.config.gdsServerIp = "xxx";
    GDSClient.config.gdsServerPort = "xx";
 
    var gdsClient = new GDSClient({
        proxy: {
            getUrl: function (srcUrl) {
                return   "http://xxxxxx/proxy/" + srcUrl;
            }
        }
    }); 
    gdsClient.getLatestDataName("ECMWF_HR/TMP/850", "*.024").then(function (strResult) {
    console.log(strResult)
    gdsClient.getData("ECMWF_HR/TMP/850", strResult.data).then(function (bytesResult) {
 
        var gridData = new GridData();
        gridData.loadByteArray(bytesResult.data);
        console.log(gridData)
    })
})
 */
function GridData() {

    this.isEnsemble = false;// 
    //MICAPS278+*float42

    //278
    this.type = GridData.SCALAR;//short2411diamond 4diamond 11

    //20200
    this.element = "";//50500
    this.description = "";//30GBK300
    this.level = 0;//float4

    this.year;//2014int4
    this.month;//int4
    this.day;//int4
    this.hour;//240-23int4
    //MeteorologicalDatatimezoneint4
    this.period;//int4

    this.startLongitude;//float4
    this.endLongitude;//float4
    this.longitudeGridSpace;//0float4
    this.latitudeGridNumber;//int4( endLongitude - startLongitude ) / longitudeGridSpace + 1
    this.startLatitude;//float4
    this.endLatitude;//float4
    this.latitudeGridSpace;//0float4
    this.longitudeGridNumber;//int4( endLatitude - startLatitude ) / latitudeGridSpace + 1latitudeGridNumber * longitudeGridNumber = 

    this.isolineStartValue;//float4type4type110
    this.isolineEndValue;//float4type4type110
    this.isolineSpace;//float4type4type110
    //100Extent,0
    this.perturbationNumber;//,,2
    this.ensembleTotalNumber;//,3-4
    //

    //
    /*float4latitudeGridNumberlongitudeGridNumber*4(latitudeGridNumber*longitudeGridNumber*4*2)
     * */
    this.dataArray = []; //
    this.angleArray = []; //090180270
    //

}
GridData.DISCRIMINATOR = "mdfs";//mdfs4mdfs

GridData.MODEL_NAME_LENGTH = 20;//
GridData.ELEMENT_LENGTH = 50;//
GridData.DESCRIPTION_LENGTH = 30;//
GridData.SCALAR = 4;//
GridData.VECTOR = 11;//
GridData.CHARSET_NAME = "GB2312";

GridData.prototype = new MeteorologicalData();

/**
 * 
 * @param {Uint8Array|ArrayBuffer} dataBytes
 * @return {Boolean}
 */
GridData.prototype.loadByteArray = function (dataBytes) {
    //GridData
    var i = 0;
    if (dataBytes instanceof ArrayBuffer) {
        dataBytes = new Uint8Array(dataBytes);
    }
    if (dataBytes == null || dataBytes.length < 278)//
    {
        console.log("");
        return false;
    }
    if (dataBytes[0] != 109 || dataBytes[1] != 100 || dataBytes[2] != 102 || dataBytes[3] != 115)//"mdfs"
    {
        console.log("mdfs");
        return false;
    }

    var br = new BinaryReader(dataBytes);
    br.seek(4, BinaryReader.SeekOrigin.Current);
    br.isLittleEndian = true;//

    //3
    this.dataPath = "";
    this.dataName = "";
    this.originalAbsoluteDataName = "";

    //
    this.type = br.readShort();//
    if (this.type != GridData.SCALAR && this.type != GridData.VECTOR)//
    {
        console.log(": " + this.type);
        return false;
    }
    try {
        var modelNameByteArray = br.readBytes(GridData.MODEL_NAME_LENGTH);//
        this.familyName = getString(modelNameByteArray, GridData.CHARSET_NAME);//UTF8
        if (this.familyName) {
            this.familyName = String.deleteNull(this.familyName)
        }

        var elementByteArray = br.readBytes(GridData.ELEMENT_LENGTH);// 
        this.element = getString(elementByteArray, GridData.CHARSET_NAME); //UTF8
        if (this.element) {
            this.element = String.deleteNull(this.element);

        }
        var descriptionByteArray = br.readBytes(GridData.DESCRIPTION_LENGTH);// 
        this.description = getString(descriptionByteArray, GridData.CHARSET_NAME);  //UTF8
        if (this.description) {
            this.description = String.deleteNull(this.description);
        }
    } catch (e) {
        console.log("GBKUnicode");
        return false;
    }

    this.level = br.readFloat();//

    this.year = br.readInt();//
    this.month = br.readInt();//
    this.day = br.readInt();//
    this.hour = br.readInt();//
    this.timezone = br.readInt();//
    this.period = br.readInt();//

    this.startLongitude = br.readFloat();//
    this.endLongitude = br.readFloat();//
    this.longitudeGridSpace = br.readFloat();//
    this.latitudeGridNumber = br.readInt();//
    this.startLatitude = br.readFloat();//
    this.endLatitude = br.readFloat();//
    this.latitudeGridSpace = br.readFloat();//
    this.longitudeGridNumber = br.readInt();//

    this.isolineStartValue = br.readFloat();
    this.isolineEndValue = br.readFloat();
    this.isolineSpace = br.readFloat();

    this.perturbationNumber = br.readShort();//
    this.ensembleTotalNumber = br.readShort();//
    if (this.ensembleTotalNumber == 0 && this.perturbationNumber == 0)//0
        this.isEnsemble = false;
    else
        this.isEnsemble = true;
    var extentByteArray = br.readBytes(96);
    extentByteArray = [];//

    //
    if (this.latitudeGridNumber * this.longitudeGridNumber <= 0)//
    {
        console.log(", latitudeGridNumber = " + this.latitudeGridNumber + " longitudeGridNumber = " + this.longitudeGridNumber);
        return false;
    }
    this.dataArray = br.readFloats(this.latitudeGridNumber * this.longitudeGridNumber);//new float[latitudeGridNumber * longitudeGridNumber];

    if (this.type == GridData.VECTOR)//
    {
        this.angleArray = br.readFloats(this.latitudeGridNumber * this.longitudeGridNumber); //new float[latitudeGridNumber * longitudeGridNumber];
    }

    //

    const gridData = this;
    var width = gridData.latitudeGridNumber,
        height = gridData.longitudeGridNumber;

    var bbox = [0, 0, 0, 0];

    var endLon = gridData.startLongitude + width * gridData.longitudeGridSpace;

    if (endLon < 180) {
        bbox[0] = gridData.startLongitude;
        bbox[2] = gridData.endLongitude;
    } else {
        bbox[0] = 180 - endLon;
        bbox[2] = 180;
    }
    bbox[1] = gridData.startLatitude < gridData.endLatitude ? gridData.startLatitude : gridData.endLatitude;
    bbox[3] = gridData.startLatitude > gridData.endLatitude ? gridData.startLatitude : gridData.endLatitude;
    this.bbox = bbox;
    this.width = width
    this.height = height

    //Micaps0360-180180
    //180360
    if (endLon > 180) {
        var width = gridData.latitudeGridNumber,
            height = gridData.longitudeGridNumber;
        var newDataArray = new Float32Array(gridData.dataArray.length);
        var newAngleArray, isVector = gridData.type == GridData.VECTOR;
        if (isVector) {
            newAngleArray = new Float32Array(gridData.dataArray.length);
        }

        var idx = 0;
        for (var i = 0; i < height; i++) {
            for (var j = 0; j < width; j++) {
                var destIdx = i * width + j + width / 2;
                if (j > width / 2) {
                    destIdx = i * width + j - width / 2;
                }
                newDataArray[destIdx] = gridData.dataArray[idx];
                if (isVector) {
                    newAngleArray[destIdx] = gridData.angleArray[idx];
                }
                idx++;
            }
        }

        gridData.dataArray = newDataArray;
        if (isVector) {
            gridData.angleArray = newAngleArray;
        }

    }

    return true;
}//end method

module.exports = GridData;

