function StationData() {
    this._type = 1;//short2
    this._description = "";//100
    this._level = 0;//float4
    this._levelDescription = "";//50
    this._year;//2014int4
    this._month;//int4
    this._day;//int4
    this._hour;//240-23int4
    this._minute = 0;//0-59,int4
    this._second = 0;//0-59int4
    //MeteorologicalDatatimezoneint4
    //100Extent,0

    //
    /*
    2
    1int
    IDIDIDshort 2ID 2(short 2)ID4
    : 1: byte, 2: short, 3: int, 4: long, 5: float, 6: double, 7: string
    14: ID(int 4), (float 4), (float 4), (,short 2)
    : ID(short 2), ()
    */
    //private Map < Integer, Map < Integer, String > > stationDataMap;// <ID,<ID>>,Map
    this._stationDataMap = {};

}
StationData.prototype = new MeteorologicalData();

