StationData.DATA_DESCRIPTION_LENGTH = 100;//
StationData.LEVEL_DESCRIPTION_LENGTH = 50;//
StationData.CHARSET_NAME = "GB2312";
//288
StationData.DISCRIMINATOR = "mdfs";//mdfs4mdfs

/**
 * 
 * @param {Uint8Array|ArrayBuffer} dataBytes
 * @return {Boolean}
 */
StationData.prototype.loadByteArray = function (dataBytes) {
    //StationData
    var i = 0;
    var j = 0;
    if (this.stationDataMap != null)//
    {
        for (var p in this.stationDataMap) {
            if (this.stationDataMap.hasOwnProperty(p)) {
                delete this.stationDataMap[p];
            }
        }
    }
    if (dataBytes instanceof ArrayBuffer) {
        dataBytes = new Uint8Array(dataBytes);
    }

    if (dataBytes == null || dataBytes.length < 292)//+1
    {
        console.log("");
        return false;
    }
    if (dataBytes[0] != 109 || dataBytes[1] != 100 || dataBytes[2] != 102 || dataBytes[3] != 115)//"mdfs"
    {
        console.log("mdfs");
        return false;
    }

    var br = new BinaryReader(dataBytes);
    br.seek(4, BinaryReader.SeekOrigin.Current);
    br.isLittleEndian = true;
    //
    this.type = br.readShort();//byteBuffer.getShort();//
    try {
        var descriptionByteArray = br.readBytes(StationData.DATA_DESCRIPTION_LENGTH);
        this.description = getString(descriptionByteArray, StationData.CHARSET_NAME);
        this.description = String.deleteNull(this.description);
        this.level = br.readFloat();
        var levelDescriptionByteArray = br.readBytes(StationData.LEVEL_DESCRIPTION_LENGTH);
        this.levelDescription = getString(levelDescriptionByteArray, StationData.CHARSET_NAME);
        this.levelDescription = String.deleteNull(this.levelDescription);
    } catch (e) {
        console.log("GBKUnicode");
        return false;
    }
    this.year = br.readInt();//
    this.month = br.readInt();//
    this.day = br.readInt();//
    this.hour = br.readInt();//
    this.minute = br.readInt();//
    this.second = br.readInt();//
    this.timezone = br.readInt();//

    var extentByteArray = br.readBytes(100);
    extentByteArray = [];
    //byte extentByteArray[] = new byte[100];//1000
    //byteBuffer.get(extentByteArray, 0, 100);//
    //

    var stationNumber = br.readInt();//1: 
    if (stationNumber < 0)//0
        return false;

    var elementCategoryNumber = br.readShort();//ID
    var elementId = 0;
    var elementType = 0;
    if (elementCategoryNumber < 0)
        return false;

    var elementTypeMap = {};
    for (i = 0; i < elementCategoryNumber; i++)//ID
    {
        elementId = br.readShort();//ID
        elementType = br.readShort();//
        elementTypeMap[elementId] = elementType;
    }

    var currentStationElementMap;
    for (i = 0; i < stationNumber; i++) {
        try {


            //
            currentStationElementMap = {};//new HashMap<Integer, String>();
            var stationId = br.readInt();//3ID
            var longitude = br.readFloat();//
            var latitude = br.readFloat();//
            var currentStationElementNumber = br.readShort();//
            currentStationElementMap[1] = longitude;//.toString();
            currentStationElementMap[2] = latitude;//.toString();

            for (j = 0; j < currentStationElementNumber; j++) {//
                elementId = br.readShort();//ID
                elementType = elementTypeMap[elementId];//
                switch (elementType) {
                    //: 1: byte, 2: short, 3: int, 4: long, 5: float, 6: double, 7: string
                    case 1:
                        var b = br.readBytes(1);
                        currentStationElementMap[elementId] = b[0];
                        break;
                    case 2:
                        var shortValue = br.readShort();//
                        currentStationElementMap[elementId] = shortValue;//ID
                        break;
                    case 3:
                        var integer = br.readInt();//
                        currentStationElementMap[elementId] = integer;//ID
                        break;
                    case 4:
                        var l = byteBuffer.readInt64();//
                        currentStationElementMap[elementId] = l;//ID
                        break;
                    case 5:
                        var f = br.readFloat();//
                        currentStationElementMap[elementId] = f;//ID
                        break;
                    case 6:
                        var d = byteBuffer.readDouble();//
                        currentStationElementMap[elementId] = d;//ID
                        break;
                    case 7:
                        var stringBytesNumber = br.readShort();//CHARSET_NAME
                        if (stringBytesNumber <= 0)//,
                        {
                            return false;
                        }
                        var stringByteArray = br.readBytes(stringBytesNumber);

                        var s = null;
                        try {
                            s = getString(stringByteArray, StationData.CHARSET_NAME); //new String(stringByteArray, CHARSET_NAME).trim();//UTF8
                            currentStationElementMap[elementId] = s;//ID
                        } catch (e) {
                            return false;
                        }
                        break;
                    default:
                        break;
                }
            }
            this.stationDataMap[stationId] = currentStationElementMap;//
        } catch (e) {
            console.log(e)
        }
    }

    return true;
}//end method

module.exports = StationData;
