
    /**
   * 
   *@property {Array.<Number>}PrjArguments
   *@property {Number}ProjectionID
   *@property {String}Datum= "WGS-84"
   *@property {String}Units= "Meters"
   *@constructor
   *@memberof MeteoLib.Data.RasterData
   */
    function HdrProjectionInfo() {
        this.Datum = "WGS-84";
        this.Name = string.Empty;
        this.Units = "Meters";
        this.ProjectionID;
        this.PrjArguments = [];
        /**
        *
        *@return {String}
        */
        this.ToString = function () {
            if (this.PrjArguments == null || this.ProjectionID == 0) {
                return null;
            }
            var str = "projection info = {";
            str = str + this.ProjectionID.toString() + ",";
            for (var i = 0; i < this.PrjArguments.length; i++) {
                str = str + this.PrjArguments[i].toString() + ",";
            }
            str = str + this.Datum + ",";
            str = str + this.Name + ",";
            return str + "units = " + this.Units + "}";
        }
        /**
        *
        *@return {String}
        */
        this.ToArgsString = function (split) {
            var text = this.ProjectionID.toString();
            if (this.PrjArguments != null) {
                var prjArguments = this.PrjArguments;
                for (var i = 0; i < prjArguments.Length; i++) {
                    var num = prjArguments[i];
                    text = text + split + num.toString();
                }
            }
            return text;
        }
    }
    module.exports= HdrProjectionInfo;

