 
var ndarray = require('ndarray')
var interp = require('ndarray-linear-interpolate');

/**
* ,
* @property {Array.<Array.<Number>>|Array.<TypeArray>}bands
* @property {Array.<Number>}calTables
* @property {Number}width
* @property {Number}height
* @property {Number}west
* @property {Number}south
* @property {Number}north
* @property {Number}east
* @property {RasterDataset.DataType}dataType=RasterDataset.DataType.UnKnown
* @property {Number}nodataValue=NaN
* @memberof MeteoLib.Data
* @constructor
*/
function RasterDataset() {
    /// <summary>,</summary>

    this.bands = [];
    this.calTables = []; //
    this.width = 0;
    this.height = 0;
    this.west = 0;
    this.south = 0;
    this.north = 0;
    this.east = 0;
    this.prop = null;
    this.dataType = RasterDataset.DataType.UnKnown;
    this.nodataValue = NaN;
    this.minMax = [];
}
/**
* 
*
*@property {enum}Byte
*@property {enum}Short
*@property {enum}Int
*@property {enum}Float
*@property {enum}Double
*@property {enum}UnKnown
*/
RasterDataset.DataType = {
    Byte: 1,
    Short: 2,
    Int: 4,
    Float: 5,
    Double: 8,
    UnKnown: 0
}

var scratchBandValues = [];
/**
*@param {Array<Array<Number>>|Array<TypeArray>}result
*@return  {Array<Array<Number>>|Array<TypeArray>}result
*/
RasterDataset.prototype.getGridXY = function (result) {
    result = [[], []];
    for (var i = 0; i < this.height; i++) {
        result[1][i] = this.north + (this.south - this.north) * i / this.height;
    }
    for (var i = 0; i < this.width; i++) {
        result[0][i] = this.west + (this.east - this.west) * i / this.width;
    }
    if (Float32Array) {
        result[1] = new Float32Array(result[1]);
    }
    if (Float32Array) {
        result[0] = new Float32Array(result[0]);
    }
    return result;
}
/**
*@param {Array<Number>|TypeArray}result
*@return {Array<Number>|TypeArray}result
*/
RasterDataset.prototype.getYCoords = function (result) {
    result = [];
    for (var i = 0; i < this.height; i++) {
        result[i] = this.north + (this.south - this.north) * i / this.height;
    }
    if (Float32Array) {
        result = new Float32Array(result);
    }
    return result
}
/**
*@param {Array<Number>|TypeArray}result
*@return {Array<Number>|TypeArray}result
*/
RasterDataset.prototype.getXCoords = function (result) {
    result = [];
    for (var i = 0; i < this.width; i++) {
        result[i] = this.west + (this.east - this.west) * i / this.width;
    }
    if (Float32Array) {
        result = new Float32Array(result);
    }
    return result
}
/**
*
*@param {function}pixelFunction
*/
RasterDataset.prototype.forEachPixel = function (pixelFunction) {
    if (!pixelFunction) {
        return;
    }
    scratchBandValues = [];
    for (var row = 0; row < this.height; row++) {
        for (var col = 0; col < this.width; col++) {
            var pos = row * this.width + col;
            for (var bandNo = 0; bandNo < this.bands.length; bandNo++) {
                scratchBandValues[bandNo] = this.bands[bandNo][pos];
            }
            pixelFunction(row, col, scratchBandValues, pos);
        }
    }
}
/**
* 
* @param {number|Number} bandNo 
* @param {number|Number} lon 
* @param {number|Number} lat 
*/
RasterDataset.prototype.selectPoint = function (bandNo, lon, lat) {
    /// <summary></summary>
    /// <param name="bandNo" type="number"></param>
    /// <param name="lat" type="number"></param>

    var x = Math.ceil(((lon - this.west) * this.width / (this.east - this.west))/*.toFixed(0)*/);
    var y = Math.ceil(((lat - this.north) * this.height / (this.south - this.north))/*.toFixed(0)*/);
    var pi = y * this.width + x;
    pi = parseInt(pi);//.toFixed(0);
    return this.bands[bandNo][pi];
}

/**
* xy
* @param {number|Number} lon 
* @param {number|Number} lat 
* @returns {{}} 
*/
RasterDataset.prototype.lonLat2XY = function (lon, lat) {
    var x = Math.ceil(((lon - this.west) * this.width / (this.east - this.west)));
    var y = Math.ceil(((lat - this.north) * this.height / (this.south - this.north)));
    return {
        x: x,
        y: y
    }
}

/**
* xy
* @param {number|Number} x  
* @param {number|Number} y  
* @returns {{ {
*   lon: lon,
*   lat: lat
*   }}} 
*/
RasterDataset.prototype.XY2Lonlat = function (x, y) {
    var lon = this.west + (this.east - this.west) * x / this.width;
    var lat = this.north + (this.south - this.north) * y / this.height
    return {
        lon: lon,
        lat: lat
    }
}

/**
* 
*/
RasterDataset.prototype.lonLat2BandArrayPostion = function (lon, lat) {
    /// <summary></summary>

    if (lon > this.east || lon < this.west || lat > this.north || lat < this.south) {
        return -1;
    }
    var xy = this.lonLat2XY(lon, lat);
    var pi = xy.y * this.width + xy.x;
    pi = parseInt(pi);//.toFixed(0);
    return pi;
}

/**
* 
*/
RasterDataset.prototype.bandArrayPostion2LonLat = function (pos) {
    /// <summary></summary>

    var x = pos % this.width;
    var y = (pos - x) / this.width;
    var lonlat = this.XY2Lonlat(x, y);
    return lonlat;
}

/**
* xy
* @param {Number|number} west 
* @param {Number|number} north 
* @param {Number|number} east 
* @param {Number|number} south 
*/
RasterDataset.prototype.geoRectangle2XYRectangle = function (west, north, east, south) {
    /// <summary>xy</summary>
    /// <param name="west" type="Number|number"></param>
    /// <param name="north" type="Number|number"></param>
    /// <param name="east" type="Number|number"></param>
    /// <param name="south" type="Number|number"></param>

    var nw = this.lonLat2XY(west, north);
    var sw = this.lonLat2XY(west, south);
    var se = this.lonLat2XY(east, south);
    var ne = this.lonLat2XY(east, north);
    return {
        NW: nw,
        SW: sw,
        SE: se,
        NE: ne,
        width: ne.x - nw.x,
        height: se.y - ne.y
    }
}

/**
* 
* @param {RasterDataset.DataType} dataType 
*/
RasterDataset.prototype.createNewBand = function (dataType) {
    /// <summary></summary>
    /// <param name="dataType" type="RasterDataset.DataType"></param>

    if (!dataType) {
        dataType = this.dataType;
    }
    var buffer = null;
    switch (dataType) {
        case RasterDataset.DataType.Byte:
            buffer = new Int8Array(this.width * this.height);
            break;
        case RasterDataset.DataType.Short:
            buffer = new Int16Array(this.width * this.height);
            break;
        case RasterDataset.DataType.Int:
            buffer = new Int32Array(this.width * this.height);
            break;
        case RasterDataset.DataType.Float:
            buffer = new Float32Array(this.width * this.height);
            break;
        case RasterDataset.DataType.Double:
            buffer = new Float64Array(this.width * this.height);
            break;
        default:
            buffer = new Array(this.width * this.height);
    }
    return buffer;
}

/**
* 
* @param {Number|number} bandNo 
* @param {Number|number} west 
* @param {Number|number} north 
* @param {Number|number} east 
* @param {Number|number} south 
* @param {Array.<Number>|TypeArray} result
*/
RasterDataset.prototype.selectRectangle = function (bandNo, west, north, east, south, buffer) {
    /// <summary></summary>
    /// <param name="bandNo" type="Number|number"></param>
    /// <param name="west" type="Number|number"></param>
    /// <param name="north" type="Number|number"></param>
    /// <param name="east" type="Number|number"></param>
    /// <param name="south" type="Number|number"></param>

    var rec = this.geoRectangle2XYRectangle(west, north, east, south);
    if (!buffer) {
        switch (this.dataType) {
            case RasterDataset.DataType.Byte:
                buffer = new Int8Array(rec.width * rec.height);
                break;
            case RasterDataset.DataType.Short:
                buffer = new Int16Array(rec.width * rec.height);
                break;
            case RasterDataset.DataType.Int:
                buffer = new Int32Array(rec.width * rec.height);
                break;
            case RasterDataset.DataType.Float:
                buffer = new Float32Array(rec.width * rec.height);
                break;
            case RasterDataset.DataType.Double:
                buffer = new Float64Array(rec.width * rec.height);
                break;
            default:
                buffer = new Array(rec.width * rec.height);
        }
    }
    var ci = 0;
    for (var i = rec.NW.y; i < rec.NW.y + rec.height; i++) {
        for (var j = rec.NW.x; j < rec.NW.x + rec.width; j++) {
            var pi = i * this.width + j;
            pi = pi.toFixed(0);
            buffer[ci] = this.bands[bandNo][pi];
            ci++;
        }
    }
    var ds = new RasterDataset();

    ds.bands = [buffer];
    ds.height = rec.height;
    ds.width = rec.width;
    ds.west = west;
    ds.south = south;
    ds.north = north;
    ds.east = east;
    ds.calTables = this.calTables;
    ds.prop = this.prop;
    ds.dataType = this.dataType;

    return ds;
}


/**
* 
* @param {Number|number} lon  
* @param {Number|number} lat  
*/
RasterDataset.prototype.isInside = function (lon, lat) {
    /// <summary></summary>
    /// <param name="lon" type="Number|number"></param>
    /// <param name="lat" type="Number|number"></param>
    var pos = this.lonLat2BandArrayPostion(lon, lat);
    var inside = false;
    if (pos >= 0 && pos < this.width * this.height) {
        inside = this.bands[0][pos] > 0;
    }
    return inside;
}

/**
* 
* @param {Number|number} lon  
* @param {Number|number} lat  
*/
RasterDataset.prototype.isOutside = function (lon, lat) {
    /// <summary></summary>
    /// <param name="lon" type="Number|number"></param>
    /// <param name="lat" type="Number|number"></param>
    var pos = this.lonLat2BandArrayPostion(lon, lat);
    if (pos >= 0 && pos < this.width * this.height) {
        return this.bands[0][pos] <= 0;
    } else {
        return true;
    }
}

/**
* 
* 
* 5
* @param {RasterDataset} polygonDataset 
*/
RasterDataset.prototype.getOutsideSampler = function (polygonDataset) {
    /// <summary>
    /// 
    /// 5
    /// 
    /// </summary>
    /// <param name="polygonDataset" type="RasterDataset"></param>

    if (this.outsideSampler && this.outsideSampler != null) {
        return this.outsideSampler;
    }

    var bandNo = 0;

    var sampler = this.createNewBand(RasterDataset.DataType.Byte);

    for (var y = 0; y < this.height; y++) {
        for (var x = 0; x < this.width; x++) {
            var lonlat = this.XY2Lonlat(x, y);
            var pos = y * this.width + x;
            if (polygonDataset.isOutside(lonlat.lon, lonlat.lat)) {
                sampler[pos] = 1;
            } else {
                sampler[pos] = -1;
            }
        }
    }
    this.outsideSampler = sampler;
    return this.outsideSampler;
}

/**
* 
* 
* 5
* @param {RasterDataset} polygonDataset 
*/
RasterDataset.prototype.getInsideSampler = function (polygonDataset) {
    /// <summary> 
    /// 
    /// 
    /// 5
    /// </summary>
    /// <param name="polygonDataset" type="RasterDataset"></param>

    if (this.insideSampler && this.insideSampler != null) {
        return this.insideSampler;
    }

    var bandNo = 0;

    var sampler = this.createNewBand(RasterDataset.DataType.Byte);

    for (var y = 0; y < this.height; y++) {
        for (var x = 0; x < this.width; x++) {
            var lonlat = this.XY2Lonlat(x, y);
            var pos = y * this.width + x;
            if (polygonDataset.isInside(lonlat.lon, lonlat.lat)) {
                sampler[pos] = 1;
            } else {
                sampler[pos] = -1;
            }
        }
    }
    this.insideSampler = sampler;
    return this.insideSampler;
}

/**
*  ,
*  00byte
*  00
* @param {RasterDataset} polygonDataset 
*/
RasterDataset.prototype.selectPolygon = function (polygonDataset, bandNoList) {
    /// <summary></summary> 
    /// <param name="polygonDataset" type="RasterDataset"></param>

    var ds = this.clone();

    if (!bandNoList || bandNoList === null) {
        bandNoList = [];
        for (var bandNo = 0; bandNo < ds.bands.length; bandNo++) {
            bandNoList.push(bandNo + 1);
        }
    } else if (typeof bandNoList === 'number') {
        bandNoList = [bandNoList];
    }
    for (var i = 0; i < ds.bands.length; i++) {
        var bandNo = i + 1;
        var contains = false;
        for (var j = 0; j < bandNoList.length; j++) {
            if (bandNo == bandNoList[j]) {
                contains = true;
                break;
            }
        }
        if (!contains) {
            delete ds.bands[i];
        }
    }
    var sampler = this.getInsideSampler(polygonDataset);
    for (var i = 0; i < bandNoList.length; i++) {
        var bandNo = bandNoList[i] - 1;
        for (var y = 0; y < this.height; y++) {
            for (var x = 0; x < this.width; x++) {
                var pos = y * this.width + x;
                if (sampler[pos] > 0) {
                    ds.bands[bandNo][pos] = this.bands[bandNo][pos];
                } else {
                    ds.bands[bandNo][pos] = this.nodataValue;
                }
            }
        }
    }
    sampler = [];
    //delete sampler;
    return ds;
}

/**
*  ,
*  00byte
*  00
* @param {RasterDataset} polygonDataset 
* @param {Number|Array.<Number>} bandNoList 
*/
RasterDataset.prototype.removePolygon = function (polygonDataset, bandNoList) {
    /// <summary></summary>
    /// <param name="polygonDataset" type="RasterDataset"></param>
    /// <param name="bandNoList" type="Number|Array.<Number>"></param>

    var ds = this.clone();

    if (!bandNoList || bandNoList === null) {
        bandNoList = [];
        for (var bandNo = 0; bandNo < ds.bands.length; bandNo++) {
            bandNoList.push(bandNo + 1);
        }
    } else if (typeof bandNoList === 'number') {
        bandNoList = [bandNoList];
    }
    for (var i = 0; i < ds.bands.length; i++) {
        var bandNo = i + 1;
        var contains = false;
        for (var j = 0; j < bandNoList.length; j++) {
            if (bandNo == bandNoList[j]) {
                contains = true;
                break;
            }
        }
        if (!contains) {
            delete ds.bands[i];
        }
    }
    var sampler = this.getOutsideSampler(polygonDataset);
    for (var i = 0; i < bandNoList.length; i++) {
        var bandNo = bandNoList[i] - 1;
        for (var y = 0; y < this.height; y++) {
            for (var x = 0; x < this.width; x++) {
                var pos = y * this.width + x;
                if (sampler[pos] > 0) {
                    ds.bands[bandNo][pos] = this.bands[bandNo][pos];
                } else {
                    ds.bands[bandNo][pos] = this.nodataValue;
                }
            }
        }
    }
    sampler = [];
    return ds;
}


/**
* 
*/
RasterDataset.prototype.clone = function () {
    /// <summary></summary>

    var ds = new RasterDataset();
    ds.bands = new Array(this.bands.length);
    for (var i = 0; i < this.bands.length; i++) {
        ds.bands[i] = this.createNewBand();
    }
    ds.height = this.height;
    ds.width = this.width;
    ds.west = this.west;
    ds.south = this.south;
    ds.north = this.north;
    ds.east = this.east;
    ds.calTables = this.calTables;
    ds.prop = this.prop;
    ds.dataType = this.dataType;
    ds.nodataValue = this.nodataValue;
    return ds;
}

/**
 * 
 * @param {Number} band
 * @param {Number} west
 * @param {Number} south
 * @param {Number} east
 * @param {Number} north
 * @param {Number} outWidth
 * @param {Number} outHeight
 * @param {Array.<Number>} outMinMax
 * @param {Array.<Number>|TypeArray} result
 */
RasterDataset.prototype.resample = function (band, west, south, east, north, outWidth, outHeight, minMax, ret) {


    var ndarr = ndarray(this.bands[band - 1], [this.height, this.width]);
    var x0y0 = this.lonLat2XY(west, north);
    var x0 = parseInt(x0y0["x"]), y0 = parseInt(x0y0["y"]);
    var x1y1 = this.lonLat2XY(east, south);
    var x1 = parseInt(x1y1["x"]), y1 = parseInt(x1y1["y"]);
    var deltX = (x1 - x0) / outWidth;
    var deltY = (y1 - y0) / outHeight;
    if (!ret) ret = new Float32Array(outHeight * outWidth);
    var x, y;
    minMax = [];
    minMax[0] = Number.MAX_VALUE;
    minMax[1] = Number.MIN_VALUE;
    var index = 0;
    for (var row = 0; row < outHeight; row++) {
        y = y0 + row * deltY;
        for (var col = 0; col < outWidth; col++) {
            x = x0 + col * deltX;
            var v = interp(ndarr, y, x);
            ret[index++] = v;
            if (isNaN(v)) {
                continue;
            }
            minMax[0] = Math.min(v, minMax[0]);
            minMax[1] = Math.max(v, minMax[1]);
        }
    }
    return ret;
}

module.exports = RasterDataset;
