function HdrFileParser() { }
/**
*
*@param {String}hdrfileContent
*@return {MeteoLib.Data.RasterData.HdrFile}
*/
HdrFileParser.ParseFromHdrfile = function (hdrfileContent) {
    var array =
        [
            "^\\s*\\bdescription\\b\\s*=\\s*{(.*)}",
            "^\\s*\\bsamples\\b\\s*=\\s*(\\d*)",
            "^\\s*\\blines\\b\\s*=\\s*(\\d*)",
            "^\\s*\\bbands\\b\\s*=\\s*(\\d*)",
            "^\\s*\\bheader\\b\\s*\\boffset\\b\\s*=\\s*(\\d*)",
            "^\\s*\\bfile\\b\\s*\\btype\\b\\s*=\\s*(.*)",
            "^\\s*\\bdata\\b\\s*\\btype\\b\\s*=\\s*(\\d*)",
            "^\\s*\\binterleave\\b\\s*=\\s*(.*)",
            "^\\s*\\bsensor\\b\\s*\\btype\\b\\s*=\\s*(.*)",
            "^\\s*\\bbyte\\b\\s*\\border\\b\\s*=\\s*(\\d*)",
            "^\\s*\\bmap\\b\\s*\\binfo\\b\\s*=\\s*{(.*),(.*),(.*),(.*),(.*),(.*),(.*),(.*)(,(.*))*}",
            "^\\s*\\bband\\b\\s*\\bnames\\b\\s*=\\s*{\\s*([^}]*)",
            "^\\s*\\bgeo\\b\\s*\\bpoints\\b\\s*=\\s*{\\s*([^}]*)}",
            "^\\s*\\bmajor\\b\\s*\\bframe\\b\\s*\\boffsets\\b\\s*=\\s*{(.*),(.*)}"
        ];
    var hdrFile = new HdrFile();
    var pattern = "\\bprojection\\b\\s*\\binfo\\b\\s*=\\s*{(\\d+),.+}"; //"\\bprojection\\b\\s*\\binfo\\b\\s*=\\s*{(?<ID>\\d+),.+}";
    var input = hdrfileContent; //File.ReadAllText(hdrfile, Encoding.Default);
    var regex = new RegExp(pattern);

    // var match = Regex.Match(input, pattern, RegexOptions.IgnoreCase);
    if (regex.test(input)/*match.Success*/) {
        var match = regex.exec(input);
        hdrFile.HdrProjectionInfo = HdrFileParser.GetHdrProjectionInfo(match[1]);
    }
    var array2 = hdrfileContent.split(/[\r\n]+/); // File.ReadAllLines(hdrfile, Encoding.Default);
    var num = 0;
    var array3 = array2;
    for (var i = 0; i < array3.length; i++) {
        var text = array3[i];
        var j = 0;
        while (j < array.length) {
            regex = new RegExp(array[j]);
            var success = regex.test(text);
            if (success) {
                var result = regex.exec(text);
                switch (j) {
                    case 0:
                        hdrFile.Description = result[1];
                        break;
                    case 1:
                        hdrFile.Samples = parseInt(String.IsNullOrEmpty(result[1]) ? "0" : result[1]);
                        break;
                    case 2:
                        hdrFile.Lines = parseInt(String.IsNullOrEmpty(result[1]) ? "0" : result[1]);
                        break;
                    case 3:
                        hdrFile.Bands = parseInt(String.IsNullOrEmpty(result[1]) ? "0" : result[1]);
                        break;
                    case 4:
                        hdrFile.HeaderOffset = parseInt(String.IsNullOrEmpty(result[1]) ? "0" : result[1]);
                        break;
                    case 5:
                        hdrFile.FileType = result[1];
                        break;
                    case 6:
                        hdrFile.DataType = parseInt(String.IsNullOrEmpty(result[1]) ? "0" : result[1]);
                        break;
                    case 7:
                        hdrFile.Intertleave = HdrFileParser.GetEnuminterleave(result[1]);
                        break;
                    case 8:
                        hdrFile.SensorType = result[1];
                        break;
                    case 9:
                        hdrFile.ByteOrder = HdrFileParser.GetEnumByteOrder(result[1]);
                        break;
                    case 10:
                        var projName = result[1];
                        var cx = result[2];
                        var cy = result[3];

                        var clon = result[4];
                        var clat = result[5];

                        var elon = result[4];
                        var elat = result[5];
                        if (elon.indexOf("E") == -1 && elon.indexOf("e") == -1) {
                            elon = null;
                        }
                        if (elat.indexOf("E") == -1 && elat.indexOf("e") == -1) {
                            elat = null;
                        }

                        var rlon = result[6];
                        var rlat = result[7];

                        var erlon = result[6];
                        var erlat = result[7];
                        if (erlon.indexOf("E") == -1 && erlon.indexOf("e") == -1) {
                            erlon = null;
                        }
                        if (erlat.indexOf("E") == -1 && erlat.indexOf("e") == -1) {
                            erlat = null;
                        }

                        var project = result[8];
                        var units = result[9];

                        hdrFile.MapInfo = HdrFileParser.GetMapInfo(projName, cx, cy, clon, clat, elon, elat, rlon, erlon, rlat, erlat, project, units);
                        break;
                    case 11:
                        hdrFile.BandNames = HdrFileParser.GetBandNames(result[1], text, array2, num);
                        break;
                    case 12:
                        hdrFile.GeoPoints = HdrFileParser.GetGeoPoint(result[1]);
                        break;
                    case 13:
                        hdrFile._majorFrameOffsets[0] = parseInt(result[1]);
                        hdrFile._majorFrameOffsets[1] = parseInt(result[2]);
                        break;
                    default:
                        break;
                }
                break;
            }
            else {
                j++;
            }
        }
        num++;
    }
    HdrFileParser.ParseSatelliteSensor(hdrFile);
    return hdrFile;
}

HdrFileParser.ParseSatelliteSensor = function (hdr) {
    if (String.IsNullOrEmpty(hdr.Description) || String.IsNullOrEmpty(hdr.SensorType)) {
        return;
    }
    var num = -1;
    try {
        num = parseInt(hdr.SensorType)
        if (isNaN(num)) {
            return;
        }
    } catch (e) {
        return;
    }
    //if (!short.TryParse(hdr.SensorType, out num))
    //{
    //return;
    //}
    var pattern = "\\S*\\s*\\S*filedatetime:(\\d{4})(\\d{2})(\\d{2})\\s*(\\d{2})(\\d{2})(\\d{2})\\S*";
    var minValue = Date.parse("0001/01/01 00:00");
    var regex = new RegExp(pattern);
    if (regex.IsMatch(hdr.Description.toLocaleLowerCase())) {
        var match = regex.Match(hdr.Description.toLocaleLowerCase());
        var dtStr = match.Groups["year"].Value +
            "-" +
            match.Groups["month"].Value +
            "-" +
            match.Groups["day"].Value +
            " " +
            match.Groups["hour"].Value +
            ":" +
            match.Groups["minute"].Value +
            ":" +
            match.Groups["second"].Value;
        try {
            minValue = Date.parse(dtStr);
        } catch (e) {
            minValue = Date.parse("0001/01/01 00:00");
        }
        //if (!DateTime.TryParse(string.Concat(new string[]
        //{
        //        match.Groups["year"].Value,
        //        "-",
        //        match.Groups["month"].Value,
        //        "-",
        //        match.Groups["day"].Value,
        //        " ",
        //        match.Groups["hour"].Value,
        //        ":",
        //        match.Groups["minute"].Value,
        //        ":",
        //        match.Groups["second"].Value
        //}), out minValue))
        //{
        //minValue = DateTime.MinValue;
        //}
    }
}

HdrFileParser.GetHdrProjectionInfo = function (prjInfoString) {
    if (String.IsNullOrEmpty(prjInfoString)) {
        return null;
    }
    prjInfoString = prjInfoString.ToUpper();
    prjInfoString = prjInfoString.Replace("{", string.Empty).Replace("}", string.Empty);
    var pattern = "\\bPROJECTION\\s*INFO\\s*=\\s*";
    prjInfoString = Regex.Replace(prjInfoString, pattern, string.Empty);
    var array = prjInfoString.split(',');
    if (array.length < 3) {
        return null;
    }
    var num = 0;
    var list = [];
    var array2 = array;
    for (var i = 0; i < array2.length; i++) {
        var s = array2[i];
        var item = 0;
        try {
            item = parseFloat(s)
        } catch (e) {
            break;
        }
        //if (!float.TryParse(s, out item))
        //{
        //break;
        //}
        list.push(item);
        num++;
    }
    var name = "";
    var text = "Meters";
    if (num < array.length) {
        var arg_C0_0 = array[num];
        if (num + 1 < array.length) {
            name = array[num + 1];
        }
        if (num + 2 < array.length) {
            text = array[num + 2];
            pattern = "\\bUNITS\\s*=\\s*";
            text = Regex.Replace(text, pattern, "");
        }
    }
    var projectionID = parseInt(list[0]);
    var array3 = new Float32Array(list.length - 1);
    for (var j = 1; j < num; j++) {
        array3[j - 1] = list[j];
    }
    var hdrProjectionInfo = new HdrProjectionInfo();

    hdrProjectionInfo.ProjectionID = projectionID;
    hdrProjectionInfo.PrjArguments = array3;
    hdrProjectionInfo.Units = text;
    hdrProjectionInfo.Name = name;
    return hdrProjectionInfo;
}

HdrFileParser.GetMapInfo = function (prjName, cX, cY, cLon, cLat, eLon, eLat, rLon, erLon, rLat, erLat, project, units) {
    var hdrMapInfo = new HdrMapInfo();

    hdrMapInfo.Name = prjName;
    hdrMapInfo.BaseRowColNumber = new Point(parseInt(String.IsNullOrEmpty(cX) ? "-1" : ((cX.indexOf('.') == -1) ? cX : cX.substring(0, cX.indexOf('.')))), parseInt(String.IsNullOrEmpty(cY) ? "-1" : ((cY.indexOf('.') == -1) ? cY : cY.substring(0, cY.indexOf('.')))));
    hdrMapInfo.BaseMapCoordinateXY = new HdrGeoPointCoord();

    hdrMapInfo.BaseMapCoordinateXY.Latitude = parseFloat(String.IsNullOrEmpty(cLat) ? "-1" : cLat) * (String.IsNullOrEmpty(eLat) ? 1.0 : Math.pow(10.0, parseFloat(eLat)));
    hdrMapInfo.BaseMapCoordinateXY.Longitude = parseFloat(String.IsNullOrEmpty(cLon) ? "-1" : cLon) * (String.IsNullOrEmpty(eLon) ? 1.0 : Math.pow(10.0, parseFloat(eLon)));
    hdrMapInfo.XYResolution = new HdrGeoPointCoord();

    hdrMapInfo.XYResolution.Latitude = parseFloat(String.IsNullOrEmpty(rLat) ? "-1" : rLat) * (String.IsNullOrEmpty(erLat) ? 1.0 : Math.pow(10.0, parseFloat(erLat)));
    hdrMapInfo.XYResolution.Longitude = parseFloat(String.IsNullOrEmpty(rLon) ? "-1" : rLon) * (String.IsNullOrEmpty(erLon) ? 1.0 : Math.pow(10.0, parseFloat(erLon)));

    hdrMapInfo.CoordinateType = project;
    hdrMapInfo.Units = units;
    return hdrMapInfo;
}
HdrFileParser.GetBandNames = function (bandNames, str, context, index) {
    if (str.indexOf("}") != -1) {
        if (!String.IsNullOrEmpty(bandNames)) {
            return bandNames.split(',');
        }
        return null;
    }
    else {
        var stringBuilder = bandNames; //new StringBuilder(bandNames);
        for (var i = index + 1; i < context.length; i++) {
            if (context[i].indexOf("}") == -1) {
                stringBuilder += context[i];
            }
            else {
                stringBuilder += (context[i].substring(0, context[i].indexOf("}")));
            }
        }
        if (!String.IsNullOrEmpty(stringBuilder)) {
            return stringBuilder.split(',');
        }
        return null;
    }
}
HdrFileParser.GetGeoPoint = function (geoPoint) {
    var list = [];
    var hdrGeoPoint = new HdrGeoPoint();
    var array = geoPoint.split(',');
    for (var i = 0; i < array.length; i++) {
        if (i == 0) {
            hdrGeoPoint.PixelPoint.Y = parseInt(array[i]);
        }
        else {
            switch (i % 4) {
                case 0:
                    hdrGeoPoint.PixelPoint.Y = parseInt(array[i]);
                    break;
                case 1:
                    hdrGeoPoint.PixelPoint.X = parseInt(array[i]);
                    break;
                case 2:
                    hdrGeoPoint.GeoPoint.Latitude = parseFloat(array[i]);
                    break;
                case 3:
                    hdrGeoPoint.GeoPoint.Longitude = parseFloat(array[i]);
                    break;
            }
        }
        if (i != 0 && i % 4 == 0) {
            list.push(hdrGeoPoint);
        }
    }
    return list;
}
HdrFileParser.GetEnumByteOrder = function (byteOder) {
    var result = enumHdrByteOder.Host_intel;
    switch (parseInt(byteOder)) {
        case 0:
            result = enumHdrByteOder.Host_intel;
            break;
        case 1:
            result = enumHdrByteOder.Network_IEEE;
            break;
    }
    return result;
}
HdrFileParser.GetEnuminterleave = function (interleave) {
    var result = enumInterleave.BIP;
    var a;
    if ((a = interleave.toLocaleLowerCase().trim()) != null) {
        if (!(a == "bsq")) {
            if (!(a == "bil")) {
                if (a == "bip") {
                    result = enumInterleave.BIP;
                }
            }
            else {
                result = enumInterleave.BIL;
            }
        }
        else {
            result = enumInterleave.BSQ;
        }
    }
    return result;
}
module.exports = HdrFileParser;
