var d3Contour = require('d3-contour').contours;
d3Contour.contourDensity = require('d3-contour').contourDensity
if (typeof Cesium !== 'undefined') {
    d3Contour._contourTaskProcessors = [
        new Cesium.TaskProcessor('contour', 5),
        new Cesium.TaskProcessor('contour', 5),
        new Cesium.TaskProcessor('contour', 5)
    ]
}

var defaultValue = require('./defaultValue');
/**
*
*@class d3Contour
*@name d3Contour
*@memberof MeteoLib.Util
*@example
    var contours = d3Contour()
                  .size([width, height])
                  .thresholds(breaks)
                  (dataArray);
*/

/**
*
*@param {Array.<Number>}dataArray
*@param {Number}width
*@param {Number}height
*@param {Array.<Number>}breaks
*@return {Array.<MultiPolygon>}
*@method extract
*@name extract
*@memberof MeteoLib.Util.d3Contour 
*/
d3Contour.extract = function (dataArray, width, height, breaks) {
    var contours = d3Contour()
        .size([width, height])
        .thresholds(breaks)
        (dataArray);
    return contours;
}

/**
*
*@param {Array.<Number>}dataArray
*@param {Number}width
*@param {Number}height
*@param {Array.<Number>}breaks
*@return {Promise.<Array.<MultiPolygon>>}
*@method extractAsync
*@name extractAsync
*@memberof MeteoLib.Util.d3Contour
*/
d3Contour.extractAsync = function (dataArray, width, height, breaks) {

    var contourPromise;
    var task;
    for (var i = 0; i < d3Contour._contourTaskProcessors.length; i++) {
        task = d3Contour._contourTaskProcessors[i];
        contourPromise = task.scheduleTask({
            methodName: "extractContour",
            args: [dataArray, width, height, breaks]
        });
        if (contourPromise) {
            break;
        }
    }
    return contourPromise;
}
/**
*@param {Object}options
* @param {Number} options.width
* @param {Number} options.height
* @param {Cesium.Rectangle} options.rectangle 
* @param {Cesium.Rectangle} options.dataRectangle 
*
*@param {Cesium.Color}[options.backColor=Cesium.Color.TRANSPARENT] 
*
* @param {Boolean} [options.fill=false] 
* @param {MeteoLib.Render.GridDataColorMap} options.colorMap
*  
* @param {Boolean} [options.isoLine=true]
* @param {Cesium.Color} [options.lineColor=undefined]
* @param {Number} [options.lineWidth=1.5] 
*
* @param {Boolean} [options.isoValue=true] 
* @param {Cesium.Color} [options.isoValueColor=Cesium.Color.BLACK] 
* @param {Cesium.Color} [options.isoValueBackColor=Cesium.Color.TRANSPARENT]  
* @param {Cesium.Color} [options.isoValueStrokeColor=Cesium.Color.WHITE] 
* @param {Boolean} [options.isoValueStroke=false] 
* @param {Number} [options.isoValueStrokeWidth=1] 
* @param {Number} [options.isoValueFontSize=9] 
* @param {String} [options.isoValueFontFamily] 
* @param {Number} [options.isoValuePadding=0] 
* @param {Number} [options.isoValueDigit=2] 2
* @param {Number} [options.isoValueColorType='single'] 'single'|'fillColor' singleisoValueColorfillColorcolorMap
* @param {Number} [options.lineColorType='single'] 'single'|'fillColor' singlelineColorfillColorcolorMap
* @return {Object}
*
*@method defaultDrawOptions
*@name defaultDrawOptions
*@memberof MeteoLib.Util.d3Contour
*/
d3Contour.defaultDrawOptions = function (options) {
    options = options ? options : {};

    options.backColor = defaultValue(options.backColor, Cesium.Color.TRANSPARENT);

    options.isoLine = defaultValue(options.isoLine, true);
    options.lineColor = defaultValue(options.lineColor, Cesium.Color.BLACK);
    options.lineColorType = defaultValue(options.lineColorType, 'single');//single|fillColor

    options.lineWidth = defaultValue(options.lineWidth, 1.5);
    options.lineDash = options.lineDash;

    options.isoValue = defaultValue(options.isoValue, true);
    options.isoValueColor = defaultValue(options.isoValueColor, Cesium.Color.BLACK);
    options.isoValueStroke = options.isoValueStroke;
    options.isoValueBackColor = defaultValue(options.isoValueBackColor, Cesium.Color.TRANSPARENT);
    options.isoValueStrokeWidth = defaultValue(options.isoValueStrokeWidth, 1);
    options.isoValueStrokeColor = defaultValue(options.isoValueStrokeColor, Cesium.Color.WHITE);
    options.isoValueFontSize = defaultValue(options.isoValueFontSize, 9);
    options.isoValueColorType = defaultValue(options.isoValueColorType, 'single');//'single'|'fillColor'
    options.isoValueDigit = defaultValue(options.isoValueDigit, 2);

    options.fill = defaultValue(options.fill, false);

    if (!options.rectangle && options.dataRectangle) {
        options.rectangle = options.dataRectangle.clone();
    }
    return options;
}

/**
*
*@param {Array.<MultiPolygon>}contours
*@param {Object}options
* @param {Number} options.width
* @param {Number} options.height
* @param {Cesium.Rectangle} options.rectangle 
* @param {Cesium.Rectangle} options.dataRectangle 
*
*@param {Cesium.Color}[options.backColor=Cesium.Color.TRANSPARENT] 
*
* @param {Boolean} [options.fill=false] 
* @param {MeteoLib.Render.GridDataColorMap} options.colorMap
*  
* @param {Boolean} [options.isoLine=true]
* @param {Cesium.Color} [options.lineColor=undefined]
* @param {Number} [options.lineWidth=1.5] 
*
* @param {Boolean} [options.isoValue=false] 
* @param {Cesium.Color} [options.isoValueColor=Cesium.Color.BLACK] 
* @param {Cesium.Color} [options.isoValueBackColor=Cesium.Color.TRANSPARENT]  
* @param {Cesium.Color} [options.isoValueStrokeColor=Cesium.Color.WHITE] 
* @param {Boolean} [options.isoValueStroke=false] 
* @param {Number} [options.isoValueStrokeWidth=1] 
* @param {Number} [options.isoValueFontSize=9] 
* @param {String} [options.isoValueFontFamily] 
* @param {Number} [options.isoValuePadding=0] 
* @param {Number} [options.isoValueDigit=2] 2  
* @param {Number} [options.isoValueColorType='single'] 'single'|'fillColor' singleisoValueColorfillColorcolorMap
* @param {Number} [options.lineColorType='single'] 'single'|'fillColor' singlelineColorfillColorcolorMap
*
*@method drawContours
*@name drawContours
*@memberof MeteoLib.Util.d3Contour
*@return {HTMLCanvasElement}
*/
d3Contour.drawContours = function (contours, options) {
    options = d3Contour.defaultDrawOptions(options)

    var colorMap = options.colorMap;

    var cv = document.createElement('canvas');

    cv.width = options.width;
    cv.height = options.height;
    var context = cv.getContext("2d");

    if (options.backColor) {
        if (options.backColor instanceof Cesium.Color) {
            context.fillStyle = options.backColor.toCssColorString()
        }
        else context.fillStyle = options.backColor
        context.fillRect(0, 0, cv.width, cv.height)
        context.fillStyle = 'rgba(0,0,0,0)';
    }

    var pointIndex = 0;

    var textDrawOpts = {
        font: options.isoValueFontSize + 'px ' + options.isoValueFontFamily,
        textBaseline: 'middle',
        fill: true,
        strokeWidth: options.isoValueStrokeWidth,
        stroke: options.isoValueStroke,
        fillColor: options.isoValueColor,
        strokeColor: options.isoValueStrokeColor,
        backgroundColor: options.isoValueBackColor,
        padding: options.isoValuePadding
    };

    var x = null, y = null;
    function drawPolygon(coordinates, value) {
        coordinates.forEach(function (line) {
            var pointIndex = 0;

            context.beginPath();
            line.forEach(function (pt) {
                x = pt[0], y = pt[1];
                if (x < 0.5) x = x - 1.5;
                else if (x >= cv.width - 1) x = x + 1;
                if (y < 0.5) y = y - 1;
                else if (y >= cv.height - 1.5) y = y + 1;

                if (pointIndex == 0) {
                    context.moveTo(x, y)
                } else {
                    context.lineTo(x, y)
                }
                pointIndex++;
            })
            if (options.fill) {
                context.fill();
            }
            if (options.isoLine) {
                context.stroke();
            }
            if (options.isoValue && line.length) {
                var idx = 0;
                if (line.length > 1) {
                    idx = parseInt(Math.random() * (line.length - 1));
                    idx = Math.max(0, idx);
                }
                var pt = line[idx];
                x = pt[0], y = pt[1];

                var textImg = Cesium.writeTextToCanvas(value.toFixed(options.isoValueDigit), textDrawOpts);
                x = x - textImg.width / 2.0,
                    y = y - textImg.height / 2.0;
                if (x >= 0 && x + textImg.width < context.canvas.width
                    && y >= 0 && y + textImg.height < +context.canvas.height) {
                    context.drawImage(textImg, x, y);
                }
            }

        })
    }
    for (var i = 0; i < contours.length; i++) {
        var contour = contours[i];
        var value = parseFloat(contour.value);
        var color = options.lineColor;
        var fillColor = 'rgba(0,0,0,0)';
        var textColor = options.isoValueColor;

        if (colorMap
            && (
                options.fill
                || options.lineColorType !== 'single'
                || options.isoValueColorType !== 'single'
            )
        ) {
            fillColor = GridDataColorMap.getColor(value, colorMap);
            if (fillColor) {
                fillColor = Cesium.Color.fromBytes(fillColor[0], fillColor[1], fillColor[2], fillColor[3])
                if (options.lineColorType !== 'single') {
                    color = fillColor;
                }
                if (options.isoValueColorType !== 'single') {
                    textColor = fillColor;
                }
                if (!options.fill) {
                    fillColor = 'rgba(0,0,0,0)';
                }
            }
            else {
                fillColor = 'rgba(0,0,0,0)';
            }
        }


        context.lineWidth = options.lineWidth;
        if (options.lineDash) {
            context.setLineDash(options.lineDash);
        }

        if (color instanceof Cesium.Color) {
            color = color.toCssColorString();
        }
        if (fillColor instanceof Cesium.Color) {
            fillColor = fillColor.toCssColorString();
        }
        if (!(textColor instanceof Cesium.Color)) {
            textColor = Cesium.Color.fromCssColorString(textColor)
        }
        textDrawOpts.fillColor = textColor

        context.strokeStyle = color;
        context.fillStyle = fillColor;

        contour.coordinates.forEach(function (plygon) {
            if (plygon.length && plygon[0].length && plygon[0][0].length == 2) {
                drawPolygon(plygon, value)
            } else {
                plygon.forEach(function (poly) {
                    drawPolygon(poly, value)
                })
            }
        })
    }
    return cv;
}
/**
*
*@param {Array.<Number>}dataArray
*@param {Number}width
*@param {Number}height
*@param {Array.<Number>}breaks
*@param {Object}drawOptions
* @param {Number} drawOptions.width
* @param {Number} drawOptions.height
* @param {Cesium.Rectangle} drawOptions.rectangle 
* @param {Cesium.Rectangle} drawOptions.dataRectangle 
*
* @param {Boolean} [drawOptions.fill=false] 
* @param {MeteoLib.Render.GridDataColorMap} drawOptions.colorMap
*  
* @param {Boolean} [drawOptions.isoLine=true]
* @param {Cesium.Color} [drawOptions.lineColor=undefined]
* @param {Number} [drawOptions.lineWidth=1.5] 
*
* @param {Boolean} [drawOptions.isoValue=false] 
* @param {Cesium.Color} [drawOptions.isoValueColor=Cesium.Color.BLACK] 
* @param {Cesium.Color} [drawOptions.isoValueBackColor=Cesium.Color.TRANSPARENT]  
* @param {Cesium.Color} [drawOptions.isoValueStrokeColor=Cesium.Color.WHITE] 
* @param {Boolean} [drawOptions.isoValueStroke=false] 
* @param {Number} [drawOptions.isoValueStrokeWidth=1] 
* @param {Number} [drawOptions.isoValueFontSize=9] 
* @param {String} [drawOptions.isoValueFontFamily] 
* @param {Number} [drawOptions.isoValuePadding=0] 
* @param {Number} [drawOptions.lineColorType='single'] single|fillColor singlelineColorfillColorcolorMap
*@return {Promise.<Canvas>}
*@method extractAndDrawAsync
*@name extractAndDrawAsync
*@memberof MeteoLib.Util.d3Contour
*/
d3Contour.extractAndDrawAsync = function (dataArray, width, height, breaks, drawOptions) {
    if (!breaks) {
        breaks = 5;
    }
    if (typeof breaks == 'number') {
        var min = Number.MAX_VALUE, max = -Number.MAX_VALUE;
        for (var i = 0; i < dataArray.length; i++) {
            min = Math.min(min, dataArray[i]);
            max = Math.max(max, dataArray[i]);
        }
        var count = breaks;
        breaks = [];
        var step = (max - min) / (count - 1);
        for (var i = 0; i < count; i++) {
            breaks.push(min + i * step);
        }
    }

    drawOptions = drawOptions ? drawOptions : {};
    Object.assign(drawOptions, {
        width: width,
        height: height
    })

    var df = Cesium.when.defer();
    function onContoursLoad(contours) {
        try {
            var canvas = d3Contour.drawContours(contours, drawOptions);
            df.resolve(canvas);
        } catch (e) {
            df.reject(e);
        }
    }
    var promise = d3Contour.extractAsync(dataArray, width, height, breaks);
    if (promise) {
        promise.then(function (contours) {
            onContoursLoad(contours)
        })
    } else {
        var contours = d3Contour.extract(dataArray, width, height, breaks);
        Cesium.requestAnimationFrame(function () {
            onContoursLoad(contours);
        })
    }
    return df;
}
