var IRasterDataDriver = require('./IRasterDataDriver');
var BinaryReader = require('../../Util/BinaryReader');

/**
*
*@property {String}Name 
*@property {String}FullName
*@constructor  
*@memberof MeteoLib.Data.RasterData
*@extends MeteoLib.Data.RasterData.IRasterDataDriver
*/
function RasterDataDriver(name, fullName) {
    this._name = name;
    this._fullName = fullName;
}
RasterDataDriver.RegisteredDrivers = [];
RasterDataDriver.prototype = new IRasterDataDriver();
/**
 * 
 * @param {String} name
 * @return {MeteoLib.Data.RasterData.IRasterDataDriver}
 */
RasterDataDriver.GetByName = function (name) {
    var registeredDrivers = this.RegisteredDrivers;
    if (registeredDrivers == null || registeredDrivers.length == 0) {
        return null;
    }
    var array = registeredDrivers;
    for (var i = 0; i < array.length; i++) {
        var rasterDataDriver = array[i];
        if (rasterDataDriver._name == name) {
            return rasterDataDriver;
        }
    }
    return null;
}
/**
 *
 *@param {String}fileName
 *@param {ArrayAuffer}arrayAuffer
 *@param {Uint8Array|Array.<Number>}header4096
 *@param {MeteoLib.Data.RasterData.enumDataProviderAccess}access
 *@param {Array.<Object>}args
 *@return {MeteoLib.Data.RasterData.RasterDataProvider}
 */
RasterDataDriver.prototype.Open = function (fileName, arrayAuffer, header4096, access, args) {
    if (!this.IsCompatible(fileName, arrayAuffer, header4096, args)) {
        return null;
    }
    return this.BuildDataProvider(fileName, arrayAuffer, header4096, access, args);
}
/**
 *
 *@param {String}fileName
 *@param {ArrayAuffer}arrayAuffer
 *@param {Uint8Array|Array.<Number>}header4096
 *@param {MeteoLib.Data.RasterData.enumDataProviderAccess}access
 *@param {Array.<Object>}args
 *@return {MeteoLib.Data.RasterData.RasterDataProvider}
 */
RasterDataDriver.prototype.BuildDataProvider = function (fileName, arrayAuffer, header4096, access, args) {
    throw new Error("RasterDataDriver.prototype.BuildDataProvider");
}
/**
 *
 *@param {String}fileName
 *@param {ArrayAuffer}arrayAuffer
 *@param {Uint8Array|Array.<Number>}header4096
 *@param {MeteoLib.Data.RasterData.enumDataProviderAccess}access
 *@param {Array.<Object>}args
 *@return {Boolean}
 */
RasterDataDriver.prototype.IsCompatible = function (fileName, arrayAuffer, header4096, args) {
    throw new Error("RasterDataDriver.prototype.IsCompatible");
}
/**
*
*/
RasterDataDriver.prototype.Dispose = function () {
    throw new Error("RasterDataDriver.prototype.Dispose");
}
/**
 *
 *@param {String}fileName
*@param {ArrayAuffer}arrayAuffer
*@param {Uint8Array|Array.<Number>}header4096
*@param {MeteoLib.Data.RasterData.enumDataProviderAccess}access
*@param {Array.<Object>}args
*@return {MeteoLib.Data.RasterData.RasterDataProvider}
*/
RasterDataDriver.Open = function (fileName, arrayAuffer, access, args) {
    var registeredDrivers = this.RegisteredDrivers;
    if (registeredDrivers == null || registeredDrivers.length == 0) {
        return null;
    }
    var header4096Bytes = RasterDataDriver.GetHeader4096Bytes(arrayAuffer);
    var array = registeredDrivers;
    for (var i = 0; i < array.length; i++) {
        var rasterDataDriver = array[i];
        var rasterDataProvider = rasterDataDriver.Open(fileName, arrayAuffer, header4096Bytes, access, args);
        if (rasterDataProvider != null) {
            return rasterDataProvider;
        }
    }
    return null;
}

RasterDataDriver.GetHeader4096Bytes = function (arrayAuffer) {
    var result;
    var fileStream = new BinaryReader(arrayAuffer)
    var array = new Uint8Array(4096);
    var readLength = arrayAuffer.length < 4096 ? arrayAuffer.length : 4096
    fileStream.read(array, 0, readLength);
    fileStream.dispose();
    result = array;
    return result;
}
module.exports = RasterDataDriver;
