var when = require('when'),
    File = require('../File'),
    loadImage = require('../ajax/loadImage'),
    getString = require('../getString'),
    parseXML = require('../parseXML');
// ) {
/**
 * inputHttpFileSystem
 * @param {Object} options 
 * @constructor
 * @memberof MeteoLib.Util
 * @constructor
 */
function FileSystem(options) {

}

/**
*
* @param {File|Blob}fileInfo 
* @param {Number} [offset=0] 
* @param {Number} [length]  
* @param {String} [responseType="arrayBuffer"] :arrayBuffer,blob,json,text,xml,image
* @param {String} [encoding]
* @returns {Promise.<ArrayBuffer>}  
* @example
* //1arrayBuffer
* fs.readFile(fileInfo).then(function(buf){
*      //TODO
* }).otherwise(function(reason){
*      //throw 
* })
* //2arrayBuffer
* fs.readFile(fileInfo,0,1024).then(function(buf){
*      //TODO
* }).otherwise(function(reason){
*      //throw 
* })
* //3json
* fs.readFile(fileInfo,'json').then(function(json){
*      //TODO
* }).otherwise(function(reason){
*      //throw 
* })
* //4
* fs.readFile(fileInfo,'text','gb2312').then(function(text){
*      //TODO
* }).otherwise(function(reason){
*      //throw 
* })
* //5
* fs.readFile(fileInfo,'image').then(function(image){
*      //TODO
* }).otherwise(function(reason){
*      //throw 
* })
*/
FileSystem.prototype.readFile = function (fileInfo, offset, length, responseType, encoding) {
    if (typeof offset == 'string' && !responseType) {
        responseType = offset;
        offset = 0;
    }
    if (typeof length == 'string' && !encoding) {
        encoding = length;
        length = null;
    }
    offset = offset ? offset : 0;
    length = length ? length : (fileInfo.size ? fileInfo.size : null);

    responseType = responseType ? responseType : "arrayBuffer";

    if (offset > 0 || length < fileInfo.size) {
        fileInfo = fileInfo.slice(offset, length)
    }
    switch (responseType) {
        case "arrayBuffer":
            return File.readAsArrayBuffer(fileInfo);
        case "blob":
            return fileInfo;
        case "xml":
            return File.readAsText(fileInfo).then(function (text) {
                return parseXML(text);
            });
        case "json":
            return File.readAsText(fileInfo).then(function (text) {
                try {
                    return JSON.parse(text);
                } catch (error) {
                    return eval("(" + text + ")");
                }
            });
        case "image":
            return File.readAsDataURL(fileInfo).then(function (dataUrl) {
                return loadImage(dataUrl);
            });
        case "text":
            if (encoding) {
                return File.readAsArrayBuffer(fileInfo).then(function (buf) {
                    return getString(buf, encoding)
                });
            } else {
                return File.readAsText(fileInfo)
            }
        default:
            return File.readAsArrayBuffer(fileInfo);
    }

}


/**
 *
 * @param {File|Blob}fileInfo 
 * @param {Number} xOffset 
 * @param {Number} yOffset 
 * @param {Number} xSize 
 * @param {Number} ySize 
 * @param {Number} [typeSize=0] 
 * @param {Number} xBufferSize 
 * @param {Number} yBufferSize 
 * @param {Number} stride 
 * @param {Array.<Number>} [bandOffsets] 
 * @param {Boolean} [flipY=false] truefalse
 * @param {Number} [numberOfY=0] 
 * @param {Number} [headerSize=0] 0
 * @param {Number} [useWorker=false] truewebWorker,MeteoLibSource/Workers/FileSystem.jsCesium/Workersfalse
 * @returns {Promise.<ArrayBuffer>}  
 */
FileSystem.prototype.readRasterData = function (
    fileInfo, xOffset, yOffset, xSize, ySize,
    stride, typeSize, xBufferSize, yBufferSize, bandOffsets, flipY, numberOfY,
    headerSize, useWorker) {

    var promise

    if (typeof Cesium != 'undefined' && useWorker) {
        if (!FileSystem._fsTaskProcessors) {
            FileSystem._fsTaskProcessors = [
                new Cesium.TaskProcessor('FileSystem', 1),
                new Cesium.TaskProcessor('FileSystem', 1)
            ];
        }

        if (useWorker) {
            for (var index = 0; index < FileSystem._fsTaskProcessors.length; index++) {
                var fsTaskProcessor = FileSystem._fsTaskProcessors[index];
                promise = fsTaskProcessor.scheduleTask({
                    methodName: "readRasterData",
                    args: [fileInfo, xOffset, yOffset, xSize, ySize,
                        stride, typeSize, xBufferSize, yBufferSize, bandOffsets, flipY, numberOfY,
                        headerSize]
                });
                if (promise) {
                    return promise
                }
            }
        }

    }

    headerSize = headerSize ? headerSize : 0;

    xOffset = parseInt(xOffset),
        yOffset = parseInt(yOffset),
        xSize = parseInt(xSize),
        ySize = parseInt(ySize),
        xBufferSize = parseInt(xBufferSize),
        yBufferSize = parseInt(yBufferSize),
        numberOfY = parseInt(numberOfY),
        flipY = flipY && numberOfY;

    typeSize = parseInt(typeSize);
    stride = parseInt(stride);
    if (headerSize) {
        headerSize = parseInt(headerSize);
    }

    if (!bandOffsets || !bandOffsets.length) {
        bandOffsets = [headerSize]
    }

    var bandCount = bandOffsets.length;
    var bufStride = xBufferSize;
    var bufByteLength = xBufferSize * yBufferSize * typeSize;

    var promise = when.defer();
    try {
        var blobArr = [];
        var xScale = xSize / xBufferSize;
        var yScale = ySize / yBufferSize;

        if (xScale == 1 && yScale == 1) {
            for (var bandNo = 0; bandNo < bandCount; bandNo++) {
                var y;

                for (var i = 0; i < ySize; i++) {
                    y = i;
                    if (flipY) y = numberOfY - y - 1;
                    //var offset = y * bufStride * typeSize + bandNo * bufByteLength;
                    var position = (y + yOffset) * stride + xOffset;
                    position *= typeSize;
                    position += bandOffsets[bandNo];

                    blobArr.push(fileInfo.slice(position, position + bufStride * typeSize));
                }
            }
            blobArr = new Blob(blobArr);
            return File.readAsArrayBuffer(blobArr);

        } else {//
            var buf = new Uint8Array(bufByteLength * bandCount);
            var tempBufferLength = xSize * typeSize;//new Buffer(xSize * typeSize);

            function readNextBand(bandNo) {

                if (bandNo >= bandCount) {
                    promise.resolve(buf.buffer);
                    return;
                }

                var idx = 0;
                var y;

                var offset = bandNo * bufByteLength;

                function readNextLine(i) {
                    if (i >= yBufferSize) {
                        readNextBand(bandNo + 1);
                        return;
                    }
                    y = parseInt(i * yScale);
                    var position = (y + yOffset) * stride + xOffset;
                    if (flipY)
                        position = (numberOfY - 1 - (y + yOffset)) * stride + xOffset;
                    position *= typeSize;
                    position += bandOffsets[bandNo];

                    var partBlob = fileInfo.slice(position, position + tempBufferLength);
                    File.readAllBytes(partBlob).then(function (tempBuffer) {

                        for (var j = 0; j < xBufferSize; j++) {
                            var x = parseInt(j * xScale) * typeSize;
                            for (var index = 0; index < typeSize; index++) {
                                buf[offset + idx++] = tempBuffer[x + index];
                            }
                        }
                        readNextLine(i + 1);
                    }).otherwise(function (reason) {
                        buf = null;
                        promise.reject(reason);
                    })
                }

                readNextLine(0);
            }

            readNextBand(0);

        }

    } catch (e) {
        promise.reject(e);
    }
    return promise.promise;
}

module.exports = FileSystem;
