/**
* DataViewTypeArray
* @param {ArrayBuffer|Uint8Array}buffer
* @property {Number} position
* @property {DataView} dataView
* @property {Number} length
* @property {Number} byteLength
* @property {Boolean} isLittleEndian  
*
*js
*
* @memberof MeteoLib.Util
* @constructor
*/
function BinaryReader(buffer) {
    /// <summary>DataViewTypeArray</summary>
    this.buffer = buffer;
    this.position = 0;
    if (buffer instanceof Uint8Array) {
        this.buffer = buffer.buffer;
        this.position = buffer.byteOffset;

    }
    this.dataView = new DataView(this.buffer);

    this.length = this.dataView.byteLength;
    this.byteLength = this.length;
    /**
    *
    *js
    *
    */
    this.isLittleEndian = false;
}

/**
* 
* @property {enum} Begin 
* @property {enum} Current 
* @property {enum} End 
*/
BinaryReader.SeekOrigin =
    {
        /**
        * 
        */
        Begin: 0,
        /**
        * 
        */
        Current: 1,
        /**
        * 
        */
        End: 2
    };
/**
*
*/
BinaryReader.prototype.skip = function (skipCount) {
    var length = skipCount;
    if (this.position >= this.dataView.byteLength
        || this.position + length > this.dataView.byteLength) {
        throw new Error("count");
    }
    this.position += length;
}
/**
*
* @param {Number}offset  origin 
* @param {BinaryReader.SeekOrigin}origin  BinaryReader.SeekOrigin  origin BinaryReader.SeekOrigin.Begin
* @returns {Number}
*/
BinaryReader.prototype.seek = function (offset, origin) {
    /// <summary></summary>
    /// <param name="offset" type="Number"> origin </param>
    /// <param name="origin" type="BinaryReader.SeekOrigin"> BinaryReader.SeekOrigin  origin BinaryReader.SeekOrigin.Begin</param>
    /// <returns type="Number" />
    if (typeof origin === 'undefined' || origin == null) {
        origin = BinaryReader.SeekOrigin.Begin;
    }
    switch (origin) {
        case BinaryReader.SeekOrigin.Begin:
            this.position = offset;
            break;
        case BinaryReader.SeekOrigin.Current:
            this.position += offset;
            break;
        case BinaryReader.SeekOrigin.End:
            this.position = this.dataView.byteLength - offset;
            break;
        default:
            this.position = offset;
            break;
    }
    return this.position;
}

/**
 * count
 * @param {Int8Array|Array.<Number>} array  offset  (offset + count - 1) 
 * @param {Number} offset array 
 * @param {Number} count  
 */
BinaryReader.prototype.read = function (array, offset, count, utf8) {
    var length = count;
    if (this.position >= this.dataView.byteLength
        || this.position + length > this.dataView.byteLength) {
        return -1;//thrownew Error("count");
    }
    var vals;
    if (this.isLittleEndian) {
        var buffer = this.dataView.buffer.slice(this.position, this.position + length);
        vals = utf8 ? new Uint8Array(buffer) : new Int8Array(buffer);
        this.position += length;

        for (var i = offset; i < offset + count; i++) {
            array[i] = vals[i];
        }
    } else {
        for (var i = 0; i < count; i++) {
            array[i + offset] = this.readByte();
        }
    }
    return length;
}
/**
* byte1 -1
* @returns {Number|Int8|byte}
*/
BinaryReader.prototype.readByte = function (toUint8Array) {
    /// <summary> byte1 -1</summary>
    /// <returns type="Number|Int8|byte" />
    if (this.position >= this.dataView.byteLength) {
        return -1;
    }
    var val;//!this.isLittleEndian);
    if (!toUint8Array) {
        val = this.dataView.getInt8(this.position, true)
    } else {
        val = this.dataView.getUint8(this.position, true)
    }
    this.position += 1;
    return val;
}


/**
*(count)byte,count -1
* @param {Number|Int32}count
* @param {Boolean}toUint8Array Uint8bytejsUint80~255Int8-128~128
* @returns {Int8Array|Uint8Array}
*/
BinaryReader.prototype.readBytes = function (count, toUint8Array) {
    var length = count;
    if (this.position >= this.dataView.byteLength
        || this.position + length > this.dataView.byteLength) {
        throw new Error("count");
    }
    if (this.isLittleEndian) {
        var buffer = this.dataView.buffer.slice(this.position, this.position + length);
        if (toUint8Array) {
            var vals = new Uint8Array(buffer);
            this.position += length;
            return vals;
        }
        var vals = new Int8Array(buffer);
        this.position += length;
        return vals;
    }
    // var buffer = this.dataView.buffer.slice(this.position, this.position + length);
    if (toUint8Array) {
        var vals = new Uint8Array(count);
        for (var i = 0; i < vals.length; i++) {
            vals[i] = this.readByte(toUint8Array);
        }
        return vals;
    }
    var vals = new Int8Array(count);
    for (var i = 0; i < vals.length; i++) {
        vals[i] = this.readByte();
    }
    return vals;

    //var vals = new Int8Array(buffer);
    // buffer = null;
    //this.position += length;
    //return vals;
}

/**
* byte1 -1
* @returns {Number|Uint8|byte}
*/
BinaryReader.prototype.readUbyte = function () {
    /// <summary> byte1 -1</summary>
    /// <returns type="Number|Uint8|byte" />

    return this.readByte(true);
}

/**
    *short2 -1
    * @returns {Number}
    */
BinaryReader.prototype.readShort = function () {
    /// <summary> short2 -1</summary>
    /// <returns type="Int16|short" />

    if (this.position >= this.dataView.byteLength) {
        return -1;
    }
    var val = this.dataView.getInt16(this.position, this.isLittleEndian);
    this.position += 2;
    return val;
}
/**
* (count)short,2*count -1
* @param {Number|Int32}count
* @returns {Int16Array}
*/
BinaryReader.prototype.readShorts = function (count) {
    var length = count * Int16Array.BYTES_PER_ELEMENT;
    if (this.position >= this.dataView.byteLength
        || this.position + length > this.dataView.byteLength) {
        throw new Error("count");
    }
    if (this.isLittleEndian) {
        var buffer = this.dataView.buffer.slice(this.position, this.position + length);
        var vals = new Int16Array(buffer);
        this.position += length;
        buffer = null;
        return vals;
    }
    var vals = new Int16Array(count);
    for (var i = 0; i < vals.length; i++) {
        vals[i] = this.readShort();
    }
    return vals;
}

