/**
* Int648 -1
* @returns {Number|Int64}
*/
BinaryReader.prototype.readInt64 = function () {
    // if (!BinaryReader.BigInteger) {
    //     throw ReferenceError("BinaryReader.BigInteger");
    // }
    if (this.position + 8 > this.dataView.byteLength) {
        return -1;
    }
    var bytes = this.readBytes(8, true);
    var arr = [];
    if (this.isLittleEndian) {

        for (var i = 0; i < bytes.length; i++) {
            arr[i] = bytes[bytes.length - 1 - i];
        }
        bytes = new Uint8Array(arr);
    }

    var long = bytes[0] & 0xFF;
    long += (bytes[1] & 0xFF) << 8;
    long += (bytes[2] & 0xFF) << 16;
    long += (bytes[3] & 0xFF) << 24;
    long += (bytes[4] & 0xFF) * Math.pow(2, 32);
    long += (bytes[5] & 0xFF) * Math.pow(2, 40);
    long += (bytes[6] & 0xFF) * Math.pow(2, 48);
    long += bytes[7] * Math.pow(2, 56);
    return long;
}

/**
* Uint648 -1
* @returns {Number|Uint64}
*/
BinaryReader.prototype.readUint64 = function () {
    // if (!BinaryReader.BigInteger) {
    //     throw ReferenceError("BinaryReader.BigInteger");
    // }
    if (this.position + 8 > this.dataView.byteLength) {
        return -1;
    }
    var bytes = this.readBytes(8, true);
    var arr = [];
    if (!this.isLittleEndian) {

        for (var i = 0; i < bytes.length; i++) {
            arr[i] = bytes[bytes.length - 1 - i];
        }
        bytes = new Uint8Array(arr);
    }

    var long = bytes[0] & 0xFF;
    long += (bytes[1] & 0xFF) << 8;
    long += (bytes[2] & 0xFF) << 16;
    long += (bytes[3] & 0xFF) << 24;
    long += (bytes[4] & 0xFF) * Math.pow(2, 32);
    long += (bytes[5] & 0xFF) * Math.pow(2, 40);
    long += (bytes[6] & 0xFF) * Math.pow(2, 48);
    long += bytes[7] * Math.pow(2, 56);
    return long;
}


