/**
* char1 -1
*/
BinaryReader.prototype.readChar = function () {
    if (this.position >= this.dataView.byteLength) {
        return -1;
    }
    var val = this.dataView.getUint8(this.position, true)//!this.isLittleEndian);
    //val = val >= 0 ? val : 256 + val;
    val = String.fromCharCode(val);
    this.position += 1;
    return val;

}
/**
* char1 -1
*/
BinaryReader.prototype.readChars = function (count) {
    var length = count;
    if (this.position >= this.dataView.byteLength
        || this.position + length > this.dataView.byteLength) {
        throw new Error("count");
    }

    var vals = "";
    for (var i = 0; i < count; i++) {
        vals += this.readChar();
    }
    return vals;
}

/**
* 4int
* @param {Number}count 
* @return {Number}
*/
BinaryReader.prototype.readVint = function (count) {
    var b = this.readBytes(count, true);
    var v = 0
    for (var x = 0; x < count; x++) {
        v = v * 256 + b[x];
    }
    return v
}


/**
 * @param {Number}length
 * @param {String}[encoding='utf8']
 * @param {Object}[iconvOptions]
 * @return {String}
 */
BinaryReader.prototype.readString = function (length, encoding, iconvOptions) {
    var arr = this.readBytes(length);
    var text = getString(arr.buffer, encoding, iconvOptions);
    var result = "";
    for (var index = 0; index < text.length; index++) {
        if (text.charCodeAt(index) != 0) {
            result += text[index]
        }
    }
    return result;
}

/**
*
*/
BinaryReader.prototype.dispose = function () {
    /// <summary></summary>
    this.buffer = null;
    this.position = null;
    this.dataView = null;
}
BinaryReader.prototype.readInt32 = BinaryReader.prototype.readInt;
BinaryReader.prototype.readUInt32 = BinaryReader.prototype.readUint;
BinaryReader.prototype.readInt16 = BinaryReader.prototype.readShort;
BinaryReader.prototype.readUInt16 = BinaryReader.prototype.readUShort;

BinaryReader.prototype.close = BinaryReader.prototype.dispose;
