    function AwxHeaderBase() {
        this._dataType = enumDataType.Float;
        this._interleave = enumInterleave.BSQ;
        this._channelCount = 1;
        this._width;
        this._height;
        this._isExtended = false;
        this._isDay = false;
        this._attributes = {};
        this._coordEnvelope = null;
        this._resolutionX = 0;
        this._resolutionY = 0;
        this._bandNames = null;
        this._fileName = null;
        this._bandName = null;
    }
    AwxHeaderBase.prototype = new IAwxHeader();
    defineProperties(AwxHeaderBase.prototype, {
        FileName:
       {
           get: function () {
               return this._fileName;
           }
       },
        BandNames:
        {
            get: function () {
                return this._bandNames;
            }
        },
        BandName:
         {
             get: function () {
                 return this._bandName;
             }
         },
        Attributes:
        {
            get: function () { return this._attributes; }
        },

        HeaderSize: {
            get: function () {
                if (this.Attributes != null) {
                    var docheight = parseInt(this.Attributes[""]);
                    var reclength = parseInt(this.Attributes[""]);
                    var begin = docheight * reclength;
                    return begin;
                }
                return 4800;
            }
        },
        CoordEnvelope:
    {
        get: function () {
            this.computeEnvelope();
            return this._coordEnvelope;
        }
    },
        ResolutionX:
        {
            get: function () {
                this.computeEnvelope();
                return this._resolutionX;
            }
        },
        ResolutionY:
    {
        get: function () {
            this.computeEnvelope();
            return this._resolutionY;
        }
    },
        DataType:
            {
                get: function () { return this._dataType; }
            },
        Interleave:
        {
            get: function () { return this._interleave; }
        },
        ChannelCount: {
            get: function () { return this._channelCount; },
            set: function (val) { this._channelCount = val; }
        },
        Width: {
            get: function () { return this._width; }
        },
        Height: {
            get: function () { return this._height; }
        },

        IsExtended: {
            get: function () { return this._isExtended; }
        },
        IsDay: {
            get: function () { return this._isDay; }
        }
    })
    AwxHeaderBase.prototype._init = function (headBuffer, awxFileName) {
        this._fileName = awxFileName;
        this._attributes = AwxHeaderBase.readOneHead(headBuffer, this._attributes);
        this._channelCount = 1;
    }
    AwxHeaderBase.prototype.mapBand = function (bandName, outOandNo) {
        outOandNo = 1;
        return outOandNo;
    }
    /**
     *
     *@return {AwxHeaderBase}
     */
    AwxHeaderBase.prototype.Clone = function () {
        var header = {};
        for (var i in this) {
            if (this.hasOwnProperty(i)) {
                header[i] = this[i];
            }
        }
        return header;
    }
