    AwxHeaderBase.prototype.GetCoordEnvelope = function (minX, minY, maxX, maxY) {
        if (this.Attributes != null && this._coordEnvelope == null) {
            if (this.Attributes.hasOwnProperty("")
            && this.Attributes.hasOwnProperty("")
            && this.Attributes.hasOwnProperty("")
            && this.Attributes.hasOwnProperty("")) {
                minY = Convert.ToDouble(this.Attributes[""]);
                maxY = Convert.ToDouble(this.Attributes[""]);
                minX = Convert.ToDouble(this.Attributes[""]);
                maxX = Convert.ToDouble(this.Attributes[""]);
                this._coordEnvelope = new CoordEnvelope(minX, maxX, minY, maxY);
                this._resolutionX = parseFloat(this._coordEnvelope.Width / parseFloat(this._width));
                this._resolutionY = parseFloat(this._coordEnvelope.Height / parseFloat(this._height));
                return {
                    minY: minX,
                    maxY: maxY,
                    minX: minX,
                    maxX: maxX
                }
            }
        }
        minY = 0;
        maxY = 0;
        minX = 0;
        maxX = 0;
        return {
            minY: minX,
            maxY: maxY,
            minX: minX,
            maxX: maxX
        }
    }

    AwxHeaderBase.prototype.GetSatellite = function (sensor) {
        throw new Error("AwxHeaderBase.prototype.  GetSatellite");
    }

    AwxHeaderBase.prototype.GetOrbitDateTime = function () {
        if (this._attributes == null) {
            return new Date();
        }
        var dateStr = this._attributes[""].toString() + "-" +
                                    this._attributes[""].toString() + "-" +
                                    this._attributes[""].toString() + " " +
                                    this._attributes[""].toString() + " " +
                                    this._attributes[""].toString();
        var dt = Date.parse(dateStr);

        return dt;
    }


    /**
    *
    *@param {Uint8Array|Array.<Number>}bs
    *@param {Object}prop
    *@return {Object}prop
    */
    AwxHeaderBase.readOneHead = function (bs, prop) {
        var flag = DmrUtil.bytesToShort(bs[12], bs[13]);
        var s0 = String.fromCharCode(bs[0] & 0xFF);
        var s1 = String.fromCharCode(bs[1] & 0xFF);
        var s2 = String.fromCharCode(bs[2] & 0xFF);
        var s3 = String.fromCharCode(bs[3] & 0xFF);
        var s4 = String.fromCharCode(bs[4] & 0xFF);
        var s5 = String.fromCharCode(bs[5] & 0xFF);
        var s6 = String.fromCharCode(bs[6] & 0xFF);
        var s7 = String.fromCharCode(bs[7] & 0xFF);
        var s8 = String.fromCharCode(bs[8] & 0xFF);
        var s9 = String.fromCharCode(bs[9] & 0xFF);
        var s10 = String.fromCharCode(bs[10] & 0xFF);
        var s11 = String.fromCharCode(bs[11] & 0xFF);

        var ss = s0 + s1 + s2 + s3 + s4 + s5 + s6 + s7 + s8 + s9 + s10 + s11;

        var s15 = DmrUtil.bytesToShortByflag(bs[14], bs[15], flag);
        var s17 = DmrUtil.bytesToShortByflag(bs[16], bs[17], flag);
        var s19 = DmrUtil.bytesToShortByflag(bs[18], bs[19], flag);
        var s21 = DmrUtil.bytesToShortByflag(bs[20], bs[21], flag);
        var s23 = DmrUtil.bytesToShortByflag(bs[22], bs[23], flag);
        var s39 = DmrUtil.bytesToShortByflag(bs[38], bs[39], flag);

        var s25 = DmrUtil.bytesToShortByflag(bs[24], bs[25], flag);
        var s27 = DmrUtil.bytesToShortByflag(bs[26], bs[27], flag);
        var s29 = DmrUtil.bytesToShortByflag(bs[28], bs[29], flag);
        prop["flag"] = flag;
        prop[""] = s15;
        prop[""] = s17;
        prop[""] = s19;
        prop[""] = s21;
        prop[""] = s23;
        prop[""] = s25;
        prop[""] = s27;
        prop[""] = s29;

        prop[""] = s39;

        return prop;
    }

    AwxHeaderBase.prototype.Update = function (coordEnvelope) {
        throw new Error("AwxHeaderBase.prototype.Update");
    }

