    AwxHeaderBase.prototype.computeEnvelope = function () {
        if (this.Attributes != null && this._coordEnvelope == null) {
            if (this.Attributes.hasOwnProperty("")
                && this.Attributes.hasOwnProperty("")
                && this.Attributes.hasOwnProperty("")
                && this.Attributes.hasOwnProperty("")) {
                var minY = parseFloat(this.Attributes[""]);
                var maxY = parseFloat(this.Attributes[""]);
                var minX = parseFloat(this.Attributes[""]);
                var maxX = parseFloat(this.Attributes[""]);
                this._coordEnvelope = new CoordEnvelope(minX, maxX, minY, maxY);
                this._resolutionX = parseFloat(this._coordEnvelope.Width / parseFloat(this._width));
                this._resolutionY = parseFloat(this._coordEnvelope.Height / parseFloat(this._height));
            }
        }
    }

    /**
   *
   */
    AwxHeaderBase.prototype.Dispose = function () {
        this._dataType = null;
        this._interleave = null;
        this._channelCount = null;
        this._width = null;
        this._height = null;
        this._isExtended = null;
        this._isDay = null;
        this._attributes = null;
        this._coordEnvelope = null;
        this._resolutionX = null;
        this._resolutionY = null;
        this._bandNames = null;
        this._fileName = null;
        this._bandName = null;
        return null;
    }

    /**
  *
  */
    AwxHeaderBase.prototype.destroy = function () {
        return this.Dispose();
    }
    module.exports= AwxHeaderBase;
