var defineProperties=require('./defineProperties');

    /**  
    *@param {Number}X   
    *@param {Number}Y
    *@param {Number}Width  
    *@param {Number}Height   
    *@property {Number}X   
    *@property {Number}Y
    *@property {Number}Width  
    *@property {Number}Height     
    *@constructor
    *@memberof MeteoLib.Data.RasterData
    */
    function RectangleF(x, y, width, height) {
        this.X = x;
        this.Y = y;
        this.Width = width;
        this.Height = height;
    }
    defineProperties(RectangleF.prototype, {
        Left: {
            get: function () {
                return this.X;
            }
        },
        Top: {
            get: function () {
                return this.Y;
            }
        },
        Bottom: {
            get: function () {
                return this.Y + this.Height;
            }
        },
        Right: {
            get: function () {
                return this.X + this.Width;
            }
        }
    });
    /**
    *@param {MeteoLib.Data.RasterData.RectangleF}rec
    *@static
    *@memberof MeteoLib.Data.RasterData.RectangleF
    */
    RectangleF.Intersect = function (rec) {
        throw new Error("RectangleF.Intersect ");
    }
    /**  
   *@param {Number}left   
   *@param {Number}top
   *@param {Number}right   
   *@param {Number}bottom    
   */
    RectangleF.FromLTRB = function (left, top, right, bottom) {
        var rec = new RectangleF(left, top, right - left, bottom - top);
        return rec;
    }
    /**
    *@static
    *@memberof MeteoLib.Data.RasterData.RectangleF
    */
    RectangleF.Empty = new RectangleF();

    module.exports= RectangleF;
