var BinaryReader=require('../../Util/BinaryReader');
var getString=require('../../Util/getString');

    /**
    *
    *@memberof MeteoLib.Data.SWAN
    *@constructor
     *@property {Number} CenterLat  
     *@property {Number} CenterLon  
     *@property {String} DataName  
     *@property {String} Flag  
     *@property {Number} LatNumGrids 
     *@property {Number} LatReso  
     *@property {Number} LonNumGrids  
     *@property {Number} LonReso  
     *@property {Number} StationCount  
     *@property {Number} StartLat  
     *@property {Number} StartLon  
     *@property {String} Version  
     *@property {Number} ZNumGrids  
     *@property {Number} ZhighGrids  
     *@property {String} ZonName  
    */
    function Diamond131Header() {
        this.CenterLat = 0.0;
        this.CenterLon = 0.0;

        this.DataName = "";
        this.Flag = "";
        this.LatNumGrids = 0;
        this.LatReso = 0.0;
        this.LonNumGrids = 0;
        this.LonReso = 0.0;
        this.StationCount = 0;
        this.StartLat = 0.0;
        this.StartLon = 0.0;
        this.Version = "";
        this.ZNumGrids = 0;
        this.ZhighGrids = 0;
        this.ZonName = "";
        this.day = 0;
        this.hour = 0;
        this.interval = 0;
        this.minute = 0;
        this.month = 0;
        this.year = 0;
    }
    /**
    *
    */
    Diamond131Header.isValid = function (buffer) {
        var binaryReader = new BinaryReader(buffer);
        binaryReader.isLittleEndian = true;
        var ZonName = getString(buffer.slice(binaryReader.position, binaryReader.position + 12), "gb2312");
        binaryReader.readBytes(12);

       var DataName = getString(buffer.slice(binaryReader.position, binaryReader.position + 38), "gb2312");
        binaryReader.readBytes(38);
        var Flag = getString(buffer.slice(binaryReader.position, binaryReader.position + 8), "gb2312");
        binaryReader.readBytes(8);
        if (ZonName!='diamond 131'|| Flag.toLowerCase().indexOf('swan') < 0) {
          return false
        }
        return true;
    }
    /**
    *@param {MeteoLib.Util.BinaryReader}
    *@return {MeteoLib.Data.SWAN.Diamond131Header}
    */
    Diamond131Header.parse = function (binaryReader) {
        var diamond131Header = new Diamond131Header();
        var buffer = binaryReader.buffer;// new MeteoLib.Util.BinaryReader(buffer);
        binaryReader.isLittleEndian = true;
        diamond131Header.ZonName = getString(buffer.slice(binaryReader.position, binaryReader.position + 12), "gb2312");
        binaryReader.readBytes(12);

        diamond131Header.DataName = getString(buffer.slice(binaryReader.position, binaryReader.position + 38), "gb2312");
        binaryReader.readBytes(38);
        diamond131Header.Flag = getString(buffer.slice(binaryReader.position, binaryReader.position + 8), "gb2312");
        binaryReader.readBytes(8);
        if (diamond131Header.Flag.toLowerCase().indexOf('swan') < 0) {
            throw new Error("Not SWAN Mosaic QP Product!");
        }
        diamond131Header.Version = getString(buffer.slice(binaryReader.position, binaryReader.position + 8), "gb2312");
        binaryReader.readBytes(8);

        //22
        diamond131Header.year = binaryReader.readShort();
        diamond131Header.month = binaryReader.readShort();
        diamond131Header.day = binaryReader.readShort();
        diamond131Header.hour = binaryReader.readShort();
        diamond131Header.minute = binaryReader.readShort();
        diamond131Header.interval = binaryReader.readShort();
        diamond131Header.LonNumGrids = binaryReader.readShort();
        diamond131Header.LatNumGrids = binaryReader.readShort();
        diamond131Header.ZNumGrids = binaryReader.readShort();
        diamond131Header.StationCount = binaryReader.readInt();

        //24
        diamond131Header.StartLon = binaryReader.readFloat();
        diamond131Header.StartLat = binaryReader.readFloat();
        diamond131Header.CenterLon = binaryReader.readFloat();
        diamond131Header.CenterLat = binaryReader.readFloat();
        diamond131Header.LonReso = binaryReader.readFloat();
        diamond131Header.LatReso = binaryReader.readFloat();
        //160
        diamond131Header.ZhighGrids = new Int32Array(40);
        for (var i = 0; i < 40; i++) {
            diamond131Header.ZhighGrids[i] = (binaryReader.readFloat() * 1000);
        }
        diamond131Header.Reserved = binaryReader.readBytes(752);

        return diamond131Header;
    }

    module.exports= Diamond131Header;
