
    /**
    *@param {Number}minX
    *@param {Number}maxX
    *@param {Number}minY
    *@param {Number}maxY
    *@property {Number}MinX
    *@property {Number}MaxX
    *@property {Number}MinY
    *@property {Number}MaxY
    *@memberof MeteoLib.Data.RasterData
    *@constructor
    */
    function CoordEnvelope(minX, maxX, minY, maxY) {
        this.MinX = minX;
        this.MaxX = maxX;
        this.MinY = minY;
        this.MaxY = maxY;
    }
    /**
    *
    *@return {MeteoLib.Data.RasterData.CoordEnvelope}
    */
    CoordEnvelope.prototype.Clone = function () {
        return new CoordEnvelope(this.MinX,this.MaxX,this.MinY,this.MaxY);
    }
    module.exports= CoordEnvelope;
