String.prototype.Substring = function (start, length) {
    if (typeof length === 'undefined') {
        length = this.length - start;
    }
    return this.substring(start, start + length);
}

String.prototype.startsWith = function (str) {
    if (!str || !str.length) {
        return false;
    }
    if (str.length > this.length) {
        return false;
    }

    var s = this.slice(0, str.length);
    return s == str;
}
String.prototype.endsWith = function (str) {
    if (!str || !str.length) {
        return false;
    }
    if (str.length > this.length) {
        return false;
    }

    var s = this.slice(this.length - str.length, this.length);
    return s == str;
}

String.deleteNull = function (originStr) {
    var str = [];
    for (var i = 0; i < originStr.length; i++) {
        var code = originStr.charCodeAt(i);
        if (code) {
            str.push(originStr[i])
        }
    }
    str = str.join('');
    return str;
}

