/**
      * js;
      * @param {String}format yyyy-MM-dd hh:mm:ss
      * @returns {String}
      * @example
      *  var date=new Date();
      *  var dateStr=date.format("yyyy-MM-dd hh:mm:ss");
      */
Date.prototype.format = function (format) {
    /// <summary></summary>
    /// <param name="format" type="String">yyyy-MM-dd hh:mm:ss</param>

    var o = {
        "M+": this.getMonth() + 1,  //month 
        "d+": this.getDate(),     //day 
        "h+": this.getHours(),    //hour 
        "m+": this.getMinutes(),  //minute 
        "s+": this.getSeconds(), //second 
        "q+": Math.floor((this.getMonth() + 3) / 3),  //quarter 
        "S": this.getMilliseconds() //millisecond 
    }
    var week = ["", "", "", "", "", "", ""];
    if (/(y+)/.test(format)) {
        format = format.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    }
    if (/(w+)/.test(format)) {
        format = format.replace(RegExp.$1, week[this.getDay()]);
    }
    for (var k in o) {
        if (new RegExp("(" + k + ")").test(format)) {
            format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length));
        }
    }
    return format;
}

/**
* js
*
* @param {String}part  y m d h ns
* @param {Number}value 
* @returns {Date}
*/
Date.prototype.add = function (part, value) {
    /// <summary> </summary>
    /// <param name="part" type="String"> y m d h ns </param>
    /// <param name="value" type="Number"></param>

    value *= 1;
    if (isNaN(value)) {
        value = 0;
    }
    var date = new Date(this);
    switch (part) {
        case "y":
            date.setFullYear(this.getFullYear() + value);
            break;
        case "m":
            date.setMonth(this.getMonth() + value);
            break;
        case "d":
            date.setDate(this.getDate() + value);
            break;
        case "h":
            date.setHours(this.getHours() + value);
            break;
        case "n":
            date.setMinutes(this.getMinutes() + value);
            break;
        case "s":
            date.setSeconds(this.getSeconds() + value);
            break;
        default:

    }
    return date;
}

