/**
*
*@param {String}dateStr
*@param {String}format yyyyMMddhhmmsshh24
*@return {Date}
*/

/**
*
*@param {String}dateStr
*@param {String}format yyyyMMddhhmmsshh24
*@return {Date}
*/
function parseDate(dateStr, format) {
    var yyyy = format.indexOf("yyyy"),
        MM = format.indexOf("MM"),
        dd = format.indexOf("dd"),
        hh = format.indexOf("hh"),
        mm = format.indexOf("mm"),
        ss = format.indexOf("ss");
    if (yyyy < 0 || MM < 0) {
        return null;
    }
    var str = "";
    yyyy = dateStr.substr(yyyy, 4);
    str += yyyy;
    MM = dateStr.substr(MM, 2);
    str += "-" + MM;
    if (dd >= 0) {
        dd = dateStr.substr(dd, 2);
        str += "-" + dd;
    }
    if (hh >= 0) {
        hh = dateStr.substr(hh, 2);
        str += " " + hh;
    } else {
        str += " 00";
    }
    if (mm >= 0) {
        mm = dateStr.substr(mm, 2);
        str += ":" + mm;
    } else {
        str += ":00";
    }
    if (ss > 0) {
        ss = dateStr.substr(ss, 2);
        str += ":" + ss
    } else {
        str += ":00";
    }
    return new Date(str);
}

typeof window != 'undefined' ? window.parseDate = parseDate : global.parseDate = parseDate;
