var Path = require('../../Util/Path');

/**
 * 
 * @constructor
 * @memberof MeteoLib.Data.Micaps
 * @property {String}familyName ,T639, SATELLITE
 * @property {String}dataPath URLCassandraCF+Row KeySambasetDataPathDecoder
 * @property {String}dataName CassandraColumn Name,SambasetDataNameDecoder
 * @property {String}originalAbsoluteDataName FTP
 * @property {Number}timezone URL[-12,12]-12120[1,11][-11,-1]Decoder
 */
function MeteorologicalData() {

    this._familyName = "";//,T639, SATELLITE
    this._dataPath = "";//URLCassandraCF+Row KeySambasetDataPathDecoder
    this._dataName = "";//CassandraColumn Name,SambasetDataNameDecoder
    this._timezone = 8;//URL[-12,12]-12120[1,11][-11,-1]Decoder
    this._originalAbsoluteDataName = ""; //FTP
}

Object.defineProperties(MeteorologicalData.prototype, {
    familyName: {
        get: function () {
            return this._familyName;
        }, set: function (val) {
            if (!val) val = "";

            val = val.replace(/\s+/g, "_");//1
            this._familyName = val;
        }
    },
    dataName: {
        get: function () {
            return this._dataName;
        }, set: function (val) {
            this._dataName = val;
        }
    },
    dataPath: {
        get: function () {
            return this._dataPath;
        }, set: function (val) {
            val = Path.normalizePath(val);
            this._dataPath = val;
        }
    },
    timezone: {
        get: function () {
            return this._timezone;
        }, set: function (val) {
            this._timezone = val;
        }
    },
    originalAbsoluteDataName: {
        get: function () {
            return this._originalAbsoluteDataName;
        }, set: function (val) {
            if (!val) val = "";
            val = val.replace(/\\\\+/g, "/");//\/
            val = val.replace(/\/{2,}/g, "/");//22//
            this._originalAbsoluteDataName = val;
        }
    },
    absoluteDataName: {
        get: function () {
            //""/
            return this.dataPath + "/" + this.dataName();
        }
    }
})

MeteorologicalData.serialVersionUID = 1;

