/**
 * 
 * @param {Int|Number} i
 * @return {Array.<Number>|Int8Array}
 */
MeteorologicalData.toLittleEndian = function (i) {
    //2shortJava
    var b = new Int8Array(2);
    b[0] = (i & 0xff);
    b[1] = (i >> 8 & 0xff);
    return b;
}

