/**
 *  envi hdr
 * <pre>
 *  The type of data representation, where 
            1=8-bit byte;
            2=16-bit signed integer; 
            3=32-bit signed long integer;
            4=32-bit floating point; 
            5=64-bit double-precision floating point; 
            6=2x32-bit complex, real-imaginary pair of double precision; 
            9=2x64-bit double-precision complex,real-imaginary pair of double precision; 
            12=16-bit unsigned integer; 
            13=32-bit unsigned long integer; 
            14=64-bit signed long integer;   
            15=64-bit unsigned long integer. 
    </pre>
 * @constructor
 * @memberof MeteoLib.Data.RasterData
 * @property {Number}MinX 
 * @property {Number}MinY 
 * @property {Number}MaxX 
 * @property {Number}MaxY 
 * @property {MeteoLib.Data.RasterData.PointF}LeftUpPoint  
 * @property {MeteoLib.Data.RasterData.PointF}RightDownPoint  
 * @property {Array.<MeteoLib.Data.RasterData.PointF>}Points  
 * @property {MeteoLib.Data.RasterData.PointF}CenterPoint  
 * @property {Number}Width
 * @property {Number}Height 
 * @property {String}Description  
 * @property {Number}Samples  
 * @property {Number}Lines  
 * @property {Number}Bands  
 * @property {Array.<Number>}BandNums 
 * @property {Number}HeaderOffset  
 * @property {String}FileType = "ENVI Standard" 
 * @property {MeteoLib.Data.RasterData.enumDataType}DataType 
 * @property {MeteoLib.Data.RasterData.enumInterleave}Intertleave = MeteoLib.Data.RasterData.enumInterleave.BSQ 
 * @property {Array.<Number>}MajorFrameOffsets  
 * @property {String}SensorType  
 * @property {MeteoLib.Data.RasterData.enumHdrByteOder}ByteOrder = MeteoLib.Data.RasterData.enumHdrByteOder.Host_intel;
 * @property {MeteoLib.Data.RasterData.HdrMapInfo}MapInfo  
 * @property {Array.<MeteoLib.Data.RasterData.HdrGeoPoint>}GeoPoints 
 * @property {Array.<String>}BandNames  
 * @property {MeteoLib.Data.RasterData.HdrProjectionInfo}HdrProjectionInfo  
*/
function HdrFile() {
    this._minX;
    this._maxX;
    this._minY;
    this._maxY;
    this._centerPoint;

    this._description = "File Imported into ENVI";
    this._samples;
    this._lines;
    this._bands;
    this._headerOffset;
    this._fileType = "ENVI Standard";
    this._dataType = 12;
    this._interleave;
    this._majorFrameOffsets;
    this._satellite;
    this._sensorType;
    this._byteOrder;
    this._mapInfo;
    this._projectionInfo;
    this._geoPoints;
    this._bandNames;
    this._regexStr;
    this._bandNums;
    this._filename;

    var majorFrameOffsets = new Int32Array(2);
    this._majorFrameOffsets = majorFrameOffsets;
    this._sensorType = "Unknown";
    this._regexStr = "^(\\d+)\\S*$";
    this._bandNums = [];
 
}
defineProperties(HdrFile.prototype, {
    //
    MinX: {
        get: function () {
            return this._minX;
        },
        set: function (value) {
            this._minX = value;
        }
    },
    //
    MinY: {
        get: function () {
            return this._minY;
        },
        set: function (value) {
            this._minY = value;
        }
    },
    //
    MaxX: {
        get: function () {
            return this._maxX;
        },
        set: function (value) {
            this._maxX = value;
        }
    },
    //
    MaxY: {
        get: function () {
            return this._maxY;
        },
        set: function (value) {
            this._maxY = value;
        }
    },
    LeftUpPoint: {
        get: function () {
            return new PointF(this._minX, this._maxY);
        }
    },
    RightDownPoint: {
        get: function () {
            return new PointF(this._maxX, this._minY);
        }
    },
    Points: {
        get: function () {
            return [this.LeftUpPoint, new PointF(this._maxX, this._maxY), this.RightDownPoint, new PointF(this._minX, this._minY)];
        }
    },
    CenterPoint: {
        get: function () {
            return this._centerPoint;
        }
    },
    Width: {
        get: function () {
            return this._maxX - this._minX;
        }
    },
    Height: {
        get: function () {
            return this._maxY - this._minY;
        }
    },
    Description: {
        get: function () {
            return this._description;
        },
        set: function (value) {
            if (!String.IsNullOrEmpty(value)) {
                this._description = value;
            }
        }
    },
    Samples: {
        get: function () {
            return this._samples;
        },
        set: function (value) {
            if (value >= 0) {
                this._samples = value;
            }
        }
    },
    Lines: {
        get: function () {
            return this._lines;
        },
        set: function (value) {
            if (value >= 0) {
                this._lines = value;
            }
        }
    },
    Bands: {
        get: function () {
            return this._bands;
        },
        set: function (value) {
            this._bands = value;
        }
    },
    BandNums: {//
        get: function () {
            if (this._bandNames == null || this._bandNames.length == 0) {
                return null;
            }
            if (this._bandNums.length != 0) {
                return this._bandNums;
            }
            var flag = false;
            var regex = new RegExp(this._regexStr);

            var bandNames = this._bandNames;
            for (var i = 0; i < bandNames.length; i++) {
                var text = bandNames[i];
                var array = text.split(' ');
                if (array.length == 1 && array[0].toLocaleLowerCase().indexOf("band") != -1) {
                    this._bandNums.push(parseInt(array[0].toLocaleLowerCase().trim().replace("band", "")));
                    flag = true;
                }
                else {
                    if (array.length > 1 && regex.test(array[1].trim()) && array[1].indexOf("-") == -1) {
                        var match = regex.exec(array[1].trim());
                        this._bandNums.push(parseInt(match[1]));
                        if (flag) {
                            flag = false;
                        }
                    }
                    else {
                        if (regex.test(array[0].trim())) {
                            var match2 = regex.exec(array[0].trim());
                            this._bandNums.push(parseInt(match2[1]));
                        }
                        else {
                            this._bandNums.push(-1);
                        }
                    }
                }
            }
            if (flag) {
                this.ProcessModisBand();
            }
            if (this._bandNums.length != 0) {
                return this._bandNums;
            }
            return null;
        }
    },
    HeaderOffset: {
        get: function () {
            return this._headerOffset;
        },
        set: function (value) {
            if (value >= 0) {
                this._headerOffset = value;
            }
        }
    },
    FileType: {
        get: function () {
            return this._fileType;
        },
        set: function (value) {
            if (!String.IsNullOrEmpty(value)) {
                this._fileType = value;
            }
        }
    },
    DataType: {
        get: function () {
            return this._dataType;
        },
        set: function (value) {
            this._dataType = value;
        }
    },
    Intertleave: {
        get: function () {
            return this._interleave;
        },
        set: function (value) {
            this._interleave = value;
        }
    },
    MajorFrameOffsets: {
        get: function () {
            return this._majorFrameOffsets;
        },
        set: function (value) {
            this._majorFrameOffsets = value;
        }
    },
    SensorType: {
        get: function () {
            return this._sensorType;
        },
        set: function (value) {
            if (!String.IsNullOrEmpty(value)) {
                this._sensorType = value;
            }
        }
    },
    ByteOrder: {
        get: function () {
            return this._byteOrder;
        },
        set: function (value) {
            this._byteOrder = value;
        }
    },
    MapInfo: {
        get: function () {
            return this._mapInfo;
        },
        set: function (value) {
            this._mapInfo = value;
        }
    },
    GeoPoints: {
        get: function () {
            return this._geoPoints;
        },
        set: function (value) {
            this._geoPoints = value;
        }
    },
    BandNames: {
        get: function () {
            return this._bandNames;
        },
        set: function (value) {
            this._bandNames = value;
        }
    },
    HdrProjectionInfo: {
        get: function () {
            return this._projectionInfo;
        },
        set: function (value) {
            this._projectionInfo = value;
        }
    }
});

