/**
*
*/
HdrFile.prototype.ProcessModisBand = function () {
    if (this._bandNums == null || this._bandNums.length == 0 || this._bandNums.indexOf(38) == -1) {
        return;
    }
    var count = this._bandNums.length;
    for (var i = 0; i < count; i++) {
        if (this._bandNums[i] > 16 && this._bandNums[i] < 30000) {
            var bandNums;
            var index;
            (bandNums = this._bandNums)[index = i] = bandNums[index] - 2;
        }
        else {
            if (this._bandNums[i] > 13 && this._bandNums[i] < 17) {
                var bandNums2;
                var index2;
                (bandNums2 = this._bandNums)[index2 = i] = bandNums2[index2] - 1;
            }
        }
    }
}
/**
 * @return {MeteoLib.Data.RasterData.enumDataType}
 */
HdrFile.prototype.GetEnumDataType = function () {
    switch (this.DataType) {
        case 1:
            return enumDataType.Byte;
        case 2:
            return enumDataType.Int16;
        case 3:
            return enumDataType.Int32;
        case 4:
            return enumDataType.Float;
        case 5:
            return enumDataType.Double;
        case 12:
            return enumDataType.UInt16;
        case 13:
            return enumDataType.UInt32;
        case 14:
            return enumDataType.Int64;
        case 15:
            return enumDataType.UInt64;
        default:
            return enumDataType.Byte;
    }
}
/**
*
*@return {MeteoLib.Data.RasterData.Envelope}
*/
HdrFile.prototype.GetEnvelope = function () {
    if (this._mapInfo == null) {
        return null;
    }
    var envelope = new Envelope();
    var num = this._mapInfo.XYResolution.Longitude * this._samples;
    var num2 = this._mapInfo.XYResolution.Latitude * this._lines;
    envelope.MinX = this._mapInfo.BaseMapCoordinateXY.Longitude - (this._mapInfo.BaseRowColNumber.X - 1) * this._mapInfo.XYResolution.Longitude;
    envelope.MaxY = this._mapInfo.BaseMapCoordinateXY.Latitude + (this._mapInfo.BaseRowColNumber.Y - 1) * this._mapInfo.XYResolution.Latitude;
    envelope.MaxX = envelope.MinX + num;
    envelope.MinY = (envelope.MaxY - num2);//.toFixed(8);
    return envelope;
}
