HdrFile.GetDataSizeFromDataType = function (dataType) {
    if (dataType == 1) {
        return 1;
    }
    if (dataType == 2) {
        return 2;
    }
    if (dataType == 3) {
        return 4;
    }
    if (dataType == 4) {
        return 4;
    }
    if (dataType == 5) {
        return 8;
    }
    if (dataType == 12) {
        return 2;
    }
    if (dataType == 13) {
        return 4;
    }
    if (dataType == 14) {
        return 8;
    }
    if (dataType == 15) {
        return 8;
    }
    return 0;
}

