/**
*@param {Array.<Array.<Number>>} positions [[lon,lat],[lon,lat],....,[lon,lat]],lonlat
*@return {Array.<Number>}
*/
RasterTileData.prototype.sampleData = function (positions) {
    var result = new this.data.constructor(positions.length);
    var west = Cesium.Math.toRadians(tileData.bbox[0])
    var south = Cesium.Math.toRadians(tileData.bbox[1])
    var east = Cesium.Math.toRadians(tileData.bbox[2])
    var north = Cesium.Math.toRadians(tileData.bbox[3])
    var deltLon = east - west, deltLat = south - north;

    var tileData = this;
    for (var i = 0; i < positions.length; i++) {
        var longitude = positions[i][0],
            latitude = positions[i][1];

        var x = tileData.width * (longitude - west) / deltLon;
        x = parseInt(x);
        var y = tileData.height * (latitude - north) / deltLat;
        y = parseInt(y);
        var idx = (tileData.width * y + x);
        result[i] = tileData.data[idx];
    }
    return result;
}

