function RasterTileImageryProvider(options) {
    options = options ? options : {};
    this.zIndex = options.zIndex;
    if (options.getImageGenerator)
        this.getImageGenerator = options.getImageGenerator;
    this.imageGenerator = options.imageGenerator;
    this.compress = options.compress;
    this.url = options.url;
    this.width = options.width;
    this.height = options.height;
    this.headerSize = defaultValue(options.headerSize, 4096);
    this.typeSize = defaultValue(options.typeSize, 1);
    this.dataType = defaultValue(options.dataType, enumDataType.Byte);
    this.defaultValidRange = options.defaultValidRange;
    this.bands = options.bands ? options.bands : [1];
    this.bandOffsets = null;
    this.userWebWorker = options.userWebWorker;
    this.enableServerCache = options.enableServerCache;

    this.nodataValue = options.nodataValue;


    this._fileName = options.fileName;
    this.hdrFileName = options.hdrFileName;
    this._ready = false;

    this._readyPromise = when.defer();
    if (typeof Cesium != 'undefined') {
        this._rectangle = defaultValue(options.rectangle, Cesium.Rectangle.MAX_VALUE);


        this._dataRectangle = options.dataRectangle;
        if (isArray(options.rectangle) && options.rectangle.length == 4) {
            this.rectangle = Cesium.Rectangle.fromDegrees(
                options.rectangle[0], options.rectangle[1],
                options.rectangle[2], options.rectangle[3]
            )
        }
        if (isArray(options.dataRectangle) && options.dataRectangle.length == 4) {
            this._dataRectangle = Cesium.Rectangle.fromDegrees(
                options.dataRectangle[0], options.dataRectangle[1],
                options.dataRectangle[2], options.dataRectangle[3]
            )
        }
        this._tilingScheme = defaultValue(options.tilingScheme, new Cesium.GeographicTilingScheme());

    }
    this._tileHeight = defaultValue(options.tileHeight, 256);
    this._tileWidth = defaultValue(options.tileWidth, 256);
    this._maximumLevel = options.maximumLevel;
    this._minimumLevel = defaultValue(options.minimumLevel, 0);

    if (options.parseHeader) {
        this.parseHeader = options.parseHeader;
    }
    this._isLocalFile = false;

    this.cacheSize = 200;
    if (this.url || (this.fileName && (
        this.fileName instanceof Blob ||
        this.fileName instanceof File
    ))) {
        if (this.fileName instanceof Blob ||
            this.fileName instanceof File) {
            // this._tileHeight = 128;
            //  this._tileWidth = 128;
            this._isLocalFile = true;
        }
        this.initialize();
    }

}
