/**
*
*/
RasterTileImageryProvider.prototype.initialize = function () {
    var that = this;
    var headerParsed = true;
    var imageGeneratorReady = true;

    function onReady() {
        that.clearCache();
        that.computeMaximumLevel();
        that._ready = true;
        that._readyPromise.resolve(true);
    }
    if (that.parseHeader) {
        headerParsed = false;
        that.requestHeader().then(function (header) {
            if (!that.header)
                that.header = header;
            headerParsed = true;

            if (!that.imageGenerator && that.getImageGenerator) {
                that.imageGenerator = that.getImageGenerator(header);
                if (that.imageGenerator)
                    that.imageGenerator.readyPromise.then(onReady);
                else {
                    onReady();
                }
            } else if (imageGeneratorReady) {
                onReady();
            }

        }).otherwise(function (err) {
            headerParsed = true;
            if (!that.imageGenerator && that.getImageGenerator) {
                that.imageGenerator = that.getImageGenerator(that.header);
                if (that.imageGenerator)
                    that.imageGenerator.readyPromise.then(onReady);
                else {
                    onReady();
                }
            } else if (imageGeneratorReady) {
                onReady();
            }
        })
    } else {

        if (this.imageGenerator && this.imageGenerator.readyPromise) {
            imageGeneratorReady = false;
            this.imageGenerator.readyPromise.then(function () {
                imageGeneratorReady = true;
                if (headerParsed) {
                    onReady();
                }

            }).otherwise(function (err) {
                imageGeneratorReady = true;
                if (headerParsed) {
                    onReady();
                }
            })
        }
    }
}
