/**
 * Requests the image for a given tile.  This function should
 * not be called before {@link MeteoLib.Scene.RasterTileImageryProvider#ready} returns true.
 * @function
 *
 * @param {Number} x The tile X coordinate.
 * @param {Number} y The tile Y coordinate.
 * @param {Number} level The tile level.
 * @returns {Promise.<Image|Canvas>|undefined} A promise for the image that will resolve when the image is available, or
 *          undefined if there are too many active requests to the server, and the request
 *          should be retried later.  The resolved image may be either an
 *          Image or a Canvas DOM object.
 *
 * @exception {DeveloperError} <code>requestImage</code> must not be called before the imagery provider is ready.
 */
RasterTileImageryProvider.prototype.requestImage = function (x, y, level) {
    var that = this;
    if (!that._ready || (this._isLoading && !this._isLocalFile)) {
        return;
    }

    scratchTileRectangle = this._tilingScheme.tileXYToRectangle(x, y, level, scratchTileRectangle)
    if (scratchTileRectangle == Cesium.Rectangle.MAX_VALUE) {
        scratchTileRectangle = scratchTileRectangle.clone();
    }
    if (!this.cache
        || (this.cache && this.cache.count > this.cacheSize)) {
        this.clearCache();
    }
    var key = x + '_' + y + '_' + level;

    if (this.cache[key]) {
        return this.cache[key];
    }

    var intersect = Cesium.Rectangle.intersection(scratchTileRectangle, this._dataRectangle);
    if (!intersect) {
        return undefined;
    }

    this._isLoading = true;

    var offsetX = //parseInt(
        this._tileWidth * (intersect.west - scratchTileRectangle.west) / scratchTileRectangle.width + 0.001
    //);
    var offsetY =// parseInt(
        this._tileHeight * Math.abs(intersect.north - scratchTileRectangle.north) / scratchTileRectangle.height + 0.001
    //);


    var srcRes = (this.bbox[2] - this.bbox[0]) / (this.width + 1);
    var currRes = Cesium.Math.toDegrees(scratchTileRectangle.width) / (this._tileWidth + 1);
    var scaleX = srcRes / currRes;

    srcRes = (this.bbox[3] - this.bbox[1]) / (this.height + 1);
    currRes = Cesium.Math.toDegrees(scratchTileRectangle.height) / (this._tileHeight + 1);

    var scaleY = srcRes / currRes;
    var scale = new Cesium.Cartesian2(scaleX, scaleY)

    var cacheFilePath = ["tileSize-" + this._tileWidth + "_" + this._tileWidth, level, y, x + '.raw'].join('/');

    var promise = this.requestTileData(intersect, scale, cacheFilePath);
    if (promise) {
        return promise.then(function (rasterTileData) {

            var image, xBufferSize, yBufferSize;
            if (rasterTileData.length && rasterTileData.length > 1) {
                xBufferSize = rasterTileData[0].width;
                yBufferSize = rasterTileData[0].height;
            } else {
                xBufferSize = rasterTileData.width;
                yBufferSize = rasterTileData.height;
            }
            if (that.imageGenerator) {
                if (rasterTileData.length && rasterTileData.length > 1) {
                    rasterTileData = rasterTileData[0];
                }
                image = that.imageGenerator.generate(rasterTileData.data, xBufferSize, yBufferSize);
            } else {
                image = that.generateByDefault(rasterTileData, xBufferSize, yBufferSize);
            }
            image = that.tileImageLoaded(image, rasterTileData);

            function onTileProcessed(image) {
                if (offsetX !== 0 || offsetY !== 0
                    || image.width != that._tileWidth
                    || image.height != that._tileHeight) {
                    var cv = document.createElement('canvas');
                    cv.width = that._tileWidth;
                    cv.height = that._tileHeight;
                    var ctx = cv.getContext('2d');

                    ctx.drawImage(image, offsetX, offsetY);
                    image = cv;
                }
                that.cache[key] = image;
                // that.cache[key].tileData = rasterTileData;
                // rasterTileData = !rasterTileData.length ? [rasterTileData] : rasterTileData;
                // for (var index = 0; index < rasterTileData.length; index++) {
                //     delete rasterTileData[index].data;
                // }
                that.cache.count++;
                Cesium.requestAnimationFrame(function () {
                    that._isLoading = false;
                })
                return image;
            }
            if (image) {
                if (image.then) {
                    image.then(function (finalImage) {
                        finalImage = onTileProcessed(finalImage);
                        return finalImage;
                    });
                } else {
                    image = onTileProcessed(image);
                }
            }
            return image;
        }).otherwise(function (err) {
            console.trace(err);
            that._isLoading = false;
        });
    }
    return promise;
};

/**
 * defaultValidRange
 * @param {Array.<Number>|TypeArray}buffer
 * @return {Uint8ClampedArray}
 */
RasterTileImageryProvider.prototype.stretch = function (buffer) {
    var grayValues = new Uint8ClampedArray(buffer.length);
    var min = Number.MAX_VALUE, max = -Number.MAX_VALUE, delt;
    if (this.defaultValidRange) {
        min = this.defaultValidRange[0];
        max = this.defaultValidRange[1];
    } else {
        for (var i = 0; i < buffer.length; i++) {
            if (isNaN(buffer[i])) continue;
            min = Math.min(buffer[i], min);
            max = Math.max(buffer[i], max);
        }
    }

    delt = max - min;
    for (var i = 0; i < buffer.length; i++) {
        var val = ((buffer[i] - min) / delt) * 255;
        if (val > 255) val = 255;
        if (val < 0) val = 0;
        grayValues[i] = val;
    }
    return grayValues;
}
/**
 * @param {Array.<MeteoLib.Scene.RasterTileImageryProvider.RasterTileData>|MeteoLib.Scene.RasterTileImageryProvider.RasterTileData}tileData
 * @param {Number}width
 * @param {Number}height
 * @return {Canvas}
 */
RasterTileImageryProvider.prototype.generateByDefault = function (tileData, width, height) {
    var pixels = new Uint8Array(width * height * 4)
    if (tileData.length == 3) {
        var rVals = this.stretch(tileData[0].data);
        var gVals = this.stretch(tileData[1].data);
        var bVals = this.stretch(tileData[2].data);
        for (var i = 0; i < rVals.length; i++) {
            pixels[i * 4] = rVals[i];
            pixels[i * 4 + 1] = gVals[i];
            pixels[i * 4 + 2] = bVals[i];
            if (tileData[0].data[i] == this.nodataValue
                || tileData[1].data[i] == this.nodataValue
                || tileData[2].data[i] == this.nodataValue) {
                pixels[i * 4 + 3] = 0;
            }
            else pixels[i * 4 + 3] = 255;
        }
    } else {
        tileData = tileData.length && tileData.length ? tileData[0] : tileData;
        buffer = tileData.data;
        buffer = this.stretch(buffer);
        for (var i = 0; i < buffer.length; i++) {
            var val = buffer[i];
            pixels[i * 4] = val;
            pixels[i * 4 + 1] = val;
            pixels[i * 4 + 2] = val;
            if (tileData.data[i] == this.nodataValue) pixels[i * 4 + 3] = 0;
            else pixels[i * 4 + 3] = 255;
        }
    }
    var cv = ImageUtil.fromPixels(pixels, width, height);
    return cv;
}

