RasterTileImageryProvider.prototype.computeMaximumLevel = function () {
    this.bbox = [
        Cesium.Math.toDegrees(this._dataRectangle.west),
        Cesium.Math.toDegrees(this._dataRectangle.south),
        Cesium.Math.toDegrees(this._dataRectangle.east),
        Cesium.Math.toDegrees(this._dataRectangle.north)
    ];
    var srcRes = (this.bbox[2] - this.bbox[0]) / (this.width + 1);

    for (var level = 0; level < 22; level++) {
        scratchTileRectangle = this._tilingScheme.tileXYToRectangle(0, 0, level, scratchTileRectangle)
        if (scratchTileRectangle == Cesium.Rectangle.MAX_VALUE) {
            scratchTileRectangle = scratchTileRectangle.clone();
        }
        var currRes = Cesium.Math.toDegrees(scratchTileRectangle.width) / (this._tileWidth + 1);
        if (currRes <= srcRes) {
            this._maximumLevel = level;
            break;
        }
    }
}

RasterTileImageryProvider.prototype._requestHeaderLocalFile = function () {

}
/**
*
*/
RasterTileImageryProvider.prototype.requestHeader = function () {

    var url = this.url;
    var that = this;
    var hdrFileName = this.hdrFileName;
    if (!hdrFileName) {
        hdrFileName = this.fileName;
    }
    var promise;
    if (hdrFileName instanceof File || hdrFileName instanceof Blob) {//
        var fs = new FileSystem();
        promise = Cesium.when.defer();

        if (!this.hdrFileName && Cesium.defined(this.headerSize)) {
            promise = fs.readFile(hdrFileName, 0, this.headerSize)
        }
        else {
            promise = fs.readFile(hdrFileName)
        }

    } else {

        var params = [];
        if (url.indexOf("?") >= 0 && !url.endsWith('&')) {
            params.push('')
        }
        params.push("fileName=" + hdrFileName);
        if (!this.hdrFileName && Cesium.defined(this.headerSize)) {
            params.push("offset=0");
            params.push("length=" + this.headerSize);
        }

        if (params.length) {
            if (url.indexOf('?') >= 0) {
                if (url.indexOf('&') < 0) {
                    url += "&";
                }
                url += params.join('&');
            } else {
                url += "?" + params.join('&');
            }
        }
        promise = loadArrayBuffer(url)

    }

    if (hdrFileName.name) {
        hdrFileName = hdrFileName.name;
    }
    if (promise)
        return promise.then(function (buf) {
            buf = new Uint8Array(buf)
            return that.parseHeader(buf, hdrFileName);
        });
}
/**
*,
*@param {ArrayBuffer}headerBuffer
*@param {String}headerFileName
*@return {Object|Promise.<Object>} 
*/
RasterTileImageryProvider.prototype.parseHeader = function (headerBuffer, headerFileName) {
    if (headerFileName.toLowerCase().endsWith('hdr')) {
        var text = getString(headerBuffer);
        var hdr = HdrFile.LoadFrom(text);
        this.header = hdr;
        this.width = this.header.Samples;
        this.height = this.header.Lines;
        this.header.CoordEnvelope = this.header.GetEnvelope();
        var ce = this.header.CoordEnvelope;
        this.bbox = [ce.MinX, ce.MinY, ce.MaxX, ce.MaxY];
        this._dataRectangle = Cesium.Rectangle.fromDegrees(
            ce.MinX, ce.MinY, ce.MaxX, ce.MaxY, undefined, this.rectangle
        );
        if (!this.rectangle || this.rectangle == Cesium.Rectangle.MAX_VALUE)
            this.rectangle = this._dataRectangle;
        this.headerSize = this.header.HeaderOffset;
        this.typeSize = HdrFile.GetDataSizeFromDataType(this.header.DataType);
        this.dataType = this.header.GetEnumDataType();

        return this.header;
    }
}


/**
*@param {Cesium.Rectangle}rectangle
*@param {Number}scale
*@param {String}[cacheFilePath]
*@param {Boolean}[force=false]
*@return {Promise.<RasterTileData|Array.<RasterTileData>>}
*/
RasterTileImageryProvider.prototype.requestTileData = function (rectangle, scale, cacheFilePath, force) {

    var bbox = this.bbox;
    var selectedBBox = bbox;

    if (rectangle) {
        selectedBBox = [
            Cesium.Math.toDegrees(rectangle.west),
            Cesium.Math.toDegrees(rectangle.south),
            Cesium.Math.toDegrees(rectangle.east),
            Cesium.Math.toDegrees(rectangle.north)
        ];
    }

    var xSize = this.width * (selectedBBox[2] - selectedBBox[0]) / (bbox[2] - bbox[0]);
    var ySize = this.height * (selectedBBox[3] - selectedBBox[1]) / (bbox[3] - bbox[1]);
    var xOffset = this.width * (selectedBBox[0] - bbox[0]) / (bbox[2] - bbox[0]);
    var yOffset = this.height * (selectedBBox[3] - bbox[3]) / (bbox[1] - bbox[3]);

    //     var xSize = parseInt(intersetRect.width / ds.resX + 0.5),
    //     ySize = parseInt(intersetRect.height / ds.resY + 0.5);
    // var xOffset = parseInt((intersetRect.west - ds.rectangle.west) / ds.resX + 0.001),
    //     yOffset = parseInt((ds.rectangle.north - intersetRect.north) / ds.resY + 0.001);
    // var xBufferSize = parseInt(tileWidth * intersetRect.width / rect.width + 0.5),
    //     yBufferSize = parseInt(tileHeight * intersetRect.height / rect.height + 0.5);


    var headerSize = this.headerSize ? this.headerSize : 0;
    var typeSize = this.typeSize ? this.typeSize : 1;
    var compress = this.compress;
    var dataType = this.dataType;
    var stride = this.width;
    var numberOfY = this.height;
    var flipY = this.flipY;
    var bands = this.bands;
    var bandOffsets = [];

    if (!this.bandOffsets || !this.bandOffsets.length) {
        for (var index = 0; index < bands.length; index++) {
            var bandNo = bands[index];
            bandOffsets[index] = headerSize + (bandNo - 1) * stride * numberOfY * typeSize;
        }
    } else {
        bandOffsets = this.bandOffsets;
    }
    this.bandOffsets = bandOffsets;

    scale = scale ? scale : 1;
    if (typeof scale == 'number') {
        scale = new Cesium.Cartesian2(scale, scale)
    }
    var xBufferSize = xSize * scale.x,
        yBufferSize = ySize * scale.y;

    var that = this;
    var promise;
    if (this.fileName instanceof File || this.fileName instanceof Blob) {//
        var fs = this.fs;

        if (!fs) {
            fs = new FileSystem();
            this.fs = fs;
        }

        if (!force) {
            if (fs.isLoading && !this.userWebWorker) {
                return undefined;
            }
            fs.isLoading = true;
        }

        var date = new Date();
        promise = fs.readRasterData(this.fileName, xOffset, yOffset, xSize, ySize,
            stride, typeSize, xBufferSize, yBufferSize, bandOffsets, flipY, numberOfY,
            headerSize, this.userWebWorker);
        if (promise) {
            promise.then(function (buf) {
                console.log(new Date() - date)
                fs.isLoading = false;
                return buf;
            }).otherwise(function (e) {
                fs.isLoading = false;
                return e;
            })
        }

    } else {
        var url = this.url;
        var params = [];


        if (url.indexOf("?") >= 0 && !url.endsWith('&')) {
            params.push('')
        }
        params.push("fileName=" + this.fileName);
        params.push("xOffset=" + (xOffset));
        params.push("yOffset=" + (yOffset));
        params.push("xSize=" + (xSize));
        params.push("ySize=" + (ySize));
        params.push("stride=" + stride);
        params.push("typeSize=" + typeSize);
        params.push("headerSize=" + headerSize);
        params.push("xBufferSize=" + xBufferSize);
        params.push("yBufferSize=" + yBufferSize);
        params.push("numberOfY=" + numberOfY);
        if (this.enableServerCache && cacheFilePath) {
            params.push("cacheFilePath=" + cacheFilePath);
        }
        if (flipY) {
            params.push("flipY=true");
        } else {
            params.push("flipY=false");
        }
        if (bandOffsets.length) {
            params.push("bandOffsets=" + JSON.stringify(bandOffsets));
        }
        var os = this.os;
        if (!this.os) {
            os = (function () {
                var UserAgent = navigator.userAgent.toLowerCase();
                return {
                    isIpad: /ipad/.test(UserAgent),
                    isIphone: /iphone os/.test(UserAgent),
                    isAndroid: /android/.test(UserAgent),
                    isWindowsCe: /windows ce/.test(UserAgent),
                    isWindowsMobile: /windows mobile/.test(UserAgent),
                    isWin2K: /windows nt 5.0/.test(UserAgent),
                    isXP: /windows nt 5.1/.test(UserAgent),
                    isVista: /windows nt 6.0/.test(UserAgent),
                    isWin7: /windows nt 6.1/.test(UserAgent),
                    isWin8: /windows nt 6.2/.test(UserAgent),
                    isWin81: /windows nt 6.3/.test(UserAgent),
                    isMac: /mac os/.test(UserAgent)
                };
            }());
            this.os = os;
        }
        var userAgent = navigator.userAgent;
        if (os.isMac || os.isIpad || os.isIphone || (Cesium.defined(compress) && !compress)) {
            params.push("compress=false");
        }

        if (params.length) {
            if (url.indexOf('?') >= 0) {
                if (url.indexOf('&') < 0) {
                    url += "&";
                }
                url += params.join('&');
            } else {
                url += "?" + params.join('&');
            }
        }

        promise = loadArrayBuffer(url)
    }
    if (promise)
        return promise.then(function (buf) {
            var xBufferSize = parseInt(xSize * scale.x),
                yBufferSize = parseInt(ySize * scale.y);
            return that.parseTileData(buf, xBufferSize, yBufferSize, selectedBBox);
        })
}
/**
 * 
 * @param {ArrayBuffer}buf
 * @param {Number} xBufferSize
 * @param {Number} yBufferSize
 * @param {Array.<Number>}bbox
 * @return {MeteoLib.Scene.RasterTileImageryProvider.RasterTileData|Array.<MeteoLib.Scene.RasterTileImageryProvider.RasterTileData>}
 */
RasterTileImageryProvider.prototype.parseTileData = function (buf, xBufferSize, yBufferSize, bbox) {
    var data;
    var dataType = this.dataType;
    var tileDataArr = [];
    var offset = 0, sizePerBand = xBufferSize * yBufferSize;
    var bands = this.bands;
    for (var index = 0; index < bands.length; index++) {
        var bandNo = bands[index];
        offset = sizePerBand * index * this.typeSize;
        switch (dataType) {
            case enumDataType.Byte:
                data = new Uint8Array(buf, offset, sizePerBand);
                break;
            case enumDataType.Int16:
                data = new Int16Array(buf, offset, sizePerBand);
                break;
            case enumDataType.UInt16:
                data = new Uint16Array(buf, offset, sizePerBand);
                break;
            case enumDataType.Int32:
                data = new Int32Array(buf, offset, sizePerBand);
                break;
            case enumDataType.UInt32:
                data = new Uint32Array(buf, offset, sizePerBand);
                break;
            case enumDataType.Float:
                data = new Float32Array(buf, offset, sizePerBand);
                break;
            case enumDataType.Double:
                data = new Float64Array(buf, offset, sizePerBand);
                break;
            default:
        }
        var tileData = new RasterTileData({
            width: xBufferSize,
            height: yBufferSize,
            data: data,
            bbox: bbox,
            bandNo: bandNo
        })
        tileDataArr.push(tileData);
    }
    if (tileDataArr.length > 1) {
        return tileDataArr;
    } else {
        return tileDataArr[0]
    }

}
/**
*
*/
RasterTileImageryProvider.prototype.clearCache = function () {
    var provider = this;
    if (provider.cache) {
        for (var cacheId in provider.cache) {
            if (provider.cache.hasOwnProperty(cacheId)) {

                var tile = provider.cache[cacheId];
                if (tile) {
                    for (var tileProperty in tile) {
                        if (tile.hasOwnProperty(tileProperty)) {
                            delete tile[tileProperty]
                        }
                    }
                }
                delete provider.cache[cacheId];
            }
        }
    }
    provider.cache = {};
    provider.cacheCount = 0;
}

/**
*
*
*/
RasterTileImageryProvider.prototype.pickFeatures = function (x, y, level, longitude, latitude) {

    var df = Cesium.when.defer();
    var that = this;
    function onSuccess(tileData) {
        var tileDataArr = tileData;
        if (tileDataArr.length > 1)
            tileData = tileDataArr[0]
        var x = tileData.width * (Cesium.Math.toDegrees(longitude) - tileData.bbox[0]) / (tileData.bbox[2] - tileData.bbox[0]);
        x = parseInt(x);
        var y = tileData.height * (Cesium.Math.toDegrees(latitude) - tileData.bbox[3]) / (tileData.bbox[1] - tileData.bbox[3]);
        y = parseInt(y);
        var idx = (tileData.width * y + x) * that.typeSize;
        var pointData = new Uint8Array(that.typeSize);
        for (var i = 0; i < that.typeSize; i++) {
            pointData[i] = tileData.data[idx + i];
        }
        var fcInfos = null;
        if (that.onFeaturesPicked) {
            fcInfos = that.onFeaturesPicked(pointData, x, y, level, longitude, latitude);
        }
        df.resolve(fcInfos)
    }

    scratchTileRectangle = this._tilingScheme.tileXYToRectangle(x, y, level, scratchTileRectangle)
    var intersect = Cesium.Rectangle.intersection(scratchTileRectangle, this._dataRectangle);
    if (!intersect) {
        return undefined;
    }
    var srcRes = (this.bbox[2] - this.bbox[0]) / (this.width + 1);
    var currRes = Cesium.Math.toDegrees(scratchTileRectangle.width) / (this._tileWidth + 1);
    var scaleX = srcRes / currRes;

    srcRes = (this.bbox[3] - this.bbox[1]) / (this.height + 1);
    currRes = Cesium.Math.toDegrees(scratchTileRectangle.height) / (this._tileHeight + 1);

    var scaleY = srcRes / currRes;
    var scale = new Cesium.Cartesian2(scaleX, scaleY)

    var promise = this.requestTileData(scratchTileRectangle, scale);
    if (promise) {
        promise.then(function (tileData) {
            onSuccess(tileData)
        }).otherwise(function (err) {
            df.reject(err);
        })
        return df;
    }
    else {
        return null;
    }
}

/**
*
*@param {Object}pointData
*@param {Number}x
*@param {Number}y
*@param {Number}level
*@param {Number}longitude
*@param {Number}latitude
*@return {Array.<Cesium.ImageryLayerFeatureInfo>}
*/
RasterTileImageryProvider.prototype.onFeaturesPicked = function (pointData, x, y, level, longitude, latitude) {
    var fcInfo = new Cesium.ImageryLayerFeatureInfo()
    fcInfo.data = pointData;
    fcInfo.position = new Cesium.Cartographic(longitude, latitude);
    fcInfo.description = pointData;
    fcInfos = [fcInfo];
    return fcInfos;
}
/**
*
*/
RasterTileImageryProvider.prototype.destroy = function () {
    this.clearCache();
}

module.exports = RasterTileImageryProvider
