var iconv = require('iconv-lite')
/**
*@param {ArrayBuffer}arrayBuffer
*@param {String}encoding
*@memberof MeteoLib.Util
*/
function getString(arrayBuffer, encoding, options) {
    if (!(arrayBuffer instanceof Uint8Array)
        && !(arrayBuffer instanceof ArrayBuffer) && arrayBuffer.buffer) {
        arrayBuffer = arrayBuffer.buffer;
    }
    var decoder
    if (typeof TextDecoder == 'undefined') {

        if (typeof iconv !== 'undefined') {
            return iconv.decode(new Uint8Array(arrayBuffer), encoding, options);
        }
        else {
            return String.fromCharCode(new Uint8Array(arrayBuffer));
        }
        // require('text-encoding-polyfill/lib/encoding-indexes')
        // TextDecoder = require('text-encoding-polyfill').TextDecoder;

        // // eval("require('text-encoding-polyfill/lib/encoding-indexes')")
        // // TextDecoder = eval("require('text-encoding-polyfill')").TextDecoder
        // decoder = new TextDecoder(encoding);
    }
    else decoder = new TextDecoder(encoding);

    var decodedText = decoder.decode(arrayBuffer, { stream: true });
    return decodedText;
}

