var enumDataType=require('./enumDataType');
var DataTypeHelper=require('./DataTypeHelper');

    /**
    *  
    *@property {Number}BandNo 
    *@property {MeteoLib.Data.RasterData.RasterDataProvider}RasterDataProvider  
    *@property {String}Description 
    *@property {Number}MeasureBits  
    *@property {MeteoLib.Data.RasterData.enumDataType}DataType = MeteoLib.Data.RasterData.enumDataType.UInt16 
    *@property {Number}DataTypeSize  DataType
    *@property {Number}Width 
    *@property {Number}Height  
    *@property {Number}ResolutionX  x
    *@property {Number}ResolutionY  x
    *@property {Number}NoDataValue 
    *@property {Number}DataOffset 
    *@property {Number}DataScale  
    *@property {Number}MinByMeasureBits  
    *@property {Number}MaxByMeasureBits  
    *@constructor
    *@memberof MeteoLib.Data.RasterData
    *@see MeteoLib.Data.Ldf.LdfRasterBand
   *@see MeteoLib.Data.Envi.EnviRasterBand
    */
    function RasterBand() {
        this._rasterDataProvider;
        //this._attributes = new AttributeManager();
        this._bandNo;
        this._description;
        this._measureBits;
        this._dataType = enumDataType.Unknow;
        this._dataTypeSize;
        this._width;
        this._height;
        this._noDataValue;
        this._dataOffset;
        this._dataScale;
        //		protected ISpatialReference _spatialRef;
        //		protected ICoordTransform _coordTransofrm;
        this._minByMeasureBits;
        this._maxByMeasureBits;
        this._resolutionX;
        this._resolutionY;
        //		protected CoordEnvelope _coordEnvelope;
        //		protected object _stretcher;
        if (typeof SDK !== 'undefined') {//
            this.BandNo = 1;
            this.RasterDataProvider = this._rasterDataProvider;
            this.Description = "";
            this.MeasureBits = -1;
            this.DataType = enumDataType.UInt16;
            this.DataTypeSize = 2;
            this.Width = 500;
            this.Height = 500;
            this.ResolutionX = 0.01;
            this.ResolutionY = 0.01;
            this.NoDataValue = 65535;
            this.DataOffset = 0;
            this.DataScale = 0;
            this.MinByMeasureBits = 0;
            this.MaxByMeasureBits = 0;
        }
    }
    defineProperties(RasterBand.prototype, {
        BandNo: {
            get: function () {
                return this._bandNo;
            },
            set: function (value) {
                this._bandNo = value;
            }
        },
        RasterDataProvider: {
            get: function () {
                return this._rasterDataProvider;
            }
        },
        Description: {
            get: function () {
                return this._description;
            },
            set: function (value) {
                this._description = value;
            }
        },
        MeasureBits: {
            get: function () {
                return this._measureBits;
            },
            set: function (value) {
                this._measureBits = value;
            }
        },
        DataType: {
            get: function () {
                return this._dataType;
            }
        },
        DataTypeSize: {
            get: function () {
                if (this._dataTypeSize == 0) {
                    this._dataTypeSize = DataTypeHelper.SizeOf(this._dataType);
                }
                return this._dataTypeSize;
            }
        },
        Width: {
            get: function () {
                return this._width;
            }
        },
        Height: {
            get: function () {
                return this._height;
            }
        },
        ResolutionX: {
            get: function () {
                return this._resolutionX;
            }
        },
        ResolutionY: {
            get: function () {
                return this._resolutionY;
            }
        },
        NoDataValue: {
            get: function () {
                return this._noDataValue;
            }
        },
        DataOffset: {
            get: function () {
                return this._dataOffset;
            }
        },
        DataScale: {
            get: function () {
                return this._dataScale;
            }
        },
        MinByMeasureBits: {
            get: function () {
                return this._minByMeasureBits;
            }
        },
        MaxByMeasureBits: {
            get: function () {
                return this._maxByMeasureBits;
            }
        }
    });
    RasterBand.prototype.CheckArgumentsisValid = function (xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize) {
        /// <summary></summary>

        if (xOffset < 0 || yOffset < 0 || xOffset + xSize > this._width || yOffset + ySize > this._height) {
            throw new Error("(OffsetX = " + xOffset + ",OffsetY = " + yOffset + ",XSize =" + xSize + ",YSize = " + ySize + ")");
        }
        if (xBufferSize == 0 || yBufferSize == 0) {
            throw new Error("");
        }
    }
    /**
    *
    *@param {Number}xOffset x
    *@param {Number}xOffset y
    *@param {Number}xSize x
    *@param {Number}ySize y
    *@param {TypeArray}xOffset 
    *@param {MeteoLib.Data.RasterData.enumDataType}dataType 
    *@param {Number}xBufferSize bufferx
    *@param {Number}yBufferSize buffery
    */
    RasterBand.prototype.Read = function (xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize) {
        /// <summary></summary>
        /// <param name="xOffset" type="Number">x</param>
        /// <param name="yOffset" type="Number">y</param>
        /// <param name="xSize" type="Number">x</param>
        /// <param name="ySize" type="Number">y</param>
        /// <param name="buffer" type="TypeArray"></param>
        /// <param name="dataType" type="enumDataType"></param>
        /// <param name="xBufferSize" type="Number">bufferx</param>
        /// <param name="yBufferSize" type="Number">buffery</param>

        this.CheckArgumentsisValid(xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize);
        this.DirectRead(xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize);
    }
    /**
  *
  *@param {Number}xOffset x
  *@param {Number}xOffset y
  *@param {Number}xSize x
  *@param {Number}ySize y
  *@param {TypeArray}xOffset 
  *@param {MeteoLib.Data.RasterData.enumDataType}dataType 
  *@param {Number}xBufferSize bufferx
  *@param {Number}yBufferSize buffery
  */
    RasterBand.prototype.DirectRead = function (xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize) {
        /// <summary></summary>

        throw new Error("RasterBand.prototype.DirectRead");
    }
    /**
 *
 *@param {Number}noDataValue  
 */
    RasterBand.prototype.Fill = function (noDataValue) {
        throw new Error("RasterBand.prototype.Fill");
    }
