/**
 *  
 * @memberof MeteoLib.Util.unbzip2
 * @param {ArrayBuffer|Uint8Array} 
 * @return {Promise.<Uint8Array>}
 */
unbzip2.decompress = function (buf) {
    var deferred = when.defer();
    var compressed;
    if (buf instanceof Uint8Array) {
        compressed = buf;
    } else {
        compressed = new Uint8Array(buf);
    }
    var unbz2 = unbzip2.unbzip2Stream();
    var list = [];
    unbz2.on("data", function (data) {
        list.push(data);
    })
    unbz2.on('end', function (data) {

        compressed = [];
        var len = 0;
        for (var i = 0; i < list.length; i++) {
            len += list[i].length;
        }

        compressed = new Uint8Array(len);

        var index = 0;
        for (var i = 0; i < list.length; i++) {
            for (var j = 0; j < list[i].length; j++) {
                compressed[index] = list[i][j];
                index++;
            }
        }
        unbz2 = null;
        list = [];

        deferred.resolve(compressed)

    });

    unbz2.on('error', function (err) {
        deferred.reject(err);
    });

    unbz2.write(compressed);
    unbz2.end();
    return deferred.promise;
}
