var BinaryReader = require('../../Util/BinaryReader');


var NEXRAD_RASTER_DATA_LEVEL_COUNT = 16;
var NEXRAD_RASTER_DEPENDENT_COUNT = 10;

function MessageHeader(br) {
    this.code = br.readShort();
    this.date = br.readShort();
    this.time = br.readInt();
    this.length = br.readInt();
    this.sourceId = br.readShort();
    this.destinationId = br.readShort();
    this.numberOfBlocks = br.readShort();
}

function ProductDescription(br) {
    this.blockDivider = br.readShort();
    this.latitude = br.readInt() * 0.001;
    this.longitude = br.readInt() * 0.001;
    this.height = br.readShort();
    this.productCode = br.readShort();
    this.operationalMode = br.readShort();
    this.volumeCoveragePattern = br.readShort();
    //this.volumeCoveragePattern =
    var spare = br.readShort();
    this.volumeScanNumber = br.readShort();
    this.volumeScanDate = br.readShort();
    this.volumeScanStartTime = br.readInt();
    this.generationDate = br.readShort();
    this.generationTime = br.readInt();

    this.productDependents = [
        br.readShort(),//p1
        br.readShort()//p2
    ];


    //this.spare1 = br.readBytes(4);//see table I

    this.elevationNumber = br.readShort();

    this.productDependents.push(br.readShort());//p3

    this.dataLevelThresholds = [];
    for (var i = 0; i < NEXRAD_RASTER_DATA_LEVEL_COUNT; i++) {
        this.dataLevelThresholds.push(br.readShort());
    }

    for (var i = 0; i < NEXRAD_RASTER_DEPENDENT_COUNT - 3; i++) {
        this.productDependents.push(br.readShort());
    }
    //this.spare2 = br.readBytes(24 * 2);//see table I

    this.numberOfMapsOrVersionOrSpotBlank = br.readShort();
    this.offsetToSymbology = br.readInt();
    this.offsetToGraphic = br.readInt();
    this.offsetToTabular = br.readInt();

}

/**
*@constructor
*@param {Function} ProductSymbologyPacket
*@memberof MeteoLib.Data.PupProductFormat
*@property {Array.<MeteoLib.Data.PupProductFormat.SymbologyLayer>} layers
*/
function ProductSymbology(br, ProductSymbologyPacket) {

    //br.seek(120, BinaryReader.SeekOrigin.Begin)
    this.blockDivider = br.readShort();
    this.blockId = br.readShort();
    this.lengthOfBlock = br.readInt();
    this.numberOfLayers = br.readShort();
    this.layers = []

    for (var i = 0; i < this.numberOfLayers; i++) {
        var layer = new SymbologyLayer(br, ProductSymbologyPacket);
        this.layers.push(layer);
    }
}

/**
*@constructor
*@memberof MeteoLib.Data.PupProductFormat
*@property {Array.<MeteoLib.Data.PupProductFormat.ProductSymbologyPacket>} packets
*/
function SymbologyLayer(br, ProductSymbologyPacket) {
    this.layerDivider = br.readShort();
    this.lengthOfDataLayer = br.readInt();
    this.packets = [];
    var pk = new ProductSymbologyPacket(br);
    this.packets.push(pk);
}

/**
*pup
*@param {MeteoLib.Util.BinaryReader} br
*@constructor
*@memberof MeteoLib.Data.PupProductFormat
*/
function ProductSymbologyPacket(br) {

}

/**
*@constructor
*@memberof MeteoLib.Data.PupProductFormat
*@property {Array.<Number>} colorCodes
*/
function RasterDataPacket(br) {

    this.type = br.readShort();
    this.flags_1 = br.readShort();
    this.flags_2 = br.readShort();
    this.iCoordinateStart = br.readShort();
    this.jCoordinateStart = br.readShort();

    this.xScale = br.readShort();
    this.xScaleFractor = br.readShort();
    this.yScale = br.readShort();
    this.yScaleFractor = br.readShort();

    this.numberOfRows = br.readShort();
    this.packingDescriptor = br.readShort();

    var maxRun = 0;
    var minRun = Number.MAX_VALUE;

    var idx = 0;
    this.colorCodes = new Uint8Array(this.numberOfRows * this.numberOfRows);

    //RLE
    for (var lineNo = 0; lineNo < this.numberOfRows; lineNo++) {
        var numberOfBytes = br.readShort();

        for (var i = 0; i < numberOfBytes; i++) {
            var byteVal = br.readUbyte();//
            if (byteVal > 0) {
                var bits = byteVal.toString(2);//80
                var leftBitCount = 8 - bits.length;
                for (var j = 0; j < leftBitCount; j++) {
                    bits = '0' + bits;
                }

                //RLE

                var run = bits.substr(0, 4);
                run = parseInt(run, 2);
                var colorCode = bits.substr(4, 4);
                colorCode = parseInt(colorCode, 2);

                for (var j = 0; j < run; j++) {
                    this.colorCodes[idx++] = colorCode;
                }
            }
        }
    }

}

function GraphicAlphanumeric(br) {
    this.blockDivider = br.readShort();
    this.blockId = br.readShort();
    this.length = br.readInt();
    this.numberOfPages = br.readShort();
    this.pageNumber = br.readShort();
    this.lengthOfPage = br.readShort();
    this.textPagckets = [];

    for (var i = 0; i < this.numberOfPages; i++) {
        var packet = new TextPacket(br);
        this.textPagckets.push(packet)
    }

}

function TextPacket(br) {
    this.packetCode = br.readShort();
    this.lengthBlock = br.readShort();
    if (this.packetCode == 8) {
        this.valueOfText = br.readShort();
    }
    this.iStartingPoint = br.readShort();
    this.jStartingPoint = br.readShort();
    this.characters = [];
    for (var i = 0; i < this.lengthBlock; i++) {
        var ch = br.readByte();
        this.characters.push(String.fromCharCode(ch));
    }
    this.str = this.characters.join('');
}

function TabularAlphanumeric(br) {
    this.blockDivider = br.readShort();
    this.blockId = br.readShort();
    this.lengthOfBlock = br.readInt();

    this.msgHeader = new MessageHeader(br);

    this.secondBlock = new TabularSecondBlock(br);
}
function TabularSecondBlock(br) {
    this.blockDivider = br.readShort();
    this.numberOfPages = br.readShort();

    this.pages = [];

    function TabularSecondBlockPage(br) {
        this.numberOfCharacters = br.readShort();
        this.characterData = [];
        for (var i = 0; i < this.numberOfCharacters; i++) {
            var ch = br.readByte();
            this.characterData.push(String.fromCharCode(ch))
        }
        this.str = this.characterData.join('');
    }

    for (var i = 0; i < this.numberOfPages; i++) {
        var page = new TabularSecondBlockPage(br);
        this.pages.push(page);
    }

    this.endOfPageFlag = br.readShort();
}
/**
*
*{@link https://github.com/xantronix/libnexrad}
*@constructor
*@memberof MeteoLib.Data
*/
function PupProductFormat() {
    this.msgHeader = null;// new MessageHeader(br);
    this.productDescription = null;//new ProductDescription(br);
    this.productSymbology = null;//new ProductSymbology(br, this.msgHeader);
    // br.seek(this.productDescription.offsetToGraphic * 2, BinaryReader.SeekOrigin.Begin);
    this.graphicAlphanumeric = null;//new GraphicAlphanumeric(br);
    this.tabularAlphanumeric = null;// new TabularAlphanumeric(br);
}

/**
*@param {Uint8Array|ArrayBuffer}buffer
*@return {Boolean}
*/
PupProductFormat.prototype.loadByteArray = function (buffer, ProductSymbologyPacket) {
    try {
        var br = new BinaryReader(buffer);
        this.msgHeader = new MessageHeader(br);
        this.productDescription = new ProductDescription(br);
        if (!ProductSymbologyPacket) {
            switch (this.msgHeader.code) {
                case 36:
                case 37:
                case 38:
                case 41:
                case 57:
                    ProductSymbologyPacket = RasterDataPacket;
                    break;
                default:
                    throw new Error('(message code:' + msgHeader.code + ')');
            }
        }
        this.productSymbology = new ProductSymbology(br, ProductSymbologyPacket);
        // br.seek(this.productDescription.offsetToGraphic * 2, BinaryReader.SeekOrigin.Begin);
        this.graphicAlphanumeric = new GraphicAlphanumeric(br);
        this.tabularAlphanumeric = new TabularAlphanumeric(br);
        return true;
    } catch (e) {
        return false;
    }

}

