/**
*
*@callback MeteoLib.Scene.GridDataImageryProvider~BeforeDrawPointCallback
*@param {HTMLCanvas2DContext}context
*@param {Object} args
*@param {Number} args.scalarValue
*@param {Number} args.vectorValue
*@param {Number} args.x
*@param {Number} args.y
*@param {Number} args.lon
*@param {Number} args.lat
*@param {Boolean} args.cancel
*/


/**
 * 
 * @param {Cesium.Rectangle} rectangle
 * @param {Object} tileData
 * @param {Number}level
 * @param {Number}tileWidth
 * @param {Number}tileHeight
 * @return {Object}
 */
GridDataImageryProvider.prototype.getTileData = function (rectangle, tileData, level, tileWidth, tileHeight) {
    var west = rectangle.west;
    var south = rectangle.south;
    var east = rectangle.east;
    var north = rectangle.north;
    var lonW = (east - west) / 2, latH = (south - north) / 2;
    var dlon = (east - west) / tileWidth;
    var dlat = (south - north) / tileHeight;
    if (!tileData) {
        tileData = {
            array: new Float32Array(tileWidth * tileHeight),
            x: new Float32Array(tileWidth),
            y: new Float32Array(tileHeight)
        };
    }
    tileData.west = west;
    tileData.south = south;
    tileData.east = east;
    tileData.north = north;

    tileData.nx = dlon;
    tileData.ny = dlat;
    for (var i = 0; i < tileWidth; i++) {
        tileData.x[i] = i * dlon + west;
    }
    for (var i = 0; i < tileHeight; i++) {
        tileData.y[i] = i * dlat + north;
    }

    if (this._point || this._value) {
        tileData.pointArray = [];
        var rec = this.ds.geoRectangle2XYRectangle(
            west - this.ds.resLon,
            north - this.ds.resLat,
            east + this.ds.resLon,
            south + this.ds.resLat);
        var deltSize = 1;
        if (level < this.maxSamplePointLevel) {
            var srcRes = (this.ds.east - this.ds.west) / (this.ds.width + 1);
            var currRes = Cesium.Math.toDegrees(rectangle.width) / (tileWidth + 1);
            var deltSize = currRes / srcRes;
            if (deltSize < 1) {
                deltSize = 1;
            } else if (this.pointDensity) {
                deltSize = this.pointDensity;
            } else if (this.maximumPointDensity && deltSize > this.maximumPointDensity) {
                deltSize = this.maximumPointDensity;
            }
        }

        //if (level >= 5) {
        //    deltSize = 1;
        //} else if (level >= 3) {
        //    deltSize = this.ds.bands.length > 1 ? 6 - level : -level + this._fontSize;
        //} else {
        //    deltSize = this.ds.bands.length > 1 ? 4 : -level + this._fontSize * 0.5;
        //}
        deltSize = parseInt(deltSize);
        if (level > 1 || this.ds.bands.length > 1) {
            for (var i = rec.NW.y; i < rec.NW.y + rec.height; i += deltSize) {

                for (var j = rec.NW.x; j < rec.NW.x + rec.width; j += deltSize) {
                    var lon = this.ds.west + j * this.ds.resLon;
                    var lat = this.ds.north + i * this.ds.resLat;

                    var pi = i * this.ds.width + j;
                    pi = pi.toFixed(0);
                    var val = this.ds.bands[0][pi];
                    var angle;
                    if (this.ds.bands.length > 1) {
                        angle = this.ds.bands[1][pi];
                    }

                    tileData.pointArray.push({
                        X: lon, Y: lat, value: val, angle: angle
                    })
                }
            }
        }

    }

    if (!this._interpolate && this._fill) {
        if (!tileData.arrayBase) {
            tileData.arrayBase = new Float32Array(this._tileWidth * this._tileHeight);
        }

        var idx = 0;
        for (var i = 0; i < this._tileHeight; i++) {
            var lat = north + i * dlat;
            for (var j = 0; j < this._tileWidth; j++) {
                var lon = west + j * dlon;
                var val = this.ds.selectPoint(0, lon, lat);
                tileData.arrayBase[idx++] = val;
            }
        }
    }
    if (this._isoLine || (this._interpolate && this._fill)) {
        tileData.array = this.ds.resample(1, west, south, east, north, tileWidth, tileHeight, tileData.array)
    }
    return tileData;
}
/**
 * 
 * @param {any} tileData
 */
GridDataImageryProvider.prototype.drawContours = function (tileData, level, tileWidth, tileHeight) {
    var that = this;
    var colorMap = this._colorMap;

    var cv = document.createElement('canvas');

    cv.width = tileWidth;
    cv.height = tileHeight;
    var context = cv.getContext("2d");
    if (this.fill) {
        var imgData = context.getImageData(0, 0, cv.width, cv.height);
        for (var i = 0; i < tileData.array.length; i++) {
            var value = this._interpolate ? tileData.array[i] : tileData.arrayBase[i];
            var color = GridDataColorMap.getColor(value, colorMap);
            if (color) {
                imgData.data[i * 4] = color[0]
                imgData.data[i * 4 + 1] = color[1]
                imgData.data[i * 4 + 2] = color[2]
                imgData.data[i * 4 + 3] = color[3]
            }
        }
        context.putImageData(imgData, 0, 0);
    }
    if (this._isoLine) {
        var pointIndex = 0;

        var opts = {
            font: this._isoValueFontSize + 'px ' + this._isoValueFontFamily,
            textBaseline: 'middle',
            fill: true,
            strokeWidth: this._isoValueStrokeWidth,
            stroke: this._isoValueStroke,
            fillColor: this._isoValueColor,
            strokeColor: this._isoValueStrokeColor,
            backgroundColor: this._isoValueBackColor,
            padding: this._isoValuePadding
        };

        var contours = tileData.contours;
        var x = null, y = null;
        function drawPolygon(coordinates) {
            coordinates.forEach(function (line) {
                var pointIndex = 0;

                context.beginPath();
                line.forEach(function (pt) {
                    x = pt[0], y = pt[1];
                    if (x < 0.5) x = x - 1.5;
                    else if (x >= tileWidth - 1) x = x + 1;
                    if (y < 0.5) y = y - 1;
                    else if (y >= tileHeight - 1.5) y = y + 1;

                    if (pointIndex == 0) {
                        context.moveTo(x, y)
                    } else {
                        context.lineTo(x, y)
                    }
                    pointIndex++;
                })
                context.stroke();

                if (that._isoValue && line.length && level > 2) {
                    var idx = 0;
                    if (line.length > 1) {
                        idx = parseInt(Math.random() * (line.length - 1));
                        idx = Math.max(0, idx);
                    }
                    var pt = line[idx];
                    x = pt[0], y = pt[1];
                    var textImg = Cesium.writeTextToCanvas(value, opts);
                    x = x - textImg.width / 2.0,
                        y = y - textImg.height / 2.0;
                    if (x >= 0 && x + textImg.width < context.canvas.width
                        && y >= 0 && y + textImg.height < +context.canvas.height) {
                        context.drawImage(textImg, x, y);
                    }
                }

            })
        }

        for (var i = 0; i < contours.length; i++) {
            var contour = contours[i];
            var value = contour.value;
            var color = that._lineColor;
            if (that._lineColorType !== 'single') {
                color = GridDataColorMap.getColor(value, colorMap);
                if (color) {
                    color = Cesium.Color.fromBytes(color[0], color[1], color[2], color[3])
                }
                else {
                    color = that._lineColor;
                }
            }

            context.lineWidth = that._lineWidth;
            if (that._lineDash) {
                context.setLineDash(that._lineDash);
            }

            if (color instanceof Cesium.Color) {
                color = color.toCssColorString();
            }
            context.strokeStyle = color;

            contour.coordinates.forEach(function (plygon) {
                if (plygon.length && plygon[0].length && plygon[0][0].length == 2) {
                    drawPolygon(plygon)
                } else {
                    plygon.forEach(function (poly) {
                        drawPolygon(poly)
                    })
                }
            })
        }
    }

    if (tileData.pointArray && tileData.pointArray.length) {
        tileData.pointArray.forEach(function (pt) {
            if (pt.value == undefined) {
                return;
            }
            var x = (pt.X - tileData.west) / (tileData.east - tileData.west);
            x *= cv.width;
            var y = (pt.Y - tileData.north) / (tileData.south - tileData.north);
            y *= cv.height;
            var value = pt.value.toString();
            if (value.length > 4) {
                value = pt.value.toFixed(2);
            }
            var color;

            if (!that._fill) {
                color = GridDataColorMap.getColor(value, colorMap);
                if (color) {
                    color = Cesium.Color.fromBytes(color[0], color[1], color[2], color[3])
                }
            }
            if (that._point) {
                if (!color) {
                    color = that._pointColor
                }
                var eventArgs = {
                    scalarValue: pt.value,
                    vectorValue: pt.angle,
                    x: x,
                    y: y,
                    lon: pt.X,
                    lat: pt.Y,
                    cancel: false
                }
                if (typeof that.beforeDrawPoint == 'function') {
                    that.beforeDrawPoint(context, eventArgs)
                }
                if (!eventArgs.cancel) {
                    context.fillStyle = color.toCssColorString();
                    context.beginPath();
                    context.arc(x, y, that._pointSize, 0, 360);
                    context.closePath()
                    context.fill();
                }
            }
            if (that._value) {
                if (!color) {
                    color = that._valueColor
                }
                var opts = {
                    strokeWidth: that._valueStrokeWidth,
                    font: that._valueFontSize + 'px ' + that._valueFontFamily,
                    textBaseline: 'middle',
                    fill: true,
                    stroke: that._isoValueStroke,
                    fillColor: color,
                    strokeColor: that._valueStrokeColor,
                    backgroundColor: that._valueBackColor,
                    padding: that._valuePadding
                };
                var textImg = Cesium.writeTextToCanvas(value, opts)
                context.drawImage(textImg, x, y + textImg.height / 2)
            }

        })

    }

    return cv;
}

/**
 * 
 */
GridDataImageryProvider.prototype.clearTileCache = function () {
    if (!this._tileCache) {
        this._tileCache = {
            count: 0
        };
    } else {
        for (var i in this._tileCache) {
            if (this._tileCache.hasOwnProperty(i)) {

                delete this._tileCache[i]
            }
        }
        this._tileCache = {
            count: 0
        };
    }
}
/**
 * 
 * @param {HTMLCanvasElement}cv
 * @param {Number}x
 * @param {Number}y
 * @param {Number}level
 * @return {HTMLCanvasElement}
 */
GridDataImageryProvider.prototype.clip = function (cv, x, y, level) {
    if (!this.clipperLayer || !this.clipperLayer.ready) return cv;
    var ctx1 = cv.getContext('2d');
    var imgData = ctx1.getImageData(0, 0, cv.width, cv.height);

    var tcv = this.clipperLayer.requestImageSync(x, y, level);
    var ctx = tcv.getContext('2d');
    var maskImgData = ctx.getImageData(0, 0, tcv.width, tcv.height);
    for (var i = 0; i < maskImgData.data.length; i += 4) {
        if (!maskImgData.data[i + 3]) {
            imgData.data[i + 3] = maskImgData.data[i + 3];
        }
    }
    ctx1.putImageData(imgData, 0, 0);
    return cv;
}

/**
 * 
 * @param {any} x
 * @param {any} y
 * @param {any} level
 */
GridDataImageryProvider.prototype.requestImageSync = function (x, y, level) {
    if (this.clipperLayer && !this.clipperLayer.ready) return;

    if (level < this._minimumLevel || level > this._maximumLevel) {
        return undefined;
    }
    var that = this;
    var key = [x, y, level].join(',')
    if (!this._tileCache || this._tileCache.count > this._maximumCacheSize) {
        this.clearTileCache();
    }
    if (this._tileCache[key]) {
        return this._tileCache[key];
    }

    if (this._isWorking || !this._ready) {
        return undefined
    }
    if (this.clipperLayer) {
        var rectangle1 = this.tilingScheme.tileXYToRectangle(x, y, level);

        var intersection = Cesium.Rectangle.intersection(this.clipperLayer.rectangle, rectangle1)
        if (!intersection) return undefined;
    }

    this._isWorking = true;

    var rectangle = this.tilingScheme.tileXYToNativeRectangle(x, y, level);

    var tw = this._tileWidth;
    var th = this._tileHeight;

    var tileData = this.getTileData(rectangle, null, level, tw, th);

    try {

        if (tileData.contours) {
            delete tileData.contours;
        }
        if (that._isoLine) {// || that._isoValue) {

            //
            var contours = d3Contour.contours()
                .size([tileData.x.length, tileData.y.length])
                .thresholds(that.breaks)
                (tileData.array);
            tileData.contours = contours;

            var innerImg = that.drawContours(tileData, level, tw, th);
            delete tileData.array;
            delete tileData.pointArray;
            delete tileData.x;
            delete tileData.y;
            delete tileData.arrayBase;

            innerImg = that.clip(innerImg, x, y, level);

            that._tileCache[key] = innerImg;
            that._tileCache.count++;
            that._isWorking = false;
            return that._tileCache[key];
        } else {
            var innerImg = that.drawContours(tileData, level, tw, th);
            delete tileData.array;
            delete tileData.pointArray;
            delete tileData.x;
            delete tileData.y;
            delete tileData.arrayBase;
            innerImg = that.clip(innerImg, x, y, level);

            that._tileCache[key] = innerImg;
            that._tileCache.count++;
            that._isWorking = false;
            return that._tileCache[key];
        }

    } catch (e) {
        console.log(e);
        that._isWorking = false;
        return undefined;
    }

}

/**
 * 
 * @param {any} x
 * @param {any} y
 * @param {any} level
 */
GridDataImageryProvider.prototype.requestImage = function (x, y, level) {
    if (this.clipperLayer && !this.clipperLayer.ready) return;

    if (level < this._minimumLevel || level > this._maximumLevel) {
        return undefined;
    }
    var that = this;
    var key = [x, y, level].join(',')
    if (!this._tileCache || this._tileCache.count > this._maximumCacheSize) {
        this.clearTileCache();
    }
    if (this._tileCache[key]) {
        return this._tileCache[key];
    }

    if (this._isWorking || !this._ready) {
        return undefined
    }
    if (this.clipperLayer) {
        var rectangle1 = this.tilingScheme.tileXYToRectangle(x, y, level);

        var intersection = Cesium.Rectangle.intersection(this.clipperLayer.rectangle, rectangle1)
        if (!intersection) return undefined;
    }

    this._isWorking = true;

    var df = Cesium.when.defer();

    var rectangle = this.tilingScheme.tileXYToNativeRectangle(x, y, level);


    var tw = this._tileWidth;
    var th = this._tileHeight;

    var tileData = this.getTileData(rectangle, null, level, tw, th);

    //setTimeout(function () {
    try {

        if (tileData.contours) {
            delete tileData.contours;
        }
        if (that._isoLine) {// || that._isoValue) {

            function onContourSuccess() {

                var innerImg = that.drawContours(tileData, level, tw, th);
                delete tileData.array;
                delete tileData.pointArray;
                delete tileData.x;
                delete tileData.y;
                delete tileData.arrayBase;

                innerImg = that.clip(innerImg, x, y, level);

                that._tileCache[key] = innerImg;
                that._tileCache.count++;
                that._isWorking = false;
                df.resolve(that._tileCache[key]);
            }

            if (that.sync) {//

                //
                var contours = d3Contour.contours()
                    .size([tileData.x.length, tileData.y.length])
                    .thresholds(that.breaks)
                    (tileData.array);
                tileData.contours = contours;

                setTimeout(function () {
                    onContourSuccess();
                }, 20);
            } else {// 
                try {

                    var breaks = [];
                    for (var i = 0; i < that.breaks.length; i++) {
                        breaks[i] = that.breaks[i];
                    }
                    var contourPromise;
                    var task;
                    for (var i = 0; i < GridDataImageryProvider._contourTaskProcessors.length; i++) {
                        task = GridDataImageryProvider._contourTaskProcessors[i];
                        if (task._isWorking) {
                            continue;
                        }
                        contourPromise = task.scheduleTask({
                            methodName: "extractContour",
                            args: [tileData.array, tileData.x.length, tileData.y.length, breaks]
                        });
                        if (contourPromise) {
                            task._isWorking = true;
                            break;
                        }
                    }

                    if (contourPromise) {
                        contourPromise.then(function (contours) {
                            tileData.contours = contours;
                            onContourSuccess();
                            task._isWorking = false;
                        }).otherwise(function (err) {
                            task._isWorking = false;
                            that._isWorking = false;
                            df.resolve(undefined);
                        })
                    } else {
                        df.resolve(undefined);
                    }
                } catch (e) {
                    that._isWorking = false;
                    df.resolve(undefined);
                }
            }

        } else {
            var innerImg = that.drawContours(tileData, level, tw, th);
            delete tileData.array;
            delete tileData.pointArray;
            delete tileData.x;
            delete tileData.y;
            delete tileData.arrayBase;
            innerImg = that.clip(innerImg, x, y, level);

            that._tileCache[key] = innerImg;
            that._tileCache.count++;
            that._isWorking = false;
            df.resolve(that._tileCache[key]);
        }

    } catch (e) {
        console.log(e);
        that._isWorking = false;
        df.resolve(undefined);
        return;
    }
    //}, 20)

    return df;
}


/**
 * 
 */
GridDataImageryProvider.prototype.destroy = function () {
    this.clearTileCache();
    for (var i in this) {
        if (this.hasOwnProperty(i)) {
            delete this[i];
        }
    }

}
module.exports = GridDataImageryProvider;