    function DmrUtil() { }
    DmrUtil.getStringArray = function (inputValue, delim) {
        if (inputValue == null) {
            inputValue = "";
        }
        return inputValue.split(delim);
    }
    DmrUtil.getMaxAndMin = function (fs) {
        if (fs.length == 0) {
            return new [0.0, 0.0];
        }
        var max = fs[0];
        var min = fs[0];
        for (var i = 0; i < fs.length; i++) {
            if (fs[i] > max) {
                max = fs[i];
            }
            if (fs[i] < min) {
                min = fs[i];
            }
        }
        return [max, min];
    }
    DmrUtil.getMaxAndMin = function (fs) {
        if (fs.length == 0) {
            return [0.0, 0.0];
        }
        var max = fs[0];
        var min = fs[0];
        for (var i = 0; i < fs.length; i++) {
            if (fs[i] > max) {
                max = fs[i];
            }
            if (fs[i] < min) {
                min = fs[i];
            }
        }
        return [max, min];
    }
    DmrUtil.bytesToShortByflag = function (b0, b1, flag) {
        if (flag == 0) {
            return (b1 << 8) + (b0 & 0xFF);
        }
        return (b0 << 8) + (b1 & 0xFF);
    }
    DmrUtil.bytesToShort = function (b0, b1) {
        return (b0 << 8) + (b1 & 0xFF);
    }
    DmrUtil.byteToInt = function (b) {
        return (b[0] & 0xFF) << 24 | (b[1] & 0xFF) << 16 |
        (b[2] & 0xFF) << 8 | (b[3] & 0xFF) << 0;
    }
    DmrUtil.getNewAreaByZoom = function (argarea, zoomFactor, bandWidth, bandHeight) {
        var newwidth = argarea.width * zoomFactor;
        var newheight = argarea.height * zoomFactor;
        if (argarea.x + newwidth * 1 - 1 >= bandWidth) {
            newwidth = bandWidth - argarea.x;
        }
        if (argarea.y + newheight * 1 - 1 >= bandHeight) {
            newheight = bandHeight - argarea.y;
        }
        return { x: argarea.x, y: argarea.y, width: newwidth, height: newheight };
    }
    DmrUtil.validArea = function (area, zoomFactor, bandWidth, bandHeight) {
        if (area.x < 0) {
            return null;
        }
        if (area.y < 0) {
            return null;
        }
        if (area.x + area.width * zoomFactor - zoomFactor >= bandWidth) {
            return null;
        }
        if (area.y + area.height * zoomFactor - zoomFactor >= bandHeight) {
            return null;
        }
        return area;
    }
    DmrUtil.byteToInt = function (b) {
        return b & 0xFF;

    }
    module.exports= DmrUtil;
