    DirectoryInfo = require('./DirectoryInfo'),
    FileInfo = require('./FileInfo'),
    ApiResponse = require('./ApiResponse'),
    loadArrayBuffer = require('../ajax/loadArrayBuffer'),
    loadJson = require('../ajax/loadJson'),
    loadBlob = require('../ajax/loadBlob'),
    loadXML = require('../ajax/loadXML'),
    loadImage = require('../ajax/loadImage'),
    defined = require('../defined'),
    getString = require('../getString'),
    loadText = require('../ajax/loadText'),
    loadWithXhr = require('../ajax/loadWithXhr'),
    base64 = require('base64-js'),
    when = require('when');


/**
*http
*@param {Object}options
*@param {MeteoLib.Util.HttpFileSystem.HFSConfig}[options.config= MeteoLib.Util.HttpFileSystem.HFSConfig.DEFAULT]
*@property {MeteoLib.Util.HttpFileSystem.HFSConfig}config
*@constructor
*@memberof MeteoLib.Util
*@example
    var HttpFileSystem = MeteoLib.Util.HttpFileSystem;
 
    var hfs = new HttpFileSystem({
        config: {
            baseUrl: 'http://127.0.0.1:18880/hfs',
            auth: {
                uid: 'xxx',
                pwd: 'xxx'
            }
        }
    });
 
    hfs.getDirectoryList().then(function (dirList) {
        console.log(dirList);
        hfs.getFiles(dirList[0], 'SATELLITE/FY4A/L1/CHINA/C002')
            .then(function (fileList) {
            console.log(fileList);
            var fileUrl = hfs.getFileUrl(fileList[0]);
            window.open(fileUrl)
        })
    })

*/
function HttpFileSystem(options) {
    options = options ? options : {};
    this._config = options.config ? options.config : HFSConfig.DEFAULT;
    if (this._config && !(this._config instanceof HFSConfig)) {
        this._config = new HFSConfig(options.config);
    }
}
Object.defineProperties(HttpFileSystem.prototype, {
    config: {
        get: function () {
            return this._config;
        },
        set: function (val) {
            this._config = val;
        }
    }
})
HttpFileSystem.HFSConfig = HFSConfig;
HttpFileSystem.DirectoryInfo = DirectoryInfo;
HttpFileSystem.FileInfo = FileInfo;
HttpFileSystem.ApiResponse = ApiResponse;

/**
* url
* @param {Object}params /
* @returns {string} 
* @private
*/
function buildParamsString(params) {

    var paramsStr = "";
    for (var i in params) {
        if (params.hasOwnProperty(i)) {
            if (paramsStr != "") {
                paramsStr += "&";
            }
            paramsStr += i + "=" + params[i];
        }
    }
    return paramsStr;
}
/**
*
*@param {Object}params
*@return {String}
*/
HttpFileSystem.prototype.getUrl = function (params) {
    params.userName = this.config.auth.uid;
    params.password = this.config.auth.pwd;
    var url = this.config.baseUrl;
    if (this.config.baseUrl.indexOf('?') >= 0) {
        url += '&'
    } else {
        url += '?'
    }
    url += buildParamsString(params);
    return url;
}
