/**
 * hdfnc
 * @param {MeteoLib.Util.HttpFileSystem.FileInfo}fileInfo
 * @param {String}variable 
 * @return {Promise.<MeteoLib.Util.ApiResponse|Object>}
 */
HttpFileSystem.prototype.readVariable = function (fileInfo, variable) {

    var params = {
        "method": "ReadVariable",
        "dir": fileInfo.dir,
        "path": fileInfo.path,
        "fileName": fileInfo.fileName,
        "variable": variable
    };

    var url = this.getUrl(params);
    return loadJson(url).then(function (res) {
        if (!res.errorCode && res.data) {
            if (typeof res.data == 'string') {
                res.data = base64.toByteArray(res.data)
            }
        }
        return res;
    });
}


/**
 * hdfnc
 * @param {MeteoLib.Util.HttpFileSystem.FileInfo}fileInfo
 * @param {Array<String>|String}variables ','
 * @return {Promise.<MeteoLib.Util.ApiResponse|Object>}
 */
HttpFileSystem.prototype.readNetCDF4 = function (fileInfo, variables) {

    if (typeof variables == 'string') {
        try {
            variables = JSON.parse('[' + variables + ']')
        } catch (error) {
            throw new Error('variables')
        }
    }

    var params = {
        "method": "ReadNetCDF4",
        "dir": fileInfo.dir,
        "path": fileInfo.path,
        "fileName": fileInfo.fileName
    };
    if (variables && variables.length > 0) {
        params.variables = variables.join(',')
    }
    var url = this.getUrl(params);
    return loadJson(url).then(function (res) {
        if (!res.errorCode && res.variables) {
            for (var varName in res.variables) {
                if (res.variables.hasOwnProperty(varName)) {
                    var variable = res.variables[varName]
                    if (typeof variable.data == 'string') {
                        variable.data = base64.toByteArray(variable.data)
                    }
                }
            }
        }
        return res;
    });
}

/**
 * 
* @param {MeteoLib.Util.HttpFileSystem.DirectoryInfo|String}dirInfo 
* @param {String}[path]
* @param {String} [ext] "*.png"
* @param {String} [startTime] 
* @param {String} [endTime] 
* @param {String} [timeRegex] 
* @param {Boolean} [fromDb=true] 
*@return {Promise.<MeteoLib.Util.HttpFileSystem.FileInfo>}
*/
HttpFileSystem.prototype.getFiles = function (dirInfo, path, ext, startTime, endTime, timeRegex, fromDb) {
    var params = {
        "method": "GetFiles"
    };
    if (path) {
        params.path = path;
    } else {
        params.path = "/";
    }
    if (typeof dirInfo == 'string') {
        params.dir = dirInfo;
    } else {
        params.dir = dirInfo.name;
    }
    if (ext) {
        params.searchPattern = ext;
    }
    if (startTime) {
        params.startTime = startTime;
    }
    if (endTime) {
        params.endTime = endTime;
    }
    if (timeRegex) {
        params.timeRegex = timeRegex;
    }
    if (!defined(fromDb)) {
        fromDb = 'true';
    }
    params.fromDb = fromDb;
    var url = this.getUrl(params);
    return loadJson(url).then(function (list) {
        if (list && list.length) {
            for (var i = 0; i < list.length; i++) {
                var curr = new FileInfo(list[i]);
                curr.previous = i > 0 ? new FileInfo(list[i - 1]) : null;
                curr.next = i < list.length - 1 ? new FileInfo(list[i + 1]) : null;
            }
        }
        return list;
    });
}

