HttpFileSystem.prototype.exists = function (dirInfo, path, fileName) {
    var params = {
        "method": "Exists"
    };
    if (path) {
        params.path = path;
    } else {
        params.path = "/";
    }
    if (typeof dirInfo == 'string') {
        params.dir = dirInfo;
    } else {
        params.dir = dirInfo.name;
    }
    if (fileName) {
        params.fileName = fileName;
    } else {
        params.fileName = '';
    }
    var url = this.getUrl(params);
    return loadJson(url).then(function (res) {
        if (res.errorCode) {
            return false
        } else {
            return true;
        }
    })
}
