/**
 *url
 * @param {MeteoLib.Util.HttpFileSystem.FileInfo}fileInfo 
 * @param {Number} [offset=0] 
 * @param {Number} [length]  
 * @returns {Promise.<ArrayBuffer>}  
 */
HttpFileSystem.prototype.getFileUrl = function (fileInfo, offset, length) {
    offset = offset ? offset : 0;
    length = length ? length : (fileInfo.size ? fileInfo.size : null);
    var params = {
        "dir": fileInfo.dir,
        "path": fileInfo.path,
        "fileName": fileInfo.fileName
    };
    if (length) {
        params.offset = offset;
        params.length = length;
    }
    var url = this.getUrl(params);
    return url;
}


/**
 *
 * @param {MeteoLib.Util.HttpFileSystem.FileInfo|Object}fileInfo 
 * @param {MeteoLib.Util.HttpFileSystem.FileInfo|Object}fileInfo.dir 
 * @param {MeteoLib.Util.HttpFileSystem.FileInfo|Object}fileInfo.path 
 * @param {MeteoLib.Util.HttpFileSystem.FileInfo|Object}fileInfo.fileName 
 * @param {Number} [offset=0] 
 * @param {Number} [length]  
 * @param {String} [responseType="arrayBuffer"] :arrayBuffer,blob,json,text,xml,image
 * @param {String} [encoding]
 * @returns {Promise.<ArrayBuffer>}  
 * @example
 * //1arrayBuffer
 * hfs.readFile(fileInfo).then(function(buf){
 *      //TODO
 * }).otherwise(function(reason){
 *      //throw 
 * })
 * //2arrayBuffer
 * hfs.readFile(fileInfo,0,1024).then(function(buf){
 *      //TODO
 * }).otherwise(function(reason){
 *      //throw 
 * })
 * //3json
 * hfs.readFile(fileInfo,'json').then(function(json){
 *      //TODO
 * }).otherwise(function(reason){
 *      //throw 
 * })
 * //4
 * hfs.readFile(fileInfo,'text','gb2312').then(function(text){
 *      //TODO
 * }).otherwise(function(reason){
 *      //throw 
 * })
 * //5
 * hfs.readFile(fileInfo,'image').then(function(image){
 *      //TODO
 * }).otherwise(function(reason){
 *      //throw 
 * })
 */
HttpFileSystem.prototype.readFile = function (fileInfo, offset, length, responseType, encoding) {
    if (typeof offset == 'string' && !responseType) {
        responseType = offset;
        offset = 0;
    }
    if (typeof length == 'string' && !encoding) {
        encoding = length;
        length = null;
    }
    offset = offset ? offset : 0;
    length = length ? length : (fileInfo.size ? fileInfo.size : null);
    var params = {
        "dir": fileInfo.dir,
        "path": fileInfo.path,
        "fileName": fileInfo.fileName
    };
    if (length) {
        params.offset = offset;
        params.length = length;
    }
    var url = this.getUrl(params);

    responseType = responseType ? responseType : "arrayBuffer";
    switch (responseType) {
        case "arrayBuffer":
            return loadArrayBuffer(url);
        case "blob":
            return loadBlob(url);
        case "xml":
            return loadXML(url);
        case "json":
            return loadJson(url);
        case "image":
            return loadImage(url);
        case "text":
            if (encoding) {
                return loadArrayBuffer(url).then(function (buf) {
                    return getString(buf, encoding)
                });
            } else {
                return loadText(url)
            }
        default:
            return loadArrayBuffer(url);
    }

}

/**
 *
 * @param {MeteoLib.Util.HttpFileSystem.FileInfo}fileInfo 
 * @param {Number} xOffset 
 * @param {Number} yOffset 
 * @param {Number} xSize 
 * @param {Number} ySize 
 * @param {Number} [typeSize=0] 
 * @param {Number} xBufferSize 
 * @param {Number} yBufferSize 
 * @param {Number} stride 
 * @param {Array.<Number>} [bandOffsets] 
 * @param {Boolean} [flipY=false] truefalse
 * @param {Number} [numberOfY=0] 
 * @param {Number} [headerSize=0] 0
 * @param {Boolean} [compress=false] falsegzipfalse
 * @returns {Promise.<ArrayBuffer>}  
 */
HttpFileSystem.prototype.readRasterData = function (
    fileInfo, xOffset, yOffset, xSize, ySize,
    stride, typeSize, xBufferSize, yBufferSize, bandOffsets, flipY, numberOfY,
    headerSize, compress) {

    typeSize = (typeSize ? typeSize : 1);
    xBufferSize = (xBufferSize ? xBufferSize : xSize);
    yBufferSize = (yBufferSize ? yBufferSize : ySize);

    var params = {
        "dir": fileInfo.dir,
        "path": fileInfo.path,
        "fileName": fileInfo.fileName,
        "xOffset": xOffset,
        "yOffset": yOffset,
        "xSize": xSize,
        "ySize": ySize,
        "stride": stride,
        "typeSize": typeSize,
        "headerSize": (headerSize ? headerSize : 0),
        "xBufferSize": xBufferSize,
        "yBufferSize": yBufferSize,
        "numberOfY": numberOfY,
        "flipY": flipY
    };

    if (bandOffsets && bandOffsets.length) {
        params.bandOffsets = JSON.stringify(bandOffsets);
    }
    var os = this.os;
    if (!this.os) {
        os = (function () {
            var UserAgent = navigator.userAgent.toLowerCase();
            return {
                isIpad: /ipad/.test(UserAgent),
                isIphone: /iphone os/.test(UserAgent),
                isAndroid: /android/.test(UserAgent),
                isWindowsCe: /windows ce/.test(UserAgent),
                isWindowsMobile: /windows mobile/.test(UserAgent),
                isWin2K: /windows nt 5.0/.test(UserAgent),
                isXP: /windows nt 5.1/.test(UserAgent),
                isVista: /windows nt 6.0/.test(UserAgent),
                isWin7: /windows nt 6.1/.test(UserAgent),
                isWin8: /windows nt 6.2/.test(UserAgent),
                isWin81: /windows nt 6.3/.test(UserAgent),
                isMac: /mac os/.test(UserAgent)
            };
        }());
        this.os = os;
    }
    if (os.isMac || os.isIpad || os.isIphone || (defined(compress) && !compress)) {
        params["compress"] = "false";
    }

    var url = this.getUrl(params);

    return loadArrayBuffer(url)
}
