/**
*
* @param {MeteoLib.Util.HttpFileSystem.DirectoryInfo|String}dirInfo
* @param {String}path
* @param {File|Blob}file 
* @param {Object}options 
* @param {String}[options.fileName] 
* @param {Date} [options.datetime] 
* @param {Boolean}[options.overwrite] 
* @param {Boolean}[options.rename] fileId
* @param {MeteoLib.Util.HttpFileSystem~uploadFileProgressCallback}[options.onProgress] 
* @param {MeteoLib.Util.HttpFileSystem~uploadFileProgressCallback}[options.onCancel] 
*@returns {Promise.<MeteoLib.Util.ApiResponse>} promise
*@returns {function} promise.abort
*@example 
        var Api = MeteoLib.Util;
        var dir = "TestDisk", path = "/", fname = "test.txt";
        var hfs = new Api.HttpFileSystem({
            config: {
                baseUrl: 'http://127.0.0.1:8081/hfs',
                auth: {
                    uid: 'gt-admin',
                    pwd: 'wm-server-1024'
                }
            }
        })
        var file = new File(["hello!hfs uploadFile"], fname)
        var promise = hfs.uploadFile(dir, '/txt', file, {
            onProgress:function(e){
                var progress=e.loaded/e.total;//0~1
                console.log(progress)
            }
        });
        if (promise) {
            promise.then(function (rsp) {

                if (rsp.errorCode) {
                    alert(rsp.msg)
                } else {
                    alert(JSON.stringify(rsp.files))
                }
                console.log(rsp)
            }).otherwise(function (err) {
                console.error(err)
                alert(err)
            })
            //
            //promise.abort();
            
        }
*/
HttpFileSystem.prototype.uploadFile = function (dirInfo, path, file, options) {
    options = options ? options : {};
    var fileName = options.fileName,
        datetime = options.datetime,
        overwrite = options.overwrite,
        rename = options.rename;

    var url = this.config.baseUrl;

    var formData = new FormData();
    formData.append('userName', this.config.auth.uid)
    formData.append('password', this.config.auth.pwd)
    formData.append('method', "UploadFile")
    formData.append('path', path)
    if (typeof dirInfo == 'string') {
        formData.append('dir', dirInfo)
    } else {
        formData.append('dir', dirInfo.name)
    }
    if (fileName && !rename) {
        file.name = fileName;
    }
    if (overwrite) {
        formData.append('overwrite', true);
    }
    if (rename) {
        formData.append('rename', true);
    }
    formData.append('file', file);
    if (datetime && datetime instanceof Date) {
        formData.append('datetime', datetime)
    }


    // var promise = MeteoLib.Util.ajax.loadWithXhr({
    //     url: url,
    //     method: 'POST',
    //     data: formData,
    //     responseType: 'json'
    // });
    // return promise;

    var method = 'POST';
    var responseType = 'json';
    var xhr = new XMLHttpRequest();
    //xhr.withCredentials = true;

    xhr.open(method, url, true);

    var deferred = when.defer();

    xhr.onload = function () {
        if (xhr.status < 200 || xhr.status >= 300) {
            deferred.reject({
                statusCode: xhr.status,
                response: xhr.response,
                responseHeaders: xhr.getAllResponseHeaders()
            })//new RequestErrorEvent(xhr.status, xhr.response, xhr.getAllResponseHeaders()));
            return;
        }

        var response = xhr.response;
        var browserResponseType = xhr.responseType;

        //All modern browsers will go into either the first if block or last else block.
        //Other code paths support older browsers that either do not support the supplied responseType
        //or do not support the xhr.response property.
        if (defined(response) && (!defined(responseType) || (browserResponseType === responseType))) {
            deferred.resolve(response);
        } else if ((responseType === 'json') && typeof response === 'string') {
            try {
                deferred.resolve(JSON.parse(response));
            } catch (e) {
                deferred.reject(e);
            }
        } else if ((browserResponseType === '' || browserResponseType === 'document') && defined(xhr.responseXML) && xhr.responseXML.hasChildNodes()) {
            deferred.resolve(xhr.responseXML);
        } else if ((browserResponseType === '' || browserResponseType === 'text') && defined(xhr.responseText)) {
            deferred.resolve(xhr.responseText);
        } else {
            deferred.reject(new Error('Invalid XMLHttpRequest response type.'));
        }
    };

    xhr.upload.onprogress = function (e) {

        if (typeof options.onProgress == 'function') {
            options.onProgress(e);
        }
    }

    xhr.onerror = function (e) {
        if (e instanceof ProgressEvent)
            return
        deferred.reject(e);
    };

    xhr.send(formData);

    deferred.promise.abort = function () {
        xhr.abort();
        if (typeof options.onCancel == 'function') {
            options.onCancel();
        }
    }

    return deferred.promise;
}
/**
 * @callback MeteoLib.Util.HttpFileSystem~uploadFileProgressCallback
 * @param {ProgressEvent} e
 */

module.exports = HttpFileSystem;