var Point=require('./Point');
var HdrGeoPointCoord=require('./HdrGeoPointCoord');

    /**
   *
   *@property {String}Name= "Geographic Lat/Lon"
   *@property {MeteoLib.Data.RasterData.Point}BaseRowColNumber=MeteoLib.Data.RasterData.Point.Empty
   *@property {MeteoLib.Data.RasterData.HdrGeoPointCoord}BaseMapCoordinateXY
   *@property {MeteoLib.Data.RasterData.HdrGeoPointCoord}XYResolution
   *@property {String}CoordinateType= "WGS-84"
   *@property {String}Units= "Degrees"
   *@constructor
   *@memberof MeteoLib.Data.RasterData
   */
    function HdrMapInfo() {
        this.Name = "Geographic Lat/Lon";
        this.BaseRowColNumber = Point.Empty;
        this.BaseMapCoordinateXY = new HdrGeoPointCoord();
        this.XYResolution = new HdrGeoPointCoord();
        this.CoordinateType = "WGS-84";
        this.Units = "Degrees";
        /**
        *
        *@return {Boolean}
        */
        this.IsEmpty = function () {
            return this.BaseMapCoordinateXY == null;
        }
    }
    module.exports= HdrMapInfo;
