    function RasterDataProvider() {
        //this. _attributes = new AttributeManager();
        this._dataType = enumDataType.Atypism;
        this._bandCount;
        this._dataTypeSize;
        this._width;
        this._height;
        this._resolutionX;
        this._resolutionY;
        this._coordEnvelope = null;
        //		protected ICoordTransform _coordTransform;
        this._filelist;
        this._rasterBands = [];
        this._bandProvider;
        this._dataIdentify = new DataIdentify();
        //		protected OrbitProjectionTransform _orbitProjectionTransform;
        //		protected IOverviewGenerator _overviewGenerator;
        this._tag;
        if (typeof SDK !== 'undefined') {//
            this.Tag = {};
            this.DataType = enumDataType.UInt16;
            this.BandCount = 38;
            this.Width = 500;
            this.Height = 500;
            this.ResolutionX = 0.01;
            this.ResolutionY = 0.01;
            this.DataIdentify = new DataIdentify();
            this.BandProvider = null;
            this.FileName = "";
            this.IsSupprtOverviews = false;
            this.IsOverviewsBuilded = false;
        }
    }
