    RasterDataProvider.prototype.BandMap = function (bandNo) {
        return bandNo;
    }
    /**
    *
    *@param {Number}bandNo 
    *@return {MeteoLib.Data.RasterData.RasterBand}
    */
    RasterDataProvider.prototype.GetRasterBand = function (bandNo) {
        if (typeof SDK !== 'undefined') {
            return new RasterBand();
        }
        if (this._rasterBands.length == 0) {
            throw new Error("");
        }
        bandNo = this.BandMap(bandNo);
        if (bandNo < 1 || bandNo > this._rasterBands.Count) {
            throw new Error("\"" + bandNo + "\",\"0~" + this._rasterBands.Count + "\"", this._bandIndex, 0, this._maxBandCount); //, bandNo);
        }
        return this._rasterBands[bandNo - 1];
    }

    RasterDataProvider.prototype.CheckArgumentsisValid = function (xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize, bandCount, bandMap, interleave) {
        if (xOffset < 0 || yOffset < 0 || xOffset >= this._width || yOffset >= this._height) {
            throw new Error("(OffsetX = " + xOffset + ",OffsetY = " + yOffset + ",XSize =" + xSize + ",YSize = " + ySize + ")");
        }
        if (buffer == IntPtr.Zero || xBufferSize == 0 || yBufferSize == 0) {
            throw new Error("");
        }
        if (xOffset + xSize > this._width || yOffset + ySize > this._height) {
            throw new Error("Access window out of range in RasterIO()!");
        }
        if (bandCount == 0) {
            return;
        }
        if (bandMap == null || bandMap.Length == 0) {
            bandMap = new Int32Array(bandCount);
            for (var i = 0; i < bandCount; i++) {
                bandMap[i] = i + 1;
            }
        }
        var array = bandMap;
        for (var j = 0; j < array.length; j++) {
            var num = array[j];
            if (num < 1 || num > this._bandCount) {
                throw new Error("\"" + num + "\",\"0~" + this._bandCount + "\"");
            }
        }
    }

    /**
    *
    *@param {Number}xOffset x
    *@param {Number}xOffset y
    *@param {Number}xSize x
    *@param {Number}ySize y
    *@param {TypeArray}buffer 
    *@param {MeteoLib.Data.RasterData.enumDataType}dataType 
    *@param {Number}xBufferSize bufferx
    *@param {Number}yBufferSize buffery
    *@param {Number}bandCount 
    *@param {Array.<Numbe>}bandMap 
    *@param {MeteoLib.Data.RasterData.enumInterleave}interleave BSQ
    */
    RasterDataProvider.prototype.Read = function (xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize, bandCount, bandMap, interleave) {
        this.CheckArgumentsisValid(xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize, bandCount, bandMap, interleave);
        switch (interleave) {
            //        case enumInterleave.BIP:                   
            //            this.ReadBandToBIP(xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize, bandCount, bandMap, buffer);                   
            //            return;                   
            case enumInterleave.BSQ:
                this.ReadBandsToBSQ(xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize, bandCount, bandMap, buffer);
                return;
                //        case enumInterleave.BIL:
                //            this.ReadBandsToBIL(xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize, bandCount, bandMap, buffer);
                //            return;
            default:
                throw new Error("\"" + interleave + "\"");
        }
    }
    /**
    *BSQ
    *@param {Number}xOffset x
    *@param {Number}xOffset y
    *@param {Number}xSize x
    *@param {Number}ySize y
    *@param {TypeArray}xOffset 
    *@param {MeteoLib.Data.RasterData.enumDataType}dataType 
    *@param {Number}xBufferSize bufferx
    *@param {Number}yBufferSize buffery
    *@param {Number}bandCount 
    *@param {Array.<Numbe>}bandMap 
    *@param {MeteoLib.Data.RasterData.enumInterleave}interleave BSQ
    */
    RasterDataProvider.prototype.ReadBandsToBSQ = function (xOffset, yOffset, xSize, ySize, buffer, dataType, xBufferSize, yBufferSize, bandCount, bandMap, ptr0) {
        var i = 0;
        var tempBuffer;
        switch (dataType) {
            case enumDataType.Byte:
                tempBuffer = new Int8Array(xBufferSize * yBufferSize);
                break;
            case enumDataType.Int16:
                tempBuffer = new Int16Array(xBufferSize * yBufferSize);
                break;
            case enumDataType.Int32:
                tempBuffer = new Int32Array(xBufferSize * yBufferSize);
                break;
            case enumDataType.Float:
                tempBuffer = new Float32Array(xBufferSize * yBufferSize);
                break;
            case enumDataType.Double:
                tempBuffer = new Float64Array(xBufferSize * yBufferSize);
                break;
            default:
                throw new Error("");
        }
        var destIndex = 0;
        while (i < bandCount) {
            this._rasterBands[bandMap[i] - 1].Read(xOffset, yOffset, xSize, ySize, tempBuffer, dataType, xBufferSize, yBufferSize);
            i++;
            Array.copy(tempBuffer, 0, destIndex, tempBuffer.length);
            destIndex += xBufferSize * yBufferSize;
        }
    }

    module.exports= RasterDataProvider;
